import { UploadOutlined } from '@ant-design/icons';
import { Button, message, Upload } from 'antd';
import Modal from 'antd/lib/modal/Modal';
import React, { useState } from 'react';
import { noImg } from '../../CommonImage';

const EventPosterUploadComponent = (props) => {
  const [previewVisible, setPreviewVisible] = useState(false);
  const [previewImage, setPreviewImage] = useState();
  const { fileList, setFileList, eventPoster } = props;

  const dummyRequest = ({ file, onSuccess }) => {
    setTimeout(() => {
      onSuccess('ok');
    }, 0);
  };

  const getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const handlePreview = async (file) => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    setPreviewImage(file.url || file.preview);
    setPreviewVisible(true);
  };

  const handleCancel = () => {
    setPreviewVisible(false);
  };

  const beforeUpload = (file, fileList) => {
    return false;
  };
  return (
    <div>
      <Modal visible={previewVisible} footer={null} onCancel={handleCancel}>
        <img
          alt='example'
          style={{ width: '100%' }}
          src={previewImage}
          onError={(error) => {
            error.currentTarget.src = noImg;
          }}
        />
      </Modal>
      <Upload
        className='event-poster-wrapped'
        listType='picture-card'
        beforeUpload={beforeUpload}
        customRequest={dummyRequest}
        onPreview={handlePreview}
        multiple={false}
        fileList={fileList}
        onChange={(info) => {
          if (
            !(
              info.file.type.includes('image') ||
              info.file.type.includes('video')
            )
          ) {
            message.error(
              `이미지 또는 비디오 파일만 업로드 가능합니다 : ${info.file.name}`
            );
          } else {
            setFileList(info.fileList);
          }
        }}
      >
        {fileList.length >= 1 ? null : (
          <Button>
            <UploadOutlined /> 포스터 업로드
          </Button>
        )}
      </Upload>
    </div>
  );
};

export default EventPosterUploadComponent;
