import React from "react";
import { Select } from "antd";
import uuid from "react-uuid";

const ItemLoanDetailMasterEquipComponent = (props) => {
  const { selectedEquipList = [], onChangeEquip, equipList = [] } = props;
  return (
    <div>
      <Select
        value={selectedEquipList}
        mode="multiple"
        showArrow
        placeholder="IS를 선택해주세요"
        style={{ width: "100%" }}
        onChange={(e) => {
          onChangeEquip(e);
        }}
      >
        {equipList.map((item, idx) => {
          return (
            <Select.Option key={uuid()} value={item.idx}>
              {item.eqpName}
            </Select.Option>
          );
        })}
      </Select>
    </div>
  );
};

export default ItemLoanDetailMasterEquipComponent;
