import { ExclamationCircleOutlined } from '@ant-design/icons';
import { Button, Space } from 'antd';
import confirm from 'antd/lib/modal/confirm';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import * as Constants from '../../Constants';
import MenuGuide from '../../MenuGuide';
import EventDetailComponent from './EventDetailComponent';
import EventDetailEnrolleeListComponent from './EventDetailEnrolleeListComponent';

const EventDetailContainer = (props) => {
  const { event_master_idx } = props.match.params;
  const [event, setEvent] = useState();

  const [fileList, setFileList] = useState([]);

  useEffect(() => {
    getEvent();
  }, []);

  useEffect(() => {
    sendImages(fileList, event_master_idx);
  }, [fileList]);

  useEffect(() => {
    getEventPoster(event);
  }, [event]);

  const getEvent = () => {
    axios
      .get(`/event/detail/${event_master_idx}`)
      .then((res) => {
        console.log(res);
        setEvent(res.data.response_body);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const getEventPoster = (event) => {
    if (
      event &&
      event.event_poster &&
      event.event_poster.event_poster_file_name
    ) {
      let uid = event.event_poster.event_poster_file_name.split('.')[0];
      let extension = event.event_poster.event_poster_file_name.split('.')[1];
      let tempFileList = [
        {
          uid: uid,
          name: event.event_poster.event_poster_file_name,
          status: 'done',
          type: `image/${extension}`,
          url: `${axios.defaults.baseURL}/files/event/${event.event_poster.event_poster_file_name}`,
        },
      ];
      setFileList(tempFileList);
    }
  };

  const deleteEvent = (event_master_idx) => {
    axios
      .delete(`/event/delete/${event_master_idx}`)
      .then((res) => {
        props.history.goBack();
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const sendImages = (imageList, event_master_idx) => {
    if (imageList.length === 0) {
      return;
    }
    if ('status' in imageList[0]) {
      return;
    }
    var formData = new FormData();
    var info = [];

    imageList.forEach((item, idx) => {
      formData.append('files', item.originFileObj);
      info.push({
        uid: item.uid,
        type: 'GES_EVENT',
        idx: idx,
        ref_idx: event_master_idx,
      });
    });
    formData.append('info', JSON.stringify(info));
    axios
      .post('/file', formData)
      .then((res) => {})
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <div style={{ width: '90%', margin: 'auto', marginBottom: '50px' }}>
      <MenuGuide title={'행사 관리 ▶ 행사 상세'} subTitle={''} />

      {event ? (
        <div>
          <EventDetailComponent
            eventMaster={event.event_master}
            eventTarget={event.event_target}
            eventPoster={event.event_poster}
            setFileList={setFileList}
            fileList={fileList}
          />
          <Space style={{ marginTop: '10px' }}>
            <Button
              type='primary'
              onClick={() => {
                props.history.push(`/event/form/${event_master_idx}`, {
                  event: event,
                });
              }}
            >
              수정
            </Button>
            <Button
              danger
              onClick={() => {
                confirm({
                  title: '행사를 삭제하시겠습니까?',
                  icon: <ExclamationCircleOutlined style={{ color: 'red' }} />,
                  content: '',
                  okText: '확인',
                  cancelText: '취소',
                  onOk() {
                    deleteEvent(event_master_idx);
                  },
                  onCancel() {},
                });
              }}
            >
              삭제
            </Button>
          </Space>
        </div>
      ) : null}
      <EventDetailEnrolleeListComponent
        event_master_idx={event_master_idx}
        getEvent={getEvent}
      />
    </div>
  );
};

export default EventDetailContainer;
