import { getDatesStartToLast } from '..';
import { HolidayProcessType } from '../../types/Lss';

//해당 함수는 getDatesStartToLast 함수를 의존합니다.

/**
 *
 * @param value moment로 calendar에서 순회하는 날짜를 뜻합니다.
 * @param holidayArray uuid,badge타입 후가공된 holiday 배열입니다.
 * @returns 해당 날짜에 존재하는 휴가정보를 반환합니다.
 */
const getIncludesHolidayOfDay = (value: moment.Moment, holidayArray: Array<HolidayProcessType>) => {
  const date = value.format('YYYY-MM-DD');
  const result: Array<HolidayProcessType> = [];
  holidayArray.forEach((parentItem) => {
    //LSS의 모든 휴가 날짜가 반환된다.
    const ArrayStartAndEnd = getDatesStartToLast(parentItem.startDate, parentItem.endDate);
    if (ArrayStartAndEnd) {
      ArrayStartAndEnd.forEach((item) => {
        //result에는 해당요일에 존재하는 모든 휴가정보가 들어간다.
        if (date === item) {
          result.push({
            idx: parentItem.idx,
            type: parentItem.type,
            desc: parentItem.desc,
            startDate: parentItem.startDate,
            endDate: parentItem.endDate,
            isUsed: parentItem.isUsed,
            period: parentItem.period,
            uuid: parentItem.uuid,
            badgeType: parentItem.badgeType,
          });
        }
      });
    }
  });
  return result;
};

export default getIncludesHolidayOfDay;
