import { Divider } from 'antd';
import React, { useEffect, useState } from 'react';
import FormGuideComponent from '../../FormGuideComponent';
import {
  requestEventStatisticPerAgedForSheet,
  requestEventStatisticPerDayForChart,
  requestEventStatisticPerEventForSheet,
} from './requestEventStatistic';
import StatisticEventAgedChart from './StatisticEventAgedChart';
import StatisticEventPerEventDataSheet from './StatisticEventPerEventDataSheet';
import StatisticEventPeriodChart from './StatisticEventPeriodChart';

const StatisticEventContainer = () => {
  const [chartData, setChartData] = useState([]);
  const [sheetData, setSheetData] = useState([]);
  const [agedData, setAgedData] = useState([]);
  useEffect(() => {
    getChartData();
    getSheetData();
    getAgedData();
  }, []);

  const getChartData = async () => {
    requestEventStatisticPerDayForChart()
      .then((res) => {
        console.log(res);
        setChartData(res.data.response_body);
      })
      .catch((e) => console.log);
  };

  const getSheetData = async () => {
    requestEventStatisticPerEventForSheet()
      .then((res) => {
        console.log(res);
        setSheetData(res.data.response_body);
      })
      .catch((e) => console.log);
  };

  const getAgedData = async () => {
    requestEventStatisticPerAgedForSheet()
      .then((res) => {
        console.log(res);
        setAgedData(res.data.response_body);
      })
      .catch((e) => console.log);
  };
  return (
    <div
      style={{
        padding: '20px',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
      }}
    >
      <div>
        <FormGuideComponent
          title='전체 행사 차트'
          guide={<div>최근 14일 간 전체 행사 신청/취소 데이터</div>}
        />
        <StatisticEventPeriodChart periodData={chartData} />
      </div>

      <Divider />
      <div>
        <FormGuideComponent
          title='연령대 별 신청 현황 차트'
          guide={<div>행사 신청의 연령대</div>}
        />
        <StatisticEventAgedChart agedData={agedData} />
      </div>
      <Divider />
      <div>
        <FormGuideComponent
          title='행사별 신청/취소 데이터 시트'
          guide={
            <>
              <div>행사별 신청/취소 조회</div>
            </>
          }
        />
        <StatisticEventPerEventDataSheet dataSource={sheetData} />
      </div>
    </div>
  );
};

export default StatisticEventContainer;
