import React, { useEffect, useState } from 'react';
import {
  Form,
  Input,
  DatePicker,
  Button,
  Checkbox,
  Row,
  Col,
  InputNumber,
  TimePicker,
  Radio,
} from 'antd';
import FormGuideComponent from '../../FormGuideComponent';
import TextArea from 'antd/lib/input/TextArea';
import Text from 'antd/lib/typography/Text';
import axios from 'axios';
import * as Constants from '../../Constants';
import usePreventLeave from '@nooks/use-prevent-leave';
import { Prompt } from 'react-router-dom';
import moment from 'moment';

const EventFormComponent = (props) => {
  const { enablePrevent, disablePrevent } = usePreventLeave();
  const [isBack, setIsBack] = useState(true);
  const [eventIsGroup, setEventIsGroup] = useState(true);
  const [form] = Form.useForm();
  const [mode, setMode] = useState('regist');
  const [maxGroupCnt, setMaxGroupCnt] = useState(1);

  useEffect(() => {
    console.log('EventFormComponent useEffect');
    console.log(props);

    enablePrevent();
    return () => {
      disablePrevent();
    };
  }, []);

  useEffect(() => {
    if (props.location.state && props.location.state.event) {
      setMode('modify');
      console.log(mode);

      let {
        event_master,
        event_poster,
        event_target,
      } = props.location.state.event;
      setMaxGroupCnt(event_master.event_applicant_limit_cnt);

      form.setFieldsValue({
        event_title: event_master.event_title,
        event_description: event_master.event_description,
        event_place: event_master.event_place,
        event_target_sex: event_target.event_target_sex,
        event_target_age: event_target.event_target_age,
        event_apply_period_date: [
          moment(event_master.event_apply_start_date),
          moment(event_master.event_apply_end_date),
        ],
        event_activity_period_date: [
          moment(event_master.event_activity_start_date),
          moment(event_master.event_activity_end_date),
        ],
        event_activity_weekday: event_master.event_activity_weekday.split(','),
        event_activity_time: [
          moment(event_master.event_activity_start_time, 'h:mm'),
          moment(event_master.event_activity_end_time, 'h:mm'),
        ],
        event_applicant_limit_cnt: event_master.event_applicant_limit_cnt,
        event_is_group: event_master.event_is_group,
        event_group_limit_cnt: event_master.event_group_limit_cnt,
        event_is_public: event_master.event_is_public,
      });
    }
  }, [props]);

  const validateMessages = {
    required: '필수 항목',
    types: {
      email: 'Not a validate email!',
      number: 'Not a validate number!',
    },
    number: {
      range: 'Must be between ${min} and ${max}',
    },
  };
  const onFinish = (values) => {
    console.log(values);

    if (values.event_activity_period_date) {
      let startDate = values.event_activity_period_date[0];
      let endDate = values.event_activity_period_date[1];

      values['event_activity_start_date'] = startDate.format('YYYY-MM-DD');
      values['event_activity_end_date'] = endDate.format('YYYY-MM-DD');
    }

    if (values.event_apply_period_date) {
      let startDate = values.event_apply_period_date[0];
      let endDate = values.event_apply_period_date[1];

      values['event_apply_start_date'] = startDate.format('YYYY-MM-DD');
      values['event_apply_end_date'] = endDate.format('YYYY-MM-DD');
    }

    if (values.event_activity_time) {
      let startTime = values.event_activity_time[0];
      let endTime = values.event_activity_time[1];

      values['event_activity_start_time'] = startTime.format('HH:mm');
      values['event_activity_end_time'] = endTime.format('HH:mm');
    }

    if (values.event_activity_weekday) {
      values[
        'event_activity_weekday'
      ] = values.event_activity_weekday.sort().toString();
    }

    axios
      .post(
        mode === 'regist'
          ? `/event/new`
          : `/event/modify/${props.location.state.event.event_master.event_master_idx}`,
        {
          eventValues: values,
        }
      )
      .then((res) => {
        // props.history.replace(`/event/detail/${res}`);

        if (res.data.description.includes('Success')) {
          setIsBack(false);
          props.history.replace(
            `/event/detail/${res.data.response_body.event_master.event_master_idx}`
          );
        }
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const rangeConfig = {
    rules: [{ type: 'array', required: true, message: '기간을 선택해주세요' }],
  };

  const initialValues = () => {};
  return (
    <div>
      <Prompt
        when={isBack}
        message='변경사항이 저장되지 않았습니다. 뒤로가시겠습니까?'
      />
      <Form
        className='event-form'
        form={form}
        validateMessages={validateMessages}
        onFinish={onFinish}
        initialValues={initialValues}
      >
        <FormGuideComponent title='행사명' guide='' required={true} />
        <Form.Item name='event_title' rules={[{ required: true }]}>
          <Input />
        </Form.Item>

        <FormGuideComponent title='행사 설명' guide='' required={true} />
        <Form.Item name='event_description' rules={[{ required: true }]}>
          <TextArea />
        </Form.Item>

        <FormGuideComponent title='행사 장소' guide='' required={true} />
        <Form.Item name='event_place' rules={[{ required: true }]}>
          <Input />
        </Form.Item>

        <FormGuideComponent title='행사 대상 - 성별' guide='' />
        <Form.Item
          name='event_target_sex'
          rules={[{ required: true }]}
          initialValue={'COMMON'}
        >
          <Radio.Group>
            <Radio value='COMMON'>제한없음</Radio>
            <Radio value='MALE'>남자</Radio>
            <Radio value='FEMALE'>여자</Radio>
          </Radio.Group>
        </Form.Item>

        <FormGuideComponent title='행사 대상 - 연령' guide='' />
        <Form.Item
          name='event_target_age'
          rules={[{ required: true }]}
          initialValue={0}
        >
          <Radio.Group>
            <Radio value={0}>제한없음</Radio>
            <Radio value={6}>{`유아(6~7 세)`}</Radio>
            <Radio value={8}>{`초등(8~13 세)`}</Radio>
            <Radio value={14}>{`청소년(14~19 세)`}</Radio>
            <Radio value={20}>{`성인`}</Radio>
          </Radio.Group>
        </Form.Item>

        <FormGuideComponent title='행사 신청 기간' guide='' required={true} />
        <Form.Item name='event_apply_period_date' {...rangeConfig}>
          <DatePicker.RangePicker />
        </Form.Item>

        <FormGuideComponent title='행사 수행 기간' guide='' required={true} />
        <Form.Item name='event_activity_period_date' {...rangeConfig}>
          <DatePicker.RangePicker />
        </Form.Item>

        <FormGuideComponent title='행사 요일 선택' guide='' required={true} />
        <Form.Item name='event_activity_weekday' rules={[{ required: true }]}>
          <Checkbox.Group style={{ width: '100%' }}>
            <Row>
              <Col span={2}>
                <Checkbox value='0'>월</Checkbox>
              </Col>
              <Col span={2}>
                <Checkbox value='1'>화</Checkbox>
              </Col>
              <Col span={2}>
                <Checkbox value='2'>수</Checkbox>
              </Col>
              <Col span={2}>
                <Checkbox value='3'>목</Checkbox>
              </Col>
              <Col span={2}>
                <Checkbox value='4'>금</Checkbox>
              </Col>
              <Col span={2}>
                <Checkbox value='5'>토</Checkbox>
              </Col>
              <Col span={2}>
                <Checkbox value='6'>일</Checkbox>
              </Col>
            </Row>
          </Checkbox.Group>
        </Form.Item>

        <FormGuideComponent
          title='행사 시작/종료 시간'
          guide=''
          required={true}
        />
        <Form.Item name='event_activity_time' rules={[{ required: true }]}>
          <TimePicker.RangePicker
            placeholder={[`시작시간`, `종료시간`]}
            format={`HH:mm`}
          />
        </Form.Item>

        <FormGuideComponent title='행사 최대 정원' guide='' />
        <Form.Item
          name='event_applicant_limit_cnt'
          rules={[{ required: true }]}
          initialValue={1}
        >
          <InputNumber
            min={1}
            formatter={(value) => `${value} 명`}
            onChange={(e) => setMaxGroupCnt(e)}
          />
        </Form.Item>

        <FormGuideComponent
          title='행사 그룹 신청 여부'
          guide='한명의 회원이 다수의 회원 신청(익명)이 가능합니다.'
        />
        <Form.Item
          name='event_is_group'
          rules={[{ required: true }]}
          initialValue={'Y'}
        >
          <Radio.Group
            onChange={(e) => {
              console.log(e.target.value);
              setEventIsGroup(e.target.value === 'Y');
            }}
          >
            <Radio value='Y'>허용</Radio>
            <Radio value='N'>비허용</Radio>
          </Radio.Group>
        </Form.Item>
        {eventIsGroup ? (
          <FormGuideComponent
            title='행사 그룹 신청 가능 회원 수'
            guide='한명의 회원이 다수의 회원 신청(익명)이 가능합니다.'
          />
        ) : null}

        {eventIsGroup ? (
          <Form.Item
            name='event_group_limit_cnt'
            rules={[{ required: true }]}
            initialValue={1}
          >
            <InputNumber
              min={1}
              formatter={(value) => `${value} 명`}
              max={maxGroupCnt}
            />
          </Form.Item>
        ) : null}

        <FormGuideComponent
          title='행사 공개 여부'
          guide='예약시스템에서 숨김처리가 가능합니다.'
        />
        <Form.Item
          name='event_is_public'
          rules={[{ required: true }]}
          initialValue={'Y'}
        >
          <Radio.Group>
            <Radio value='Y'>공개</Radio>
            <Radio value='N'>비공개</Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item>
          <Button type='primary' htmlType='submit' block>
            등 록
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default EventFormComponent;
