import React, { useState, useEffect } from 'react';
import { Table, Button } from 'antd';
import uuid from 'react-uuid';
import {
  requestEventJoinDataSheetStatistic,
  requestExportExcel,
  requestDlsAgeStatisticForSheet,
} from './StatisticHttpService';
import { DatePicker } from 'antd';
import Text from 'antd/lib/typography/Text';
import moment from 'moment';
import { FileExcelOutlined } from '@ant-design/icons';

const StatisticDlsAgeStatisticSheetComponent = () => {
  const [isLoading, setIsLoading] = useState();
  const [dataSource, setDataSource] = useState();

  const { RangePicker } = DatePicker;
  const [selectedDate, setSelectedDate] = useState({
    start_date: moment().add(-1, 'month').format('YYYY-MM-DD'),
    end_date: moment().format('YYYY-MM-DD'),
  });
  const columns = [
    {
      title: '일자',
      dataIndex: 'A일자',
      key: uuid(),
      align: 'center',
    },
    {
      title: '영유아(0~5 세)',
      dataIndex: 'B영유아(0~5 세)',
      key: uuid(),
      align: 'center',
    },
    {
      title: '유아(6~7 세)',
      dataIndex: 'C유아(6~7 세)',
      key: uuid(),
      align: 'center',
    },
    {
      title: '초등(8~13 세)',
      dataIndex: 'D초등(8~13 세)',
      key: uuid(),
      align: 'center',
    },
    {
      title: '청소년(14~19 세)',
      dataIndex: 'E청소년(14~19 세)',
      key: uuid(),
      align: 'center',
    },
    {
      title: '20 대',
      dataIndex: 'F20 대',
      key: uuid(),
      align: 'center',
    },
    {
      title: '30 대',
      dataIndex: 'G30 대',
      key: uuid(),
      align: 'center',
    },
    {
      title: '40 대',
      dataIndex: 'H40 대',
      key: uuid(),
      align: 'center',
    },
    {
      title: '50 대',
      dataIndex: 'I50 대',
      key: uuid(),
      align: 'center',
    },
    {
      title: '60 세 이상',
      dataIndex: 'J60 세 이상',
      key: uuid(),
      align: 'center',
    },
  ];

  useEffect(() => {
    fillDateInTable(selectedDate.start_date, selectedDate.end_date);
    return () => {};
  }, []);

  const fillDateInTable = (start_date, end_date) => {
    setIsLoading(true);
    requestDlsAgeStatisticForSheet(start_date, end_date).then((res) => {
      console.log(res);
      setDataSource(res.data.datas);
      setIsLoading(false);
    });
  };

  return (
    <div style={{ width: '100%' }}>
      <div>
        <Text>기간 선택 : </Text>
        <RangePicker
          style={{ width: '400px', margin: '10px 0px' }}
          defaultValue={[
            moment(selectedDate.start_date),
            moment(selectedDate.end_date),
          ]}
          onChange={(e) => {
            console.log(e);
            if (e) {
              selectedDate.start_date = e[0].format('YYYY-MM-DD');
              selectedDate.end_date = e[1].format('YYYY-MM-DD');
              fillDateInTable(
                e[0].format('YYYY-MM-DD'),
                e[1].format('YYYY-MM-DD')
              );
            }
          }}
        />
        <Button
          type='link'
          onClick={(e) => {
            requestExportExcel(
              dataSource,
              '기간별 DLS 스마트 추천 연령 현황 시트'
            );
          }}
        >
          <FileExcelOutlined />
          Excel 다운로드
        </Button>
      </div>
      <Table
        rowKey={(dataSource) => uuid()}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        size='small'
        bordered
        // pagination={{
        //   pageSize: 10,
        // }}
      />
    </div>
  );
};

export default StatisticDlsAgeStatisticSheetComponent;
