import React, { useState, useEffect } from 'react';
import { TimePicker, Button, Space } from 'antd';
import moment from 'moment';
import axios from 'axios';
import * as Constants from '../../Constants';
import { confirm } from '../../ConfirmModal';

const ItemLoanDetailMasterOperationTimeComponent = (props) => {
  const {
    itemMaster = {
      ...props.itemMaster,
      item_master_operation_start_time: '',
      item_master_operation_end_time: '',
    },
    setItemMaster,
  } = props;
  const [isEditable, setIsEditable] = useState(false);
  const [timeValue, setTimeValue] = useState([]);

  useEffect(() => {
    if (itemMaster.item_master_operation_start_time) {
      setTimeValue([
        moment(itemMaster.item_master_operation_start_time, 'HH:mm'),
        moment(itemMaster.item_master_operation_end_time, 'HH:mm'),
      ]);
    }
  }, [props]);

  const modifyOperationTime = () => {
    if (timeValue.length > 2) return;
    confirm({
      title: `운영 시간을 변경하시겠습니까?`,
      content: ``,
      onOk: () => {
        axios
          .post(`/is/updateIsItemOperationTime`, {
            item_master_idx: itemMaster.item_master_idx,
            item_master_operation_start_time: timeValue[0].format('HH:mm'),
            item_master_operation_end_time: timeValue[1].format('HH:mm'),
          })
          .then((res) => {
            setItemMaster(res.data.itemMaster);
            setIsEditable(false);
          })
          .catch((err) => {
            console.error(err);
          });
      },
      onCancle: () => {
        setIsEditable(false);
      },
    });
  };
  return (
    <div>
      <Space>
        <TimePicker.RangePicker
          className='operationTimePick'
          style={{ width: '360.67px' }}
          format={'HH:mm'}
          disabled={!isEditable}
          value={timeValue}
          onChange={(e) => {
            setTimeValue(e);
          }}
        />
        <Button
          type='primary'
          className={isEditable ? 'operationTimeApplyBtn' : ''}
          onClick={() => {
            if (isEditable) {
              modifyOperationTime();
            } else {
              setIsEditable(!isEditable);
            }
          }}
        >
          {isEditable ? '적 용' : '변 경'}
        </Button>
      </Space>
    </div>
  );
};

export default ItemLoanDetailMasterOperationTimeComponent;
