import axios from 'axios';

// index.tsx에서 호출
export const setAxiosDefaultUrl = () => {
  process.env.NODE_ENV === 'development' ? (axios.defaults.baseURL = SERVER_URL[SITE.TAK].DEV) : (axios.defaults.baseURL = SERVER_URL[SITE.TAK].PROD);
};

export type ServerUrl = {
  PROD: string;
  DEV: string;
};

export const SITE = {
  GES_DEV: 'GES_DEV',
  LOCALHOST: 'LOCALHOST',
  TAK: 'TAK',
} as const;

export const SERVER_URL: { [key: string]: ServerUrl } = {
  [SITE.GES_DEV]: {
    PROD: 'http://183.102.131.99:28080',
    DEV: 'http://183.102.131.99:28080',
  },
  [SITE.LOCALHOST]: {
    PROD: 'http://localhost:28080',
    DEV: 'http://localhost:28080',
  },
  [SITE.TAK]: {
    PROD: 'http://192.168.0.101:28080',
    DEV: 'http://192.168.0.101:28080',
  },
};
