/* eslint-disable no-undef */
import { CaretDownOutlined, SettingOutlined } from '@ant-design/icons';
import { Button, Collapse, Descriptions, Modal, Space } from 'antd';
import Text from 'antd/lib/typography/Text';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { confirm } from '../../ConfirmModal';
import ItemLoanDetailMasterBlockComponent from './ItemLoanDetailMasterBlockComponent';
import ItemLoanDetailMasterEquipComponent from './ItemLoanDetailMasterEquipComponent';
import ItemLoanDetailMasterLoanReturnStatusComponent from './ItemLoanDetailMasterLoanReturnStatusComponent';
import ItemLoanDetailMasterOperationTimeComponent from './ItemLoanDetailMasterOperationTimeComponent';
import ItemLoanDetailMasterStatusComponent from './ItemLoanDetailMasterStatusComponent';

const ItemLoanDetailMasterComponent = (props) => {
  const [isBlockSettingVisible, setIsBlockSettingVisible] = useState(false);
  const [equipList, setEquipList] = useState([]);
  const [selectedEquipList, setSelectedEquipList] = useState([]);
  const {
    itemMaster = {
      item_master_idx: '',
      item_master_subject: '',
      item_master_desc: '',
      total_item_cnt: '',
      item_master_is_block: '',
      item_status_state: '',
      is_item_loaning_cnt: '',
      is_item_ready_cnt: '',
      is_reserve_cnt: '',
      is_inspection_cnt: '',
      is_block_cnt: '',
      group_concat_equip_idx: '',
      item_master_is_editable: '',
    },
    setItemMaster,
  } = props;

  useEffect(() => {
    getIsEquipList();
    setSelectedEquipList(
      itemMaster.group_concat_equip_idx
        ? itemMaster.group_concat_equip_idx
            .split(',')
            .map((item) => parseInt(item))
        : []
    );

    return () => {};
  }, [props]);

  const deleteIsItemMaster = (YN) => {
    confirm({
      title: `물품을 삭제하시겠습니까?`,
      content: ``,
      onOk: () => {
        axios
          .post(`/is/deleteItemMaster`, {
            item_master_is_del: YN,
            item_master_idx: itemMaster.item_master_idx,
          })
          .then((res) => {
            props.history.replace('/is', {});
          })
          .catch((err) => {
            console.error(err);
          });
      },
      onCancle: () => {},
    });
  };

  const modifyIsItemMasterIsBlock = (YN) => {
    if (itemMaster.item_master_is_block === 'Y') {
      Modal.error({
        width: 500,
        title: `물품 상태를 정상운영 상태로 변경 할 수 없습니다.`,
        content: `대여 불가 기간 및 이용시간을 확인해주세요`,
        onOk() {},
      });
    } else {
      confirm({
        title: `물품 상태를 변경하시겠습니까?`,
        content: ``,
        onOk: () => {
          axios
            .post(`/is/modifyIsItemMasterIsBlock`, {
              item_master_is_block: YN,
              item_master_idx: itemMaster.item_master_idx,
            })
            .then((res) => {
              setItemMaster(res.data);
            })
            .catch((err) => {
              console.error(err);
            });
        },
        onCancle: () => {},
      });
    }
  };

  const onChangeEquip = (e) => {
    confirm({
      title: `적용 IS를 변경하시겠습니까?`,
      content: ``,
      onOk: () => {
        axios
          .post(`/is/modifyIsItemEquip`, {
            item_master_idx: itemMaster.item_master_idx,
            equipIdxList: e,
          })
          .then((res) => {
            setItemMaster(res.data);
          })
          .catch((err) => {
            console.error(err);
          });
      },
      onCancle: () => {},
    });
  };

  const getIsEquipList = () => {
    axios
      .get(`/equip/index`, {
        params: {
          eqpType: 'DRS',
        },
      })
      .then((res) => {
        setEquipList(res.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <div
      className='ItemLoanDetailMasterComponentWrap'
      onClick={(e) => {
        if (isBlockSettingVisible) {
          setIsBlockSettingVisible(false);
        }
      }}
    >
      <Descriptions bordered>
        <Descriptions.Item label='물품 명' span={3}>
          <Text>{`${
            itemMaster.item_master_subject ? itemMaster.item_master_subject : ''
          }`}</Text>
        </Descriptions.Item>
        <Descriptions.Item label='물품 설명' span={3}>
          <Text>
            {itemMaster.item_master_desc && (
              <pre
                style={{ fontFamily: 'NotoSansKR-Bold', fontSize: '0.98em' }}
              >
                {itemMaster.item_master_desc}
              </pre>
            )}
          </Text>
        </Descriptions.Item>
        <Descriptions.Item label='물품 상태' span={3}>
          <ItemLoanDetailMasterStatusComponent
            modifyIsItemMasterIsBlock={modifyIsItemMasterIsBlock}
            isBlockSettingVisible={isBlockSettingVisible}
            setIsBlockSettingVisible={setIsBlockSettingVisible}
            itemMaster={itemMaster}
          />
        </Descriptions.Item>
        {/* <Descriptions.Item label='SMS 발송 여부' span={1}>
          <ItemLoanDetailMasterIsAutoComponent
            itemMaster={itemMaster}
            setItemMaster={setItemMaster}
          />
        </Descriptions.Item> */}
        <Descriptions.Item label='물품 현황' span={3}>
          <Space direction='horizontal' size='large'>
            <div>전체 : {itemMaster.total_item_cnt}</div>
            <div>대여가능 : {itemMaster.is_item_ready_cnt}</div>
            <div>예약중 : {itemMaster.is_reserve_cnt}</div>
            <div>대여중 : {itemMaster.is_item_loaning_cnt}</div>
            <div>점검중 : {itemMaster.is_inspection_cnt}</div>
            <div>대여불가 : {itemMaster.is_block_cnt}</div>
          </Space>
        </Descriptions.Item>

        <Descriptions.Item label='반납일 상태' span={3}>
          <ItemLoanDetailMasterLoanReturnStatusComponent
            itemMaster={itemMaster}
          />
        </Descriptions.Item>
      </Descriptions>
      <Collapse
        expandIcon={({ isActive }) => (
          <SettingOutlined
            style={{ fontSize: '1.2em', color: 'rgba(0, 0, 0, 0.65)' }}
            rotate={isActive ? 90 : 0}
          />
        )}
        style={{ marginTop: '10px' }}
        bordered={false}
      >
        <Collapse.Panel
          header={
            <Text style={{ cursor: 'pointer' }}>
              {`운영 상세 설정  `}
              <CaretDownOutlined />
            </Text>
          }
          key='1'
        >
          <Descriptions bordered size='small'>
            <Descriptions.Item label='적용된 시스템' span={3}>
              <ItemLoanDetailMasterEquipComponent
                onChangeEquip={onChangeEquip}
                selectedEquipList={selectedEquipList}
                equipList={equipList}
              />
            </Descriptions.Item>
            <Descriptions.Item label='대여 운영 시간 설정' span={3}>
              <ItemLoanDetailMasterOperationTimeComponent
                itemMaster={itemMaster}
                setItemMaster={setItemMaster}
              />
            </Descriptions.Item>
            <Descriptions.Item label='대여 불가 기간 설정' span={3}>
              <ItemLoanDetailMasterBlockComponent
                itemMaster={itemMaster}
                setItemMaster={setItemMaster}
              />
            </Descriptions.Item>
          </Descriptions>

          <div style={{ marginTop: '10px', textAlign: 'right' }}>
            <Space>
              <Link
                to={{
                  pathname: `/loanitem/form/${itemMaster.item_master_idx}`,
                }}
              >
                <Button type='primary'>정보 및 목록 수정</Button>
              </Link>
              {itemMaster.item_master_is_editable === 'Y' ? (
                <Button
                  type='primary'
                  danger
                  onClick={() => deleteIsItemMaster('Y')}
                >
                  삭 제
                </Button>
              ) : (
                <></>
              )}
            </Space>
          </div>
        </Collapse.Panel>
      </Collapse>
    </div>
  );
};

export default ItemLoanDetailMasterComponent;
