import { Button, Descriptions, Input, Modal, Select, Space } from 'antd';
import Text from 'antd/lib/typography/Text';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { getUserFromLas, registBlacklist } from './BlackList.api';

const InputWrapped = styled.div`
  display: flex;
`;

const ActionWrapped = styled.div`
  width: 100%;
  margin: 20px 0px;
`;

const MemberSearchModal = ({ isVisible, setIsVisible }) => {
  const [user, setUser] = useState();
  const [inputText, setInputText] = useState('');
  const [stateCode, setStateCode] = useState();
  const [type, setType] = useState('');

  const findUser = (user_key) => {
    getUserFromLas(user_key)
      .then((res) => {
        console.log(res);
        setStateCode(res.data.status_code);
        if (res.data.status_code === 200) {
          setUser(res.data.response_body);
        }
      })
      .catch(console.log);
  };

  useEffect(() => {
    // 창 닫을 때
    if (!isVisible) {
      setInputText('');
      setUser();
      setStateCode();
    }
  }, [isVisible]);

  return (
    <Modal
      footer={null}
      title='블랙리스트 등록'
      visible={isVisible}
      destroyOnClose
      centered
      onCancel={(e) => {
        setIsVisible(false);
      }}
      maskClosable
      onOk={(e) => {
        setIsVisible(false);
      }}
      style={{ minWidth: '700px' }}
    >
      <div>회원번호 또는 ID 를 입력해주세요.</div>
      <InputWrapped>
        <Input
          onChange={(e) => {
            setInputText(e.target.value);
          }}
          onPressEnter={() => {
            if (inputText.trim()) findUser(inputText.trim());
          }}
        />
        <Button
          onClick={() => {
            findUser(inputText.trim());
          }}
          style={{ margin: '0px 10px' }}
        >
          검색
        </Button>
      </InputWrapped>
      <ActionWrapped>
        {stateCode === 200 && (
          <Space direction='vertical' style={{ width: '100%' }}>
            <Descriptions bordered title='회원 정보' size='small'>
              <Descriptions.Item label='ID'>
                {user?.is_user_id}
              </Descriptions.Item>
              <Descriptions.Item label='이름'>
                {user?.is_user_nm}
              </Descriptions.Item>
              <Descriptions.Item label='전화번호'>
                {user?.is_user_phone}
              </Descriptions.Item>
            </Descriptions>
            <div>
              <Text>블랙리스트 타입 : </Text>
              <Select
                placeholder='타입을 선택해주세요'
                style={{ minWidth: '150px' }}
                onChange={(e) => {
                  setType(e);
                }}
              >
                <Select.Option value='EVENT'>행사</Select.Option>
                <Select.Option value='ITEM'>물품</Select.Option>
              </Select>
            </div>
            <Button
              type='primary'
              block
              style={{ marginTop: '20px' }}
              onClick={() => {
                if (user.is_user_no && type) {
                  registBlacklist(user.is_user_no, type).then((res) => {
                    console.log(res);
                    if (res.data.status_code === 200) {
                      setIsVisible(false);
                    }
                  });
                }
              }}
            >
              등록
            </Button>
          </Space>
        )}

        {stateCode === 404 && <div>회원을 찾을 수 없습니다.</div>}
      </ActionWrapped>
    </Modal>
  );
};

export default MemberSearchModal;
