import { SettingTwoTone } from '@ant-design/icons';
import { Drawer } from 'antd';
import TextArea from 'antd/lib/input/TextArea';
import styled from 'styled-components';

export const Wrapped = styled.div`
  margin-left: 1rem;
`;

export const InnerWrapped = styled.div`
  display: flex;
  align-items: center;
`;

type Icon = {
  isActive?: boolean;
};
export const IconWrapped = styled.div<Icon>`
  width: 4rem;
  border-bottom: 1px inset rgba(29, 150, 224, 0.3);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.35s;
  margin: 0 1rem 0 0;

  ${(props) =>
    props.isActive &&
    `    transform: rotate3d(180, 20, 36, 360deg);
  `}
  &:hover {
    transform: scale(0.9);
  }
`;

export const CardsWrapped = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1.5fr);
  overflow: hidden;
`;
export const TextWrapped = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 4rem;
`;

export const DrawerWrapped = styled(Drawer)``;

export const SettingIconWrapped = styled(SettingTwoTone)`
  margin: 0.5rem;
  font-size: 2rem;
  cursor: pointer;
`;

export const TextAreaWrapped = styled(TextArea)`
  text-align: center;
  cursor: default;
`;
