import usePreventLeave from '@nooks/use-prevent-leave';
import {
  Button,
  DatePicker,
  Form,
  Input,
  InputNumber,
  message,
  Select,
  Space,
} from 'antd';
import TextArea from 'antd/lib/input/TextArea';
import Paragraph from 'antd/lib/typography/Paragraph';
import Text from 'antd/lib/typography/Text';
import axios from 'axios';
import { Map } from 'immutable';
import moment from 'moment';
import 'moment/locale/ko';
import React, { useEffect, useState } from 'react';
import { Prompt } from 'react-router-dom';
import * as Constants from '../../Constants';
import FormGuideComponent from '../../FormGuideComponent';
import MenuGuide from '../../MenuGuide';
import ItemLoanItemEditor from './ItemLoanItemEditor';

const ItemLoanForm = (props) => {
  const [isBack, setIsBack] = useState(true);
  const { enablePrevent, disablePrevent } = usePreventLeave();
  const [loanMode, setLoanMode] = useState('nolimit');
  const [closureList, setClosureList] = useState([]);
  const [itemMaster, setItemMaster] = useState({
    item_master_subject: '',
    item_master_is_editable: 'Y',
  });

  useEffect(() => {
    // 목록을 클릭해 들어왔을 때

    if (props.match.params.loan_idx !== 'new') {
      getLoanItem(props.match.params.loan_idx);
    }

    getClosureSchedule();

    // enablePrevent();
    return () => {
      disablePrevent();
    };
  }, []);

  const getClosureSchedule = () => {
    axios
      .get(`/lib/getLibClosureAfterNow`, {})
      .then((res) => {
        setClosureList(res.data.closureList);
      })
      .catch((err) => {
        console.error(err);
      });
  };
  const disabledDate = (current = moment()) => {
    // Can not select days before today and today

    var closureDate = closureList.find(
      (closure) => closure.closure_date === current.format('YYYY-MM-DD')
    );

    var closureDateBoolean =
      current.format('YYYY-MM-DD') ===
      (closureDate ? closureDate.closure_date : null);

    return (
      current.startOf('day') < moment().startOf('day') || closureDateBoolean // 이것도 지우지 마시오
    );
  };

  const [form] = Form.useForm();
  const validateMessages = {
    required: '필수 항목',
    types: {
      email: 'Not a validate email!',
      number: 'Not a validate number!',
    },
    number: {
      range: 'Must be between ${min} and ${max}',
    },
  };
  const [items, setItems] = useState({});

  const getLoanItem = (itemMasterIdx) => {
    axios
      .get(`/is/getLoanItem`, {
        params: {
          item_master_idx: itemMasterIdx,
        },
      })
      .then((res) => {
        form.setFieldsValue({
          item_master_subject: res.data.item_master_subject,
          item_master_desc: res.data.item_master_desc,
          item_master_loan_mode: res.data.item_master_loan_mode,
          item_master_loan_days: res.data.item_master_loan_days,
          item_master_end_date: moment(
            res.data.item_master_end_date
              ? res.data.item_master_end_date
              : new Date(),
            'YYYY-MM-DD'
          ),
        });
        setLoanMode(res.data.item_master_loan_mode);
        setItemMaster(res.data);
        setItems(
          Map(res.data.itemList)
            .sortBy((item) => item.item_prop_seq)
            .toJS()
        );
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const onFinish = (values) => {
    if (
      Object.keys(items).filter(
        (key) =>
          items[key].item_status_state === 'loaning' ||
          items[key].item_status_state === 'reserve'
      ).length > 0
    ) {
      message.error(
        <Paragraph>
          반납하지 않은 또는 예약된 물품이 있습니다. <br />
          모든 물품이 반납되어야 수정이 가능합니다.
        </Paragraph>
      );
      return;
    }
    if (
      Object.keys(items).filter(
        (key) => items[key] && items[key].item_prop_subject.trim().length === 0
      ).length !== 0
    ) {
      message.error('물품명을 모두 입력해주세요.');
      return;
    }
    if (Object.keys(items).length === 0) {
      message.error('물품을 추가해주세요');
      return;
    }
    if (
      'item_master_end_date' in values &&
      values.item_master_end_date === ''
    ) {
      message.error('반납일을 지정해주세요.');
      return;
    } else if (
      values.item_master_loan_mode === 'end' &&
      values.item_master_end_date
    ) {
      values.item_master_end_date = values.item_master_end_date.format(
        'YYYY-MM-DD'
      );
    }

    var formData = Map(values).toJS();
    var itemArray = [];
    Object.keys(items).forEach((key) => {
      itemArray.push(items[key]);
    });

    formData['itemList'] = itemArray;

    formData['item_master_idx'] = props.match.params.loan_idx;

    axios
      .post(`/is/insertItemLoan`, formData)
      .then((res) => {
        setIsBack(false);

        props.history.push(
          `/loanitem/detail/${res.data.resultMap.item_master_idx}`
        );
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const LoanModeComponent = (props) => {
    if (props.loanMode === 'start') {
      return (
        <>
          <Form.Item
            noStyle
            name='item_master_loan_days'
            style={{ animation: 'fadein 0.25s' }}
            initialValue={3}
          >
            <InputNumber
              className='item-loan-form-limit-days-input'
              style={{ textAlign: 'center', width: '100px' }}
              min={1}
            />
          </Form.Item>
          <Text> 일 후 반납</Text>
        </>
      );
    } else if (props.loanMode === 'end') {
      return (
        <>
          <Form.Item
            noStyle
            name='item_master_end_date'
            style={{ animation: 'fadein 0.25s' }}
          >
            <DatePicker
              disabledDate={disabledDate}
              style={{ width: '150px' }}
              onChange={(e) => {}}
            />
          </Form.Item>
          <Text> 까지 반납</Text>
        </>
      );
    } else {
      return <></>;
    }
  };
  return (
    <div
      style={{
        width: '50%',
        margin: 'auto',
      }}
    >
      <Prompt
        when={isBack}
        message='변경사항이 저장되지 않았습니다. 뒤로가시겠습니까?'
      />
      <MenuGuide
        title={`${itemMaster.item_master_subject} 등록/수정`}
        subTitle={`${itemMaster.item_master_subject} 물품을 등록/수정 할 수 있습니다.`}
      />
      <div>
        <Form
          form={form}
          validateMessages={validateMessages}
          onFinish={onFinish}
        >
          <FormGuideComponent title='대분류-물품' guide='' />
          <Form.Item name='item_master_subject' rules={[{ required: true }]}>
            <Input disabled={itemMaster.item_master_is_editable === 'N'} />
          </Form.Item>
          <FormGuideComponent title='대분류-물품 설명' guide='' />
          <Form.Item name='item_master_desc' rules={[{ required: false }]}>
            <TextArea rows={5} />
          </Form.Item>
          <FormGuideComponent title='대분류-대여 기간 설정' guide='' />
          <Form.Item
            noStyle
            style={{
              display: 'flex',
              flexDirection: 'row',
              justifyContent: 'left',
            }}
          >
            <Space style={{ marginBottom: '20px' }}>
              <Form.Item
                noStyle
                name='item_master_loan_mode'
                initialValue={loanMode}
              >
                <Select
                  value={loanMode}
                  onChange={(e) => {
                    setLoanMode(e);
                  }}
                >
                  <Select.Option value={'nolimit'}>제한 없음</Select.Option>
                  <Select.Option value={'start'}>시작일 기준</Select.Option>
                  <Select.Option value={'end'}>반납일 기준</Select.Option>
                </Select>
              </Form.Item>
              <LoanModeComponent loanMode={loanMode} />
            </Space>
          </Form.Item>

          {itemMaster.item_master_is_editable === 'Y' ? (
            <Form.Item>
              <FormGuideComponent title='소분류-물품' guide='' />
              <ItemLoanItemEditor setItems={setItems} items={items} />
            </Form.Item>
          ) : null}

          <Form.Item>
            <div style={{ margin: '0px 0px 100px 0px' }}>
              <Button type='primary' htmlType='submit' block size='large'>
                {props.match.params.loan_idx !== 'new' ? '수 정' : '등 록'}
              </Button>
            </div>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default ItemLoanForm;
