import {
  UploadOutlined,
  DownloadOutlined,
  WarningFilled,
} from '@ant-design/icons';
import { Button, Upload, message, Space } from 'antd';
import React, { useEffect } from 'react';
import axios from 'axios';
import * as Constants from '../Constants';
import confirm from 'antd/lib/modal/confirm';
import './LgsFileUploadComponent.css';
import Text from 'antd/lib/typography/Text';

const LgsFileUploadComponent = (props) => {
  const { getExcelList } = props;
  const downloadExcel = () => {
    axios({
      method: 'GET',
      url: `/files/ayExcel`,
      responseType: 'blob', // 가장 중요함
    }).then((response) => {
      const url = window.URL.createObjectURL(
        new Blob([response.data], { type: response.headers['content-type'] })
      );
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'ayang_excel_data.xlsx');
      document.body.appendChild(link);
      link.click();
    });
  };

  const downloadExcelOrigin = () => {
    axios({
      method: 'GET',
      url: `/files/ayExcelOrigin`,
      responseType: 'blob', // 가장 중요함
    }).then((response) => {
      const url = window.URL.createObjectURL(
        new Blob([response.data], { type: response.headers['content-type'] })
      );
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'ayang_excel_data_org.xlsx');
      document.body.appendChild(link);
      link.click();
    });
  };

  const dummyRequest = ({ file, onSuccess }) => {
    var formData = new FormData();
    formData.append('file', file);

    confirm({
      icon: <WarningFilled style={{ color: 'red' }} />,
      title: (
        <Text style={{ fontWeight: 'bold' }}>
          해당 엑셀 파일을 업로드하시겠습니까?
        </Text>
      ),
      content: (
        <Text style={{ color: 'red' }}>
          기존 데이터가 해당 파일의 데이터로 즉시 대체됩니다.
        </Text>
      ),
      onOk: () => {
        axios
          .post(`/lgs/file`, formData)
          .then((res) => {
            onSuccess('ok');
            getExcelList();
            message.info('업로드 완료');
          })
          .catch((err) => {
            console.error(err);
          });
      },
      okText: `확인`,
      cancelText: '취소',
    });

    // setTimeout(() => {
    //   onSuccess('ok');
    // }, 0);
  };

  const beforeUpload = (file, fileList) => {
    return true;
  };
  return (
    <div
      style={{
        margin: '20px 0px',
        width: '250px',
        // display: 'flex',
        // flexDirection: 'row',
        // justifyContent: 'space-evenly',
      }}
    >
      <Space>
        <Upload
          className='excel-upload'
          beforeUpload={beforeUpload}
          customRequest={dummyRequest}
        >
          <Button>
            <UploadOutlined /> 엑셀 업로드
          </Button>
        </Upload>
        <Button onClick={downloadExcel}>
          <DownloadOutlined />
          엑셀 다운로드
        </Button>
        <Button onClick={downloadExcelOrigin}>
          <DownloadOutlined />
          엑셀 원본 다운로드
        </Button>
      </Space>
    </div>
  );
};

export default LgsFileUploadComponent;
