import { Button, Input, Popconfirm } from 'antd';
import React, { useState } from 'react';
import styled from 'styled-components';

const NumberInput = styled(Input)`
  width: 6em;
`;
const ManagementDcsPlaylistTimeControlPanelWrapped = styled.div`
  width: 130px;
  display: flex;
  justify-content: space-between;
`;
type ManagementDcsPlaylistTimeControlPanelProps = {
  setPlayDuration: React.Dispatch<React.SetStateAction<number>>;
};
const ManagementDcsPlaylistTimeControlPanel = ({
  setPlayDuration,
}: ManagementDcsPlaylistTimeControlPanelProps) => {
  const [inputValue, setInputValue] = useState<number>(10);
  return (
    <ManagementDcsPlaylistTimeControlPanelWrapped>
      <NumberInput
        type='number'
        size='small'
        suffix={'초'}
        min={1}
        value={inputValue}
        onChange={(e) => {
          setInputValue(Number(e.target.value));
        }}
      />
      <Popconfirm
        title='적용하시겠습니까?'
        onConfirm={() => {
          setPlayDuration(inputValue);
        }}
      >
        <Button type='default' size='small'>
          적용
        </Button>
      </Popconfirm>
    </ManagementDcsPlaylistTimeControlPanelWrapped>
  );
};

export default ManagementDcsPlaylistTimeControlPanel;
