import React, { useEffect, useState } from 'react';
import {
  Bar,
  BarChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';
import { requestDlsAgeStatisticForChart } from './StatisticHttpService';

const StatisticDlsAgeStatisticChartComponent = (props) => {
  const [periodData, setPeriodData] = useState([]);

  const getDlsAgeStatisticForChart = () => {
    requestDlsAgeStatisticForChart()
      .then((res) => {
        console.log(res);
        setPeriodData(res.data.datas);
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {});
  };

  useEffect(() => {
    getDlsAgeStatisticForChart();
    return () => {};
  }, []);

  return (
    <div style={{ width: '100%', height: '300px' }}>
      {/* <div>
        <Select
          defaultValue={period}
          onChange={(e) => {
            setPeriod(e);
            getOfficeStatisticForPeriod(e);
          }}
        >
          <Select.Option value={7}>최근 7일간</Select.Option>
          <Select.Option value={30}>최근 30일간</Select.Option>
        </Select>
      </div> */}

      <ResponsiveContainer>
        <BarChart data={periodData}>
          <CartesianGrid strokeDasharray='3 3' />
          <XAxis dataKey='연령' />
          <YAxis domain={[0, 'dataMax + 1']} allowDecimals={false} unit='건' />
          <Tooltip />
          <Bar dataKey='조회수' fill='#8884d8' />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};
export default StatisticDlsAgeStatisticChartComponent;
