import { Button, Divider, Input } from 'antd';
import { motion } from 'framer-motion';
import styled from 'styled-components';

export const DateWrapped = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

export const TitleWrapped = styled.header`
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
`;
export const RangePickerWrapped = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const InnerColumnButton = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  flex-grow: 0.1;
`;

export const TitleTextWrapped = styled.p`
  margin: auto;
`;

export const DivideWrapped = styled(Divider)`
  height: 100px;
`;

export const ButtonWrapped = styled(Button)`
  width: 100%;
`;

export const InputWrapped = styled(Input)`
  width: 100%;
`;
// style={{ fontFamily: 'bmjua' }}
export const MotionDivWrapped = styled(motion.div)`
  font-family: 'bmjua';
`;
