/* eslint-disable react-hooks/exhaustive-deps */
import { DeleteOutlined, EyeOutlined } from '@ant-design/icons';
import DragIndicatorIcon from '@material-ui/icons/DragIndicator';
import { Col, Input, Row, Tooltip } from 'antd';
import React, { useEffect, useState } from 'react';
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd';
import uuid from 'react-uuid';
import { modalDcsPreview } from './ManagementDcsFilePreviewComponent';

const ManagementDcsContentsListComponent = (props) => {
  const [fileList, setFileList] = useState([]);
  const [selectedScreen, setSelectedScreen] = useState('');
  const { onChangeFileList } = props;

  useEffect(() => {
    console.log(fileList);
    setFileList(props.fileList ? createSeqFileList() : []);
    setSelectedScreen(props.selectedScreen ? props.selectedScreen : '');
  }, [props]);

  // required :: props.fileList
  // props.fileList가 비어있을 때 예외처리를 안함
  // 반드시 props.fileList가 있을 때 사용해야함
  const createSeqFileList = () => {
    var copiedFileList = Array.from(props.fileList);
    copiedFileList.forEach((item, idx) => {
      if (!('seq' in item)) {
        item['seq'] = idx;
      }
      if (!('duration' in item)) {
        item['duration'] = 10;
      }
    });

    setFileList(copiedFileList);
    return copiedFileList;
  };

  const onDragEnd = (result) => {
    // dropped outside the list
    if (!result.destination) {
      return;
    }
    const fixedItems = reorder(
      fileList,
      result.source.index,
      result.destination.index
    );

    fixedItems.forEach((item, idx) => {
      item['seq'] = idx;
    });
    setFileList(fixedItems);
    onChangeFileList(selectedScreen, fixedItems);
  };

  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
    return result;
  };

  const grid = 2;
  const getItemStyle = (isDragging, draggableStyle) => ({
    // some basic styles to make the items look a bit nicer
    userSelect: 'none',
    padding: grid * 2,
    margin: `0 0 ${grid}px 0`,
    // change background colour if dragging
    background: isDragging ? '#ecf0f188' : 'white',
    borderRadius: '8px',
    width: '100%',
    // styles we need to apply on draggables
    ...draggableStyle,
  });

  const onDeleteFile = (targetIdx) => {
    if (typeof targetIdx === 'number') {
      let copiedFileList = Array.from(fileList);
      copiedFileList.splice(targetIdx, 1);
      copiedFileList.forEach((copiedFile, idx) => {
        copiedFile.seq = idx;
      });
      onChangeFileList(selectedScreen, copiedFileList);
    }
  };

  const onChangeDuration = (targetIdx, duration) => {
    const copiedFileList = Array.from(fileList);
    copiedFileList[targetIdx]['duration'] = duration;
    onChangeFileList(selectedScreen, copiedFileList);
  };
  return (
    <div style={{ marginTop: '15px' }}>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId={uuid()}>
          {(provided, snapshot) => (
            <div {...provided.droppableProps} ref={provided.innerRef}>
              <div style={{ marginTop: '15px' }}>
                {fileList.map((item, index) => {
                  return (
                    <Draggable
                      key={`key-${index}`}
                      draggableId={`id-${index}`}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          style={getItemStyle(
                            snapshot.isDragging,
                            provided.draggableProps.style
                          )}
                        >
                          <Row>
                            <Col span={11}>
                              <div style={{ display: 'flex' }}>
                                <span {...provided.dragHandleProps}>
                                  <DragIndicatorIcon
                                    style={{
                                      color: '#dfe6e9',
                                      verticalAlign: 'middle',
                                    }}
                                  />
                                </span>
                                <div
                                  className={`content-title`}
                                  title={item.name}
                                  onClick={() => {
                                    console.log(item);

                                    modalDcsPreview({ item });
                                  }}
                                >
                                  {item.name.length > 20
                                    ? `${item.name.substring(0, 21)}...`
                                    : item.name}
                                </div>
                              </div>
                            </Col>
                            <Col
                              span={6}
                              offset={1}
                              style={{ textAlign: 'center' }}
                            >
                              <Input
                                style={{ width: '70%', borderColor: '#ecf0f1' }}
                                size='small'
                                value={item.duration}
                                disabled={
                                  item.type.includes('video') ? true : false
                                }
                                onChange={(e) => {
                                  const { value } = e.target;
                                  const reg = /^-?[0-9]*(\.[0-9]*)?$/;
                                  if (
                                    (!isNaN(value) && reg.test(value)) ||
                                    value === '' ||
                                    value === '-'
                                  ) {
                                    onChangeDuration(index, value);
                                  }
                                }}
                              />{' '}
                              초
                            </Col>
                            <Col
                              span={4}
                              offset={1}
                              style={{ textAlign: 'center' }}
                            >
                              <Tooltip title='미리보기'>
                                <EyeOutlined
                                  style={{
                                    cursor: 'pointer',
                                    fontSize: '1.3em',
                                    marginRight: '10px',
                                  }}
                                  onClick={() => {
                                    modalDcsPreview({ item });
                                  }}
                                />
                              </Tooltip>
                              <Tooltip title='삭제'>
                                <DeleteOutlined
                                  style={{
                                    cursor: 'pointer',
                                    fontSize: '1.3em',
                                  }}
                                  onClick={(e) => {
                                    onDeleteFile(index);
                                  }}
                                />
                              </Tooltip>
                            </Col>
                          </Row>
                        </div>
                      )}
                    </Draggable>
                  );
                })}
              </div>
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
      {fileList.length === 0 ? (
        <div style={{ textAlign: 'center', marginTop: '100px' }}>
          <span style={{ fontStyle: 'italic' }}>등록된 컨텐츠가 없습니다.</span>
        </div>
      ) : null}
    </div>
  );
};

export default ManagementDcsContentsListComponent;
