import { ExclamationCircleOutlined, MobileOutlined } from '@ant-design/icons';
import { Button, Descriptions, Divider, Popover, Modal } from 'antd';
import confirm from 'antd/lib/modal/confirm';
import Text from 'antd/lib/typography/Text';
import axios from 'axios';
import React, { useState } from 'react';
import * as Constants from '../../Constants';
import ItemLoanDetailPropStatusComponent from './ItemLoanDetailPropStatusComponent';
import ItemLoanSmsComponent from './ItemLoanSmsComponent';
import ItemLoanDetailItemHistoryComponent from './ItemLoanDetailItemHistoryComponent';

const ItemLoanDetailItemComponent = (props) => {
  const {
    itemDetail = {
      is_user_id: '',
      is_user_info: '',
      is_user_nm: '',
      is_user_phone: '',
      item_master_idx: 0,
      item_prop_idx: 0,
      item_prop_reg_date: '',
      item_prop_seq: '',
      item_prop_subject: '',
      item_prop_uuid: '',
      item_status_is_sys: '',
      item_status_reg_date: '',
      item_status_start_dttm: '',
      item_status_state: '',
      item_status_user: '',
    },
    idx,
    itemMaster,
    setItemMaster,
  } = props;

  const [isItemHistoryModal, setIsItemHistoryModal] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  return (
    <div
      className='itemLoanDetailItem'
      onClick={(e) => {
        if (isVisible) {
          setIsVisible(false);
        }
      }}
    >
      <ItemLoanDetailItemHistoryComponent
        isItemHistoryModal={isItemHistoryModal}
        setIsItemHistoryModal={setIsItemHistoryModal}
        itemDetail={itemDetail}
      />
      <Descriptions
        title={`${idx + 1}번 ${itemMaster.item_master_subject}`}
        bordered
        size='small'
        className={`desc-${itemDetail.item_status_state}-wrap`}
      >
        <Descriptions.Item label='소분류-물품 명' span={3}>
          {itemDetail.item_prop_subject}
        </Descriptions.Item>

        <Descriptions.Item label='물품 상태' span={1}>
          <ItemLoanDetailPropStatusComponent
            itemDetail={itemDetail}
            setItemMaster={setItemMaster}
            isVisible={isVisible}
            setIsVisible={setIsVisible}
          />
        </Descriptions.Item>
        <Descriptions.Item label='대여 이력' span={2}>
          <Button
            type='link'
            onClick={() => {
              setIsItemHistoryModal(true);
            }}
          >
            보기
          </Button>
        </Descriptions.Item>
        {itemDetail.item_status_state === 'reserve' ||
        itemDetail.item_status_state === 'loaning' ? (
          <>
            <Descriptions.Item label='대여자 정보' span={1}>
              {itemDetail.is_user_info}
            </Descriptions.Item>
            <Descriptions.Item label='연락처' span={2}>
              {itemDetail.is_user_phone}
            </Descriptions.Item>
            {/* <Descriptions.Item label="SMS" span={1}>
              <ItemLoanSmsComponent itemDetail={itemDetail} />
            </Descriptions.Item> */}
            <Descriptions.Item label='대여일' span={3}>
              {itemDetail.item_status_start_dttm}
            </Descriptions.Item>
            <Descriptions.Item label='반납일' span={3}>
              {itemDetail.item_status_end_dttm}
            </Descriptions.Item>
            {itemDetail.item_status_state === 'reserve' ||
            itemDetail.item_status_state === 'loaning' ? (
              <Descriptions.Item label='물품 상태 처리' span={3}>
                {itemDetail.item_status_state === 'reserve' ? (
                  <Button
                    className='item-loan-btn'
                    type='primary'
                    block
                    style={{ height: '80px', fontSize: '2em' }}
                    onClick={() => {
                      confirm({
                        title: `${itemDetail.item_prop_subject}을 대출 승인 하시겠습니까?`,
                        icon: <ExclamationCircleOutlined />,
                        content: (
                          <Text>
                            반납 처리 후{' '}
                            <Text
                              style={{ color: '#077707', fontWeight: 'bold' }}
                            >
                              대여 중 상태
                            </Text>
                            로 전환됩니다.
                          </Text>
                        ),
                        okText: '확인',
                        cancelText: '취소',
                        onOk() {
                          axios
                            .post(`/is/confirmIsItemProp`, itemDetail)
                            .then((res) => {
                              setItemMaster(res.data);
                            })
                            .catch((err) => {
                              console.error(err);
                            });
                        },
                        onCancel() {},
                      });
                    }}
                  >
                    대출 확인
                  </Button>
                ) : (
                  <></>
                )}
                {itemDetail.item_status_state === 'loaning' ? (
                  <Button
                    className='item-return-btn'
                    type='primary'
                    block
                    style={{ height: '80px', fontSize: '2em' }}
                    onClick={() => {
                      confirm({
                        title: `${itemDetail.item_prop_subject}을 반납 처리 하시겠습니까?`,
                        icon: <ExclamationCircleOutlined />,
                        content: (
                          <Text>
                            반납 처리 후{' '}
                            <Text
                              style={{ color: '#3498db', fontWeight: 'bold' }}
                            >
                              대여 가능 상태
                            </Text>
                            로 전환됩니다.
                          </Text>
                        ),
                        okText: '확인',
                        cancelText: '취소',
                        onOk() {
                          axios
                            .post(`/is/returnIsItemProp`, itemDetail)
                            .then((res) => {
                              setItemMaster(res.data);
                            })
                            .catch((err) => {
                              console.error(err);
                            });
                        },
                        onCancel() {},
                      });
                    }}
                  >
                    반납 처리
                  </Button>
                ) : (
                  <></>
                )}
              </Descriptions.Item>
            ) : (
              <></>
            )}
          </>
        ) : (
          <></>
        )}
      </Descriptions>
      <Divider dashed />
    </div>
  );
};

export default ItemLoanDetailItemComponent;
