import React from 'react';
import {
  Bar,
  BarChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';

const StatisticEventPeriodChart = ({ periodData = [] }) => {
  return (
    <div style={{ width: '100%', height: '300px' }}>
      <ResponsiveContainer>
        <BarChart data={periodData}>
          <CartesianGrid strokeDasharray='3 3' />
          <XAxis dataKey='일자' />
          <YAxis domain={[0, 'dataMax + 1']} allowDecimals={false} unit='건' />
          <Tooltip />
          <Bar dataKey='신청' stackId='a' fill='#8884d8' />
          <Bar dataKey='취소' stackId='a' fill='#82ca9d' />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default StatisticEventPeriodChart;
