import React, { FC, useCallback, useEffect, useRef, useState } from 'react';
import { DragDropContext, Draggable, Droppable, DroppableProvided, DroppableStateSnapshot, DropResult } from 'react-beautiful-dnd';
import {
  CaptionItemWrapped,
  CaptionSpan,
  DeleteOutlinedWrapped,
  DragIndicatorIconWrapped,
  ItemsWrapped,
  NoticeBox,
  SpanIconWrapped,
} from './CaptionItems.styled';
import { v4 as uuid } from 'uuid';
import { useDispatch, useSelector } from 'react-redux';
import { fetchDeleteCaptionContents, fetchPutCaptionContents, LSS_DETAIL, selectLssDetail } from '../../../../../slice/LssSlice';
import { Input, Popconfirm } from 'antd';
import { ContentsType } from '../../../../../types/Lss';

const grid: number = 2;
const getItemStyle = (isDragging: any, draggableStyle: any) => ({
  userSelect: 'none',
  padding: grid * 2,
  margin: `0 0 ${grid}px 0`,
  background: isDragging ? '#ecf0f188' : 'white',
  borderRadius: '8px',
  width: '100%',
  ...draggableStyle,
});
const reorder = (list: Array<any>, startIndex: number, endIndex: number) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);
  return result;
};
//드래그끝나면 재정렬해야함
type CaptionType = Partial<ContentsType>;

type CaptionComponentType = {
  captionList?: Array<CaptionType>;
  innerText?: string;
  uuid?: string;
  selectedUuid?: string;
};

const CaptionItems: FC = () => {
  const dispatch = useDispatch();
  const state = useSelector(selectLssDetail);
  const [captionList, setCaptionList] = useState<Array<CaptionType>>(state[LSS_DETAIL].caption);
  const [selectedItemUUID, setSelectedItemUUID] = useState('');
  const [selectedCaption, setSelectedCaption] = useState('');
  const [editMode, setEditMode] = useState(false);
  const InputTextRef = useRef<Input>(null);
  //drawer가 out되고 다시 나타나더라도 텍스트를 가지고있기 위해 존재하는 상태값

  /**
   * 자막의 내용, 삭제아이콘을 반환합니다
   */
  const CatpionContent: FC<CaptionComponentType> = ({ uuid, innerText }: CaptionComponentType) => {
    const onConfirm = useCallback(
      (e: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => {
        e && e.stopPropagation();
        const data = JSON.parse(JSON.stringify(captionList));
        //선택된 요소를 삭제후 put을 전송합니다.
        const result = data.filter((caption: ContentsType) => caption.uuid !== uuid);
        dispatch(fetchDeleteCaptionContents(result));
      },
      [captionList]
    );
    const onCancel = useCallback((e: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => {
      e && e.stopPropagation();
      resetDefaultState();
    }, []);
    return (
      <SpanIconWrapped>
        <CaptionSpan data-set={'caption'}>{innerText}</CaptionSpan>
        <Popconfirm title="삭제 하시겠습니까?" okText="삭제" onConfirm={onConfirm} onCancel={onCancel} okType="primary">
          <DeleteOutlinedWrapped />
        </Popconfirm>
      </SpanIconWrapped>
    );
  };

  /**
   * 내용을 수정할때 input박스가 나오는 기능
   */
  const ChangeModeComponent: FC = () => {
    const onKeyDown = useCallback((e: React.KeyboardEvent<HTMLInputElement>) => {
      if (e.code === 'Escape') {
        e.stopPropagation();
        e.preventDefault();
        resetDefaultState();
      }
    }, []);
    /**
     * input 생성시 포커스맞춤
     */
    useEffect(() => {
      const target = InputTextRef.current as Input;
      target.setValue(selectedCaption);
      target.focus({ preventScroll: true }); //preventscroll설정을 false로 할 경우 drawer 관련 랜더링 깨짐 증상이 있음
    }, []);
    return <Input ref={InputTextRef} maxLength={100} onPressEnter={onPressEnter} onKeyDown={onKeyDown}></Input>;
  };

  /**
   * 선택아이템,아이템,editmode 값들을 초기화합니다.
   */
  const resetDefaultState = useCallback(() => {
    setSelectedItemUUID('');
    setSelectedCaption('');
    setEditMode(false);
  }, []);

  /**
   * 현재 인풋이 아니라 drawer화면의 아무곳이나 클릭하면 수정을 취소하는 훅스함수
   */
  useEffect(() => {
    const $el = document.querySelector('[data-caption="caption"]');
    $el &&
      $el.addEventListener('click', (e) => {
        const target = e.target as HTMLInputElement;
        //input요소를 클릭하면 그냥 함수종료
        if (target.classList.contains('ant-input')) {
          return;
        }
        resetDefaultState();
      });
  }, []);

  /**
   * uuid 생성하는 훅스
   */
  useEffect(() => {
    const data = JSON.parse(JSON.stringify(state[LSS_DETAIL].caption));
    data.forEach((caption: CaptionType) => {
      caption['uuid'] = uuid();
    });
    setCaptionList(data);
  }, [state[LSS_DETAIL].caption]);

  /**
   * 자막을 클릭하면 입력상태를 변경함
   */
  const onCaptionClick = (e: React.MouseEvent<HTMLDivElement, MouseEvent>, item: CaptionType) => {
    e.preventDefault();
    e.stopPropagation();

    //선택된 아이템이 다르면 편집모드를 끄고, 현재 편집모드일 경우 다른곳을 클릭하면 편집모드를 취소시킨다.
    const target = e.target as HTMLElement;
    //자막이 들어있는 span이 아니라면 모두 취소
    if (target.dataset.set !== 'caption') {
      return;
    }
    // 다른 아이템을 클릭하면 입력상태를 초기화시킴
    if (selectedItemUUID !== item.uuid && editMode) {
      resetDefaultState();
      return;
    }
    //선택된아이템을 설정하고 편집모드로 설정함
    if (item.uuid) {
      setSelectedItemUUID(item.uuid);
      setSelectedCaption(item.content as string);
      setEditMode(true);
    }
  };
  /**
   * 엔터 클릭시 수정된 내용으로 서버로 전송
   */
  const onPressEnter = (e: React.KeyboardEvent<HTMLInputElement>) => {
    e.preventDefault();
    //빈값이면 그냥 리턴
    const target = e.target as HTMLTextAreaElement;
    const inputText = target.value;
    if (inputText.trim() === '') {
      return;
    }
    const data = JSON.parse(JSON.stringify(captionList));
    data.forEach((caption: CaptionType) => {
      if (selectedItemUUID === caption.uuid) {
        caption.content = inputText;
      }
    });
    dispatch(fetchPutCaptionContents(data));
    resetDefaultState();
  };

  /**
   * 드래그앤드랍 정렬후 uuid 재생성하고 서버로 전송함
   */
  const onDragEnd = (result: DropResult): void => {
    if (!result.destination) {
      return;
    }
    const fixedItems = reorder(captionList, result.source.index, result.destination.index);
    const data = JSON.parse(JSON.stringify(fixedItems));
    data.forEach((caption: CaptionType) => {
      caption['uuid'] = uuid();
    });
    setCaptionList(data);
    resetDefaultState();
    dispatch(fetchPutCaptionContents(data));
  };
  return (
    <>
      <NoticeBox isEdit={editMode}>enter키를 누르면 내용을 수정합니다, esc키를 누르면 수정을 취소합니다</NoticeBox>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId={'droppable'} key={`droppable`}>
          {(provided: DroppableProvided, snapshot: DroppableStateSnapshot) => (
            <div {...provided.droppableProps} ref={provided.innerRef}>
              {captionList.map((caption, index) => {
                return (
                  <CaptionItemWrapped
                    key={`caption_${index}`}
                    onClick={(e) => {
                      onCaptionClick(e, caption);
                    }}
                  >
                    <Draggable key={`key-${caption.uuid}`} draggableId={`id-${index}`} index={index}>
                      {(provided, snapshot) => (
                        <div ref={provided.innerRef} {...provided.draggableProps} style={getItemStyle(snapshot.isDragging, provided.draggableProps.style)}>
                          <ItemsWrapped isSelected={editMode && selectedItemUUID !== caption.uuid ? true : false}>
                            <span {...provided.dragHandleProps}>
                              <DragIndicatorIconWrapped />
                            </span>
                            {selectedItemUUID === caption.uuid ? <ChangeModeComponent /> : <CatpionContent innerText={caption.content} uuid={caption.uuid} />}
                          </ItemsWrapped>
                        </div>
                      )}
                    </Draggable>
                  </CaptionItemWrapped>
                );
              })}
              <div>{provided.placeholder}</div>
            </div>
          )}
        </Droppable>
      </DragDropContext>
    </>
  );
};

export default CaptionItems;
