/* eslint-disable jsx-a11y/alt-text */
import { DeleteOutlined, QuestionCircleOutlined } from '@ant-design/icons';
import { Button, Checkbox, Col, Popconfirm, Row, Select, Table } from 'antd';
import Search from 'antd/lib/input/Search';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import uuid from 'react-uuid';
import { noImg } from '../CommonImage';
import MenuGuide from '../MenuGuide';

const ManagementBook = (props) => {
  const { Option } = Select;
  const [isLoading, setIsLoading] = useState(false);
  const [dataSource, setDataSource] = useState([]);
  const [filter, setFilter] = useState('title');
  const [currentPage, setCurrentPage] = useState(sessionStorage.getItem('currentPage') ? Number.parseInt(sessionStorage.getItem('currentPage')) : 1);
  const [filterValue, setFilterValue] = useState();
  const [isUse, setIsUse] = useState(false);

  const columns = [
    {
      title: 'NO',
      dataIndex: 'master_no',
      key: '0',
      width: 80,
      align: 'center',
      render: (item, record, idx) => {
        var currentPage = Number.parseInt(sessionStorage.getItem('currentPage'));
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: '도서명',
      dataIndex: 'title',
      key: '1',
      align: 'left',
      render: (text, record, index) => {
        return (
          <Link
            to={{
              pathname: '/book/form',
              mode: 'modify',
              record: record,
            }}
          >
            {text ? text : '제목없음'}
          </Link>
        );
      },
    },
    {
      title: '표지',
      dataIndex: 'org_name',
      key: '2',
      align: 'center',
      render: (text, record, index) => {
        return (
          <img
            src={`${text}`}
            style={{ width: '50px' }}
            onError={(error) => {
              error.currentTarget.src = noImg;
            }}
          />
        );
      },
    },
    {
      title: '위치',
      dataIndex: 'shelf_loc_name',
      width: 150,
      key: '3',
      align: 'center',
    },
    { title: '저자', dataIndex: 'author', key: '4', align: 'center' },
    {
      title: '등록일',
      dataIndex: 'reg_dttm',
      width: 200,
      key: '5',
      align: 'center',
    },
    // {
    //   title: '사용여부',
    //   dataIndex: 'useYN',
    //   key: '9',
    //   width: 100,
    //   align: 'center',
    // },
    {
      title: '삭제',
      dataIndex: 'delete',
      width: 80,
      align: 'center',
      key: uuid(),
      render: (text, record, index) => {
        return (
          <Popconfirm
            title="삭제하시겠습니까?"
            icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
            onConfirm={() => {
              axios
                .post('/book/recommend/delete', {
                  master_no: record.master_no,
                })
                .then((res) => {
                  console.log(res);
                  getBookIndex({
                    value: filterValue || '',
                  });
                })
                .catch((err) => {
                  console.error(err);
                });
            }}
            onCancel={() => {
              return;
            }}
            okText="예"
            cancelText="아니오"
          >
            <DeleteOutlined
              style={{ fontSize: '1.5em', color: '#c0392b' }}
              onClick={(e) => {
                e.preventDefault();
              }}
            />
          </Popconfirm>
        );
      },
    },
  ];

  useEffect(() => {
    getBookIndex();
  }, []);

  const getBookIndex = (value) => {
    setIsLoading(true);
    if (!value) {
      value = {};
    }
    axios
      .get('/bookV2/librariansRecommendBook', {
        params: {
          filter: filter,
          value: 'value' in value ? value.value : '',
          useYN: 'useYN' in value ? value.useYN : '',
        },
      })
      .then((res) => {
        console.log(res);
        setTimeout(() => {
          setDataSource(res.data.response_body.books);
          setIsLoading(false);
        }, 1000);
      })
      .catch((err) => {
        console.error(err);
        setIsLoading(false);
      });
  };

  const onChangeSelectFilter = (e) => {
    setFilter(e);
  };

  const selectFilter = (
    <Select defaultValue="title" style={{ minWidth: '90px' }} onChange={onChangeSelectFilter}>
      <Option key="title">도서명</Option>
      {/* <Option key="isbn13">ISBN</Option> */}
      <Option key="author">저자</Option>
    </Select>
  );

  const searchBook = (filterTitle) => {
    getBookIndex({
      value: filterValue,
      useYN: isUse ? 'Y' : '',
    });
  };

  return (
    <>
      <Row>
        <Col span={12}>
          <MenuGuide title="사서 추천 도서" subTitle="도서 등록을 통해 사서 추천 도서 관리가 가능합니다." />
        </Col>
        <Col span={12}>
          <Row style={{ marginTop: '10px' }}>
            <Col span={24}>
              <div
                style={{
                  width: '100%',
                  textAlign: 'right',
                  margin: '10px',
                }}
              >
                <Checkbox
                  checked={isUse}
                  onChange={(e) => {
                    setIsUse(e.target.checked);
                    getBookIndex({
                      useYN: e.target.checked ? 'Y' : '',
                      value: filterValue,
                    });
                  }}
                  disabled={isLoading}
                >
                  <span
                    style={{
                      cursor: 'pointer',
                      fontFamily: 'NanumGothic',
                    }}
                  >
                    사용중인 추천 도서만 보기
                  </span>
                </Checkbox>
              </div>
              <Search
                addonBefore={selectFilter}
                enterButton
                value={filterValue}
                onChange={(e) => {
                  setFilterValue(e.target.value);
                }}
                onSearch={(value) => {
                  searchBook();
                }}
                style={{ marginBottom: '10px' }}
              />
            </Col>
          </Row>
        </Col>
      </Row>
      <Table
        rowKey={(dataSource) => dataSource.master_no}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        bordered
        size="small"
        pagination={{
          pageSize: 10,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', e);
            setCurrentPage(e);
          },
        }}
      ></Table>
      <div id="regBtn" style={{ width: '100%', textAlign: 'center' }}>
        <Link
          to={{
            pathname: '/book/form',
            state: {
              mode: 'regist',
            },
          }}
        >
          <Button type="primary" style={{ width: '100px', marginBottom: '100px' }}>
            등 록
          </Button>
        </Link>
      </div>
    </>
  );
};

export default ManagementBook;
