import React from 'react';
import { Popover, Badge, Space, Modal } from 'antd';

const ItemLoanDetailMasterStatusComponent = (props) => {
  const { isBlockSettingVisible, setIsBlockSettingVisible, itemMaster, modifyIsItemMasterIsBlock } = props;

  const isBlockPopOver = () => {
    return (
      <Space direction="vertical">
        <Badge
          status="processing"
          text="정상 운영"
          onClick={(e) => {
            if (itemMaster.item_master_is_block === 'Y') {
              Modal.error({
                title: '변경이 불가합니다.',
                content: '운영시간 또는 대여 불가 기간을 확인해주세요.',
              });
            } else if (itemMaster.item_master_is_block === 'N' && itemMaster.item_master_is_block_origin === 'Y') {
              modifyIsItemMasterIsBlock('N');
            }
            setIsBlockSettingVisible(false);
          }}
        />
        <Badge
          status="default"
          text="대여 불가"
          onClick={(e) => {
            if (itemMaster.item_master_is_block_origin === 'N') {
              modifyIsItemMasterIsBlock('Y');
            }
            setIsBlockSettingVisible(false);
          }}
        />
      </Space>
    );
  };

  return (
    <Popover content={isBlockPopOver} title="물품 상태 변경" trigger="click" className="isBlockSetting" visible={isBlockSettingVisible}>
      {itemMaster.item_master_is_block_origin === 'Y' || (itemMaster.item_master_is_block_origin === 'N' && itemMaster.item_master_is_block === 'Y') ? (
        <Badge
          status="default"
          text="대여 불가"
          onClick={() => {
            setIsBlockSettingVisible(true);
          }}
        />
      ) : (
        <Badge
          status="processing"
          text="정상 운영"
          onClick={() => {
            setIsBlockSettingVisible(true);
          }}
        />
      )}
    </Popover>
  );
};

export default ItemLoanDetailMasterStatusComponent;
