import React, { FC, memo, useCallback } from 'react';
import { ContentsType } from '../../../../../types/Lss';
import NoticeCards from './NoticeCards';
import DrawerTitle from './NoticeDrawTitle';
import { v4 as uuid } from 'uuid';
import { useDispatch, useSelector } from 'react-redux';
import { fetchPostNoticeContents, LSS_DETAIL, selectLssDetail } from '../../../../../slice/LssSlice';
import styles from './Notice.module.scss';
import { useGetUsedNotice } from './hooks/useGetUseNotice';
import { Wrapped, InnerWrapped, IconWrapped, SettingIconWrapped, TextWrapped, TextAreaWrapped, DrawerWrapped, CardsWrapped } from './Notice.styled';

type NoticeType = {
  isVisible: boolean;
  notice: Array<ContentsType>;
  setIsVisible: React.Dispatch<React.SetStateAction<boolean>>;
};

const Notice: FC<NoticeType> = ({ isVisible, setIsVisible, notice }) => {
  const dispatch = useDispatch();
  const state = useSelector(selectLssDetail);
  //is_used가 y로 설정된 값을 가져옴
  const selectedContent = useGetUsedNotice(notice);

  //3.
  const onNoticePost = (content: ContentsType) => {
    content.lss_master_idx = state[LSS_DETAIL].idx;
    dispatch(fetchPostNoticeContents({ ...content }));
  };

  const onClickAddIcon = useCallback((e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    onNoticePost({
      type: 'NOTICE',
      lss_content_idx: 0,
      lss_master_idx: 0,
      content: '메모',
      is_used: 'N',
    });
  }, []);
  const onClick = useCallback(() => {
    setIsVisible(true);
  }, []);
  const onClose = useCallback(() => {
    setIsVisible(false);
  }, []);

  const items = notice.map((item, index) => (
    <NoticeCards
      key={uuid()}
      type={item.type}
      content={item.content}
      is_used={item.is_used}
      lss_content_idx={item.lss_content_idx}
      lss_master_idx={item.lss_master_idx}
    />
  ));

  return (
    <Wrapped>
      <InnerWrapped>
        <IconWrapped isActive={isVisible} onClick={onClick}>
          <SettingIconWrapped twoToneColor={'#3b80ff'} />
        </IconWrapped>
        <TextWrapped className={styles.text}>
          <h5>미리보기</h5>
          <TextAreaWrapped
            disabled={true}
            value={selectedContent ? selectedContent.content : '적용된 메모가 없습니다'}
            maxLength={25 * 6}
            autoSize={{ maxRows: 6 }}
            cols={1}
            rows={6}
          ></TextAreaWrapped>
        </TextWrapped>
      </InnerWrapped>
      <DrawerWrapped
        className={styles.drawer}
        title={<DrawerTitle onClick={onClickAddIcon} />}
        placement="right"
        closable={true}
        visible={isVisible}
        getContainer={false}
        onClose={onClose}
        width={'100%'}
        maskStyle={{ backgroundColor: 'rgba(0,0,0,0)' }}
      >
        <CardsWrapped>{items}</CardsWrapped>
      </DrawerWrapped>
    </Wrapped>
  );
};

export default Notice;
