import React, { useEffect } from 'react';
import { Modal } from 'antd';

import ReactPlayer from 'react-player';
import { DEV_SERVER } from '../Constants';
import Axios from 'axios';

export const modalDcsPreview = (props) => {
  const { item } = props;
  console.log(props);
  let sourceUrl = '';
  if (item && item.file_status && item.file_status === 'done') {
    if (item.type.includes('image')) {
      sourceUrl = `${Axios.defaults.baseURL}/files/dcs/stream/${item.saveName}`;
    } else {
      sourceUrl = `${Axios.defaults.baseURL}/files/dcs/stream/video/${item.saveName}`;
    }
  } else {
    console.log('usrlcreateobjecturl');
    sourceUrl = URL.createObjectURL(item.originFileObj);
  }
  Modal.confirm({
    title: ``,
    width: 600,
    className: 'modal-preview',
    maskClosable: true,
    content: (
      <div style={{ display: 'flex', justifyContent: 'center' }}>
        {item.type.includes('image')
          ? imagePlayer(sourceUrl)
          : videoPlayer(sourceUrl)}
      </div>
    ),
    okText: '확인',
  });
};

const imagePlayer = (url) => {
  return <img style={{ maxWidth: '400px' }} src={url} alt={`imagePreview`} />;
};
const videoPlayer = (url) => {
  return (
    <ReactPlayer
      width='100%'
      height='100%'
      style={{ maxWidth: '500px' }}
      config={{
        file: {
          attributes: {
            // preload: 'none',
            controlsList: 'nodownload',
          },
        },
      }}
      url={url}
      playing={url ? true : false}
      muted
    />
  );
};

const ManagementDcsFilePreviewComponent = (props) => {
  return <div></div>;
};

export default ManagementDcsFilePreviewComponent;
