import React, { FC, useCallback, useEffect, useState } from 'react';
import { Popconfirm, Radio, RadioChangeEvent } from 'antd';
import { ModesType } from '../../../../../types/Lss';
import { Wrapped } from './RadioMode.styled';
import { useDispatch, useSelector } from 'react-redux';
import { fetchPutMode, LSS_DETAIL, selectLssDetail } from '../../../../../slice/LssSlice';

type RadioModeType = {
  value: ModesType;
};

/**
 *
 * @param value 라디오그룹에서 기본으로 정의된 데이터입니다.
 * @returns
 */
const RadioMode: FC<RadioModeType> = ({ value }) => {
  const [mode, setMode] = useState(value);
  const [prevMode, setPrevMode] = useState(value);
  const dispatch = useDispatch();
  const state = useSelector(selectLssDetail);

  useEffect(() => {
    setMode(value);
  }, [value]);

  //모드 변경을 위한 이벤트 핸들러
  const onConfirmPutMode = useCallback(
    (mode: ModesType) => {
      dispatch(
        fetchPutMode({
          equip_idx: state[LSS_DETAIL].equip_idx,
          mode: mode,
        })
      );
    },
    [value]
  );

  const onChangeMode = useCallback(
    (e: RadioChangeEvent) => {
      const mode = e.target.value as ModesType;
      setMode(mode);
    },
    [value]
  );
  const onCancelMode = useCallback(
    (e: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => {
      setMode(prevMode);
    },
    [prevMode]
  );
  const onConfirm = useCallback(
    (e: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => {
      setPrevMode(mode);
      onConfirmPutMode(mode);
    },
    [mode, onConfirmPutMode]
  );

  return (
    <>
      <Wrapped>
        <Radio.Group onChange={onChangeMode} value={mode}>
          <Popconfirm title="저장 하시겠습니까?" okText="저장" onConfirm={onConfirm} onCancel={onCancelMode} okType="primary">
            <Radio value={'LOAN'}>대출일</Radio>
          </Popconfirm>

          <Popconfirm title="저장 하시겠습니까?" okText="저장" onConfirm={onConfirm} onCancel={onCancelMode} okType="primary">
            <Radio value={'CLOSURE'}>휴관일</Radio>
          </Popconfirm>

          <Popconfirm title="저장 하시겠습니까?" okText="저장" onConfirm={onConfirm} onCancel={onCancelMode} okType="primary">
            <Radio value={'MEMO'}>메시지</Radio>
          </Popconfirm>
        </Radio.Group>
      </Wrapped>
    </>
  );
};

export default RadioMode;
