import { Button } from 'antd';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import MenuGuide from '../MenuGuide';
import BlackTable from './BlackTable';
import MemberSearchModal from './MemberSearchModal';
import MemberTable from './MemberTable';

const BlackListContainerWrapped = styled.div`
  width: 100%;
`;
const TableWrapped = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
`;

const BlackListContainer = () => {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {}, [isVisible]);
  return (
    <BlackListContainerWrapped>
      <MenuGuide
        title={'블랙리스트'}
        subTitle={'도서관 회원의 행사/물품의 신청을 제한 할 수 있습니다.'}
      />
      <Button
        type='default'
        onClick={() => {
          setIsVisible(true);
        }}
        style={{ margin: '10px 0px' }}
      >
        블랙리스트 등록하기
      </Button>
      <MemberSearchModal
        isVisible={isVisible}
        setIsVisible={(visible) => setIsVisible(visible)}
      />

      <TableWrapped>
        <BlackTable />
      </TableWrapped>
    </BlackListContainerWrapped>
  );
};

export default BlackListContainer;
