import { FilterOutlined } from '@ant-design/icons';
import { Button, Collapse, Descriptions, Space, Input } from 'antd';
import Text from 'antd/lib/typography/Text';
import React from 'react';

const LgsFilterComponent = (props) => {
  const { lgsFilterList = [], setLgsFilterList, onSubmitLgsFilterList } = props;

  const getFilter = (value) => {
    if (!lgsFilterList.length) {
      return '';
    }

    if (value === 'jonghap') {
      return lgsFilterList.filter(
        (filter) => filter.filter_name === 'jonghap'
      )[0].filter_value;
    } else if (value === 'kids') {
      return lgsFilterList.filter((filter) => filter.filter_name === 'kids')[0]
        .filter_value;
    } else if (value === 'baby') {
      return lgsFilterList.filter((filter) => filter.filter_name === 'baby')[0]
        .filter_value;
    }
  };

  const onChangeFilterValue = (type = '', value = '') => {
    let idx = lgsFilterList.findIndex((item) => item.filter_name === type);

    lgsFilterList[idx].filter_value = value;

    setLgsFilterList(Array.from(lgsFilterList));
  };

  return (
    <div>
      <Collapse
        expandIcon={({ isActive }) => (
          <FilterOutlined
            style={{ fontSize: '1.2em', color: 'rgba(0, 0, 0, 0.65)' }}
          />
        )}
        style={{ marginTop: '10px' }}
        // bordered={false}
      >
        <Collapse.Panel
          header={<Text style={{ cursor: 'pointer' }}>신간 상세 설정</Text>}
          key='1'
        >
          <Descriptions bordered size='small'>
            <Descriptions.Item label='종합 신간' span={3}>
              <Input
                value={getFilter('jonghap')}
                onChange={(e) => {
                  onChangeFilterValue(
                    'jonghap',
                    String(e.target.value).toUpperCase()
                  );
                }}
              />
            </Descriptions.Item>
            <Descriptions.Item label='유아 신간' span={3}>
              <Input
                value={getFilter('baby')}
                onChange={(e) => {
                  onChangeFilterValue(
                    'baby',
                    String(e.target.value).toUpperCase()
                  );
                }}
              />
            </Descriptions.Item>
            <Descriptions.Item label='아동 신간' span={3}>
              <Input
                value={getFilter('kids')}
                onChange={(e) => {
                  onChangeFilterValue(
                    'kids',
                    String(e.target.value).toUpperCase()
                  );
                }}
              />
            </Descriptions.Item>
          </Descriptions>

          <div style={{ marginTop: '10px', textAlign: 'right' }}>
            <Space>
              <Button type='primary' onClick={onSubmitLgsFilterList}>
                적용
              </Button>
            </Space>
          </div>
        </Collapse.Panel>
      </Collapse>
    </div>
  );
};

export default LgsFilterComponent;
