import {
  DeleteOutlined,
  EyeOutlined,
  QuestionCircleOutlined,
} from '@ant-design/icons';
import { Button, Checkbox, Modal, Popconfirm, Table } from 'antd';
import { ColumnsType } from 'antd/lib/table';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { v4 as uuid } from 'uuid';
import {
  CAPTION,
  fetchCreateCaption,
  fetchDeleteCaption,
  fetchGetCaptionList,
  selectCaption,
} from '../../slice/CaptionSlice';
import { CaptionsListType } from '../../types/Caption';
import DcsPreviewComponent from '../dcs-preview/DcsPreviewComponent';
import MenuGuide from '../MenuGuide';

const ManagementCaption = () => {
  const dispatch = useDispatch();
  const [equipList, setEquipList] = useState([]);
  const history = useHistory();
  const state = useSelector(selectCaption)[CAPTION];
  const [dataSource, setDataSource] = useState<CaptionsListType>([]);
  const [isUsingEquip, setIsUsingEquip] = useState(false);
  const [currentPage, setCurrentPage] = useState(
    sessionStorage.getItem('currentPage')
      ? Number.parseInt(sessionStorage.getItem('currentPage')!)
      : 1
  );

  useEffect(() => {
    const getEquipList = () => {
      axios
        .get<any, any>(`/equip/index`, {
          params: {
            eqpType: '',
          },
        })
        .then((res) => {
          setEquipList(res.data);
        })
        .catch((err) => {
          console.error(err);
        });
    };
    getEquipList();
  }, []);
  useEffect(() => {}, [state.captionsList]);
  useEffect(() => {
    dispatch(fetchGetCaptionList());
  }, [isUsingEquip]);

  useEffect(() => {
    //captionslist가 변경되었을때.
    if (equipList && state.captionsList) {
      //작업후 데이터소스에 삽입.
      const result = [...JSON.parse(JSON.stringify(state.captionsList))];
      console.log(result);
      result.forEach((item, index) => {
        item.equiplength = `${item.equipsIdxList.length}개`;
      });
      setDataSource(result);
    }
  }, [state.captionsList]);

  const columns: ColumnsType<any> = [
    {
      title: 'NO',
      dataIndex: '',
      align: 'center',
      width: 50,
      key: uuid(),
      render: (item: any, record: any, idx: number) => {
        var currentPage = Number.parseInt(
          sessionStorage.getItem('currentPage')!
        );
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: '제목',
      dataIndex: 'title',
      align: 'center',
      width: 200,
      key: uuid(),
      render: (
        text:
          | boolean
          | React.ReactChild
          | React.ReactFragment
          | React.ReactPortal
          | null
          | undefined,
        record: any,
        index: any
      ) => {
        return (
          <Link
            to={{
              pathname: `/caption/form/${record.caption_idx}`,
              state: { mode: 'modify', record: record },
            }}
          >
            {text}
          </Link>
        );
      },
    },
    {
      title: '설명',
      dataIndex: 'desc',
      align: 'center',
      width: 200,
      key: uuid(),
      render: (
        text:
          | boolean
          | React.ReactChild
          | React.ReactFragment
          | React.ReactPortal
          | null
          | undefined,
        record: any,
        index: any
      ) => {
        return (
          <Link
            to={{
              pathname: `/caption/form/${record.caption_idx}`,
              state: { mode: 'modify', record: record },
            }}
          >
            {text}
          </Link>
        );
      },
    },
    {
      title: '사용중인 장치 수',
      dataIndex: 'equiplength',
      align: 'center',
      width: 200,
      key: uuid(),
      render: (
        text:
          | boolean
          | React.ReactChild
          | React.ReactFragment
          | React.ReactPortal
          | null
          | undefined,
        record: any,
        index: any
      ) => {
        return (
          <Link
            to={{
              pathname: `/caption/form/${record.caption_idx}`,
              state: { mode: 'modify', record: record },
            }}
          >
            {text}
          </Link>
        );
      },
    },
    {
      title: '등록일',
      dataIndex: 'reg_date',
      width: 180,
      align: 'center',
      key: uuid(),
    },
    {
      title: '삭제',
      dataIndex: 'delete',
      width: 80,
      align: 'center',
      key: uuid(),
      render: (text: any, record: { caption_idx: number }, index: any) => {
        return (
          <Popconfirm
            title='삭제하시겠습니까?'
            icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
            onCancel={() => {
              return;
            }}
            onConfirm={() => {
              //   console.log(record.caption_idx);
              dispatch(fetchDeleteCaption(record.caption_idx));
              setTimeout(() => {
                dispatch(fetchGetCaptionList());
              }, 100);
            }}
            okText='예'
            cancelText='아니오'
          >
            <DeleteOutlined
              style={{ fontSize: '1.5em', color: '#c0392b' }}
              onClick={(e) => {
                e.preventDefault();
              }}
            />
          </Popconfirm>
        );
      },
    },
  ];
  const onChangeUsingEquip = () => {
    setIsUsingEquip(!isUsingEquip);
  };
  return (
    <>
      <MenuGuide
        title='화면 관리 ▶ 자막관리'
        subTitle='자막 등록이 가능합니다.'
      />
      {/* <div
        style={{
          width: '100%',
          textAlign: 'right',
          margin: '10px',
        }}
      >
        <Checkbox
          onChange={(e) => {
            onChangeUsingEquip();
          }}
          disabled={state?.isSpin}
          checked={isUsingEquip}
        >
          <span>사용중인 자막만 보기</span>
        </Checkbox>
      </div> */}

      <Table
        loading={state.isSpin}
        rowKey={(dataSource) => uuid()}
        dataSource={dataSource}
        columns={columns}
        pagination={{
          pageSize: 10,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', `${e}`);
            setCurrentPage(e);
          },
        }}
        bordered
        size='small'
      ></Table>
      <div id='regBtn' style={{ width: '100%', textAlign: 'center' }}>
        {/* <Link 
          to={{
            pathname: '/caption/form',
            state: {
              mode: 'regist',
            },
          }}
        >
               </Link> */}
        <Button
          type='primary'
          style={{ width: '100px', marginBottom: '100px' }}
          onClick={(e) => {
            // dispatch(fetchCreateCaption());
            // setTimeout(() => {
            //   dispatch(fetchGetCaptionList());
            // }, 100);
            history.push('/caption/form');
          }}
        >
          등 록
        </Button>
      </div>
    </>
  );
};

export default ManagementCaption;
