import { Table } from 'antd';
import React, { useState } from 'react';
import uuid from 'react-uuid';
import styled from 'styled-components';

const MemberTableWrapped = styled.div`
  width: 49%;
`;

const columns = [
  {
    title: 'NO',
    dataIndex: '',
    align: 'center',
    width: 50,
    key: uuid(),
    render: (item, record, idx) => {
      var currentPage = Number.parseInt(sessionStorage.getItem('currentPage'));
      return idx + (currentPage - 1) * 10 + 1;
    },
  },
  {
    title: 'ID',
    dataIndex: 'cSubject',
    align: 'center',
    width: 80,
    key: uuid(),
  },
  {
    title: '이름',
    dataIndex: 'eqpName',
    align: 'center',
    width: 80,
    key: uuid(),
  },
  {
    title: '전화번호',
    dataIndex: 'question_cnt',
    width: 90,
    align: 'center',
    key: uuid(),
  },
  {
    title: '블랙',
    dataIndex: 'question_cnt',
    width: 80,
    align: 'center',
    key: uuid(),
  },
];

const MemberTable = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [dataSource, setDataSource] = useState([]);
  return (
    <MemberTableWrapped>
      <div>회원</div>
      <Table
        rowKey={(dataSource) => dataSource.dcs_idx}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        bordered
        size='small'
      />
    </MemberTableWrapped>
  );
};

export default MemberTable;
