import { Button, Col, Input, message, Row, Select } from 'antd';
import Text from 'antd/lib/typography/Text';
import React, { useEffect, useState } from 'react';
import { Link, Prompt, useHistory } from 'react-router-dom';
import { v4 as uuid } from 'uuid';
import axios from 'axios';
import FormGuideComponent from '../FormGuideComponent';
import MenuGuide from '../MenuGuide';
import CaptionList from './components/CaptionList';
import { useDispatch, useSelector } from 'react-redux';

import { confirm } from '../ConfirmModal';
import {
  CAPTION,
  fetchCreateCaption,
  fetchPutCaption,
  selectCaption,
  setCaptionContents,
  setCaptionDesc,
  setCaptionEquipIdx,
  setCaptionSelectedIdx,
  setCaptionTitle,
} from '../../slice/CaptionSlice';
import { CaptionAddDetailType, CaptionDetailType } from '../../types/Caption';
const ManagementCaptionForm = (props: any) => {
  const history = useHistory();
  const equipIdx = props.match.params?.equip_idx;

  const dispatch = useDispatch();
  const state = useSelector(selectCaption)[CAPTION];
  const [isBack, setIsBack] = useState(true);
  const [equipList, setEquipList] = useState([]);

  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [applyEquip, setApplyEquip] = useState<Array<number>>([]);
  const [captionIdx, setCaptionidx] = useState(equipIdx);

  const mode = props.location.state?.mode;
  useEffect(() => {
    if (mode === 'modify' && props.location.state?.record) {
      const title = props.location.state.record?.title;
      const desc = props.location.state.record?.desc;
      const equipIdxList = props.location.state.record?.equipsIdxList;
      console.log(equipIdxList);
      setTitle(title);
      dispatch(
        setCaptionTitle({
          title: title,
        })
      );
      setDescription(desc);
      dispatch(
        setCaptionDesc({
          desc: desc,
        })
      );
      setApplyEquip(equipIdxList);
      dispatch(
        setCaptionEquipIdx({
          equip_idx: equipIdxList,
        })
      );
    }
    getEquipList();
  }, [props.location.state?.record, dispatch]);

  const getEquipList = () => {
    axios
      .get<any, any>(`/equip/index`, {
        params: {
          eqpType: '',
        },
      })
      .then((res) => {
        setEquipList(res.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const onSubmit = () => {
    // 수정된 제목, 설명, 장비들을 전송한다.
    if (mode === 'modify') {
      confirm({
        title: `자막을 수정하시겠습니까?`,
        content: '',
        onOk: () => {
          if (!title) {
            message.error('제목이 없습니다');
            return;
          }
          const sendData: CaptionDetailType = {
            title: title,
            desc: description,
            equip_idx: applyEquip,
            contents: state.contents,
            caption_idx: captionIdx,
          };
          dispatch(fetchPutCaption(sendData));
          setIsBack(false);
          setTimeout(() => history.push('/screen'), 1000);
        },
      });
    } else {
      confirm({
        title: `자막을 등록하시겠습니까?`,
        content: '',
        onOk: () => {
          if (!title) {
            message.error('제목이 없습니다');
            return;
          }
          const sendData: CaptionAddDetailType = {
            title: title,
            desc: description,
            equip_idx: applyEquip,
            contents: state.contents,
            caption_idx: captionIdx,
          };
          dispatch(fetchCreateCaption(sendData));
          setIsBack(false);
          setTimeout(() => history.push('/screen'), 200);
        },
      });
    }
  };

  const formBlockStyle = {
    marginTop: '25px',
  };

  return (
    <div style={{ width: '100%' }}>
      <Prompt
        when={isBack}
        message='변경사항이 저장되지 않았습니다. 뒤로가시겠습니까?'
      />
      <MenuGuide
        title='자막 등록/수정'
        subTitle='화면별 자막 출력 목록을 편집할 수 있습니다.'
      />
      <Row
        style={{
          border: '1px solid #dfe6e9',
          padding: '50px',
        }}
      >
        <Col
          span={12}
          style={{
            height: '600px',
          }}
        >
          <div>
            <FormGuideComponent title='제목' />
            <Input
              value={title}
              onChange={(e) => {
                setTitle(e.target.value);
              }}
            />
          </div>
          <div style={formBlockStyle}>
            <FormGuideComponent title='설명' />
            <Input
              value={description}
              onChange={(e) => {
                setDescription(e.target.value);
              }}
            />
          </div>
          <div style={formBlockStyle}>
            <FormGuideComponent title='기기 선택' />
            <Select
              showArrow
              mode='multiple'
              value={applyEquip}
              placeholder='자막을 사용할 기기를 선택해주세요'
              style={{ width: '100%' }}
              onChange={(e) => {
                setApplyEquip(e);
              }}
            >
              {equipList.map((item: any, idx) => {
                return (
                  <Select.Option key={uuid()} value={item.idx}>
                    {item.eqpName} ({item.idx})
                  </Select.Option>
                );
              })}
            </Select>
          </div>
          <div style={formBlockStyle}>
            <FormGuideComponent title='등록된 장치 수' />
            <Text
              keyboard
              style={{
                marginTop: '20px',
              }}
            >
              {applyEquip.length}개
            </Text>
          </div>
          <div style={formBlockStyle}>
            <FormGuideComponent title='등록된 자막 수' />
            <Text
              keyboard
              style={{
                marginTop: '20px',
              }}
            >
              {state.contents.length}개
            </Text>
          </div>
          <div
            style={{
              width: '100%',
              position: 'absolute',
              bottom: 0,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
            }}
          >
            <Button type='primary' block onClick={onSubmit}>
              업 로 드
            </Button>
          </div>
        </Col>
        <Col
          span={12}
          style={{
            height: '600px',
          }}
        >
          <CaptionList
            title={title}
            desc={description}
            equip_idx={applyEquip}
            captionIdx={captionIdx}
          />
        </Col>
      </Row>
      {/* <UploadingProgressIndicator isProgress={isProgress} progressPercentage={progressPercentage} /> */}
    </div>
  );
};

export default ManagementCaptionForm;
