import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

const ManagementPollComplete = (props) => {
  const [pollTitle, setPollTitle] = useState(
    props.history.location.pollTitle ? props.history.location.pollTitle : ''
  );
  const [masterId, setMasterId] = useState(
    props.history.location.masterId ? props.history.location.masterId : ''
  );

  return (
    <div style={{ width: '100%', padding: '20px' }}>
      <div style={{ width: '50%', minWidth: '800px', margin: 'auto' }}>
        <div
          style={{
            width: '100%',
            border: '1px solid #7f8c8d',
            borderTop: '0px',
            borderRadius: '8px',
            minHeight: '120px',
            backgroundColor: 'white',
          }}
        >
          <div
            style={{
              backgroundColor: '#576574',
              borderTopRightRadius: '8px',
              borderTopLeftRadius: '8px',
              width: '100%',
              height: '15px',
            }}
          ></div>
          <div
            style={{
              width: '100%',
              padding: '10px 30px 20px 30px',
            }}
          >
            <p style={{ fontSize: '2em', fontFamily: 'NotoSansKR-Bold' }}>
              {pollTitle}
            </p>
            <pre style={{ fontFamily: 'NotoSansKR-Medium' }}>
              설문을 완료해 주셔서 감사합니다!
            </pre>

            {masterId ? (
              <Link to={`/poll/detail/${masterId}`}>다른 응답 제출하기</Link>
            ) : (
              ''
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManagementPollComplete;
