import { PlusCircleOutlined } from '@ant-design/icons';
import { Button, message, Upload } from 'antd';
import React from 'react';
import styled from 'styled-components';
import './ManagementDcsFileUploadComponent.css';

const ContentsBtn = styled.div`
  user-select: none;
  cursor: pointer;
  margin-top: 5px;
  font-family: NotoSansKR-Bold;
  font-style: italic;
  color: #0984e3;
`;

const UploadWrapped = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

type ManagementDcsFileUploadComponentProps = {
  id: string;
  fileInfoForScreen: any;
  onChangeFileInfo: (fileInfo: any) => void;
  onSelectScreen: (screen: any) => void;
};

const ManagementDcsFileUploadComponent = ({
  id,
  fileInfoForScreen,
  onChangeFileInfo,
  onSelectScreen,
}: ManagementDcsFileUploadComponentProps) => {
  const dummyRequest = ({ onSuccess }: any) => {
    setTimeout(() => {
      onSuccess('ok');
    }, 0);
  };

  const beforeUpload = () => {
    return false;
  };

  return (
    <UploadWrapped>
      <Upload
        showUploadList={false}
        beforeUpload={beforeUpload}
        customRequest={dummyRequest}
        multiple={true}
        fileList={fileInfoForScreen?.fileList}
        onChange={(info) => {
          if (!info) return;
          if (
            !(
              info?.file?.type?.includes('image') ||
              info?.file?.type?.includes('video')
            )
          ) {
            message.error(
              `이미지 또는 비디오 파일만 업로드 가능합니다 : ${info.file.name}`
            );
            return;
          }
          let copiedFileList = Array.from(
            info.fileList.filter(
              (item) =>
                item?.type?.includes('image') || item?.type?.includes('video')
            )
          );
          copiedFileList.forEach((file: any, idx) => {
            file.seq = idx;

            if (
              !('file_status' in file && file.file_status !== 'done') &&
              file.type.includes('img')
            )
              file.duration = 10;
          });
          console.log(copiedFileList);
          onChangeFileInfo({
            id: id,
            // 이미지와 비디오만 필터링
            fileList: copiedFileList,
          });
        }}
      >
        <PlusCircleOutlined
          style={{
            fontSize: '2em',
            cursor: 'pointer',
          }}
        />
      </Upload>

      <ContentsBtn
        onClick={(e) => {
          onSelectScreen(id);
        }}
      >
        <Button>
          {`콘텐츠 (${fileInfoForScreen?.fileList?.length | 0} 개)`}
        </Button>
      </ContentsBtn>
    </UploadWrapped>
  );
};

export default ManagementDcsFileUploadComponent;
