import React, { useState, useEffect } from "react";
import { Row, Col } from "antd";
import DcsPreviewContentViewerComponent from "../DcsPreviewContentViewerComponent";

const Layout_m_h_9 = (props) => {
  const {
    fileInfo = {
      screen1: {
        fileList: [],
      },
      screen2: {
        fileList: [],
      },
      screen3: {
        fileList: [],
      },
      screen4: {
        fileList: [],
      },
      screen5: {
        fileList: [],
      },
      screen6: {
        fileList: [],
      },
    },
  } = props;

  return (
    <div style={{ width: "100%", height: "480px" }}>
      <Row style={{ width: "100%", height: "100%" }}>
        <Col span={22} style={{ width: "100%", height: "100%" }}>
          <div style={{ height: "100%" }}>
            <DcsPreviewContentViewerComponent
              fileList={fileInfo.screen1.fileList}
            />
          </div>
        </Col>
        <Col span={2} style={{ width: "100%", height: "100%" }}>
          <div style={{ height: "100%" }}>
            <DcsPreviewContentViewerComponent
              fileList={fileInfo.screen2.fileList}
            />
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default Layout_m_h_9;
