import { FileExcelOutlined } from '@ant-design/icons';
import { Button, Space, Table } from 'antd';
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import uuid from 'react-uuid';
import { requestExportExcel } from '../StatisticHttpService';

const StatisticEventPerEventDataSheet = ({ dataSource = [] }) => {
  const history = useHistory();
  const columns = [
    {
      title: '행사',
      dataIndex: 'B행사',
      key: uuid(),
      align: 'center',
      render: (item, record, idx) => {
        return (
          <Button
            type='link'
            onClick={() => {
              history.push(`/event/detail/${record['A행사일련번호']}`);
            }}
          >
            {item}
          </Button>
        );
      },
    },

    {
      title: '신청수',
      dataIndex: 'C신청',
      key: uuid(),
      align: 'center',
    },
    {
      title: '취소수',
      dataIndex: 'D취소',
      key: uuid(),
      align: 'center',
    },
    {
      title: '총 합계',
      dataIndex: 'E합계',
      key: uuid(),
      align: 'center',
    },
  ];

  return (
    <Space direction='vertical' style={{ width: '100%' }}>
      <div>
        <Button
          onClick={(e) => {
            requestExportExcel(dataSource, '행사별 신청취소 데이터 시트');
          }}
        >
          <FileExcelOutlined />
          Excel 다운로드
        </Button>
      </div>
      <Table
        rowKey={(dataSource) => uuid()}
        dataSource={dataSource}
        columns={columns}
        size='small'
        bordered
      />
    </Space>
  );
};

export default StatisticEventPerEventDataSheet;
