import React, { useEffect, useState } from 'react';
import MenuGuide from '../../MenuGuide';
import ItemLoanDetailMasterComponent from './ItemLoanDetailMasterComponent';
import * as Constants from '../../Constants';
import axios from 'axios';
import { Map } from 'immutable';
import { Divider, Space } from 'antd';
import ItemLoanDetailItemComponent from './ItemLoanDetailItemComponent';
import './ItemLoanDetail.css';
import Text from 'antd/lib/typography/Text';

const ItemLoanDetailContainer = (props) => {
  const [
    itemMaster = {
      itemList: {},
      item_master_subject: String,
    },
    setItemMaster,
  ] = useState();
  useEffect(() => {
    document.body.className = 'item-loan-detail';

    getItemMasterAndStatus();

    return () => {
      document.body.className = '';
    };
  }, []);

  const getItemMasterAndStatus = () => {
    axios
      .get(`/is/getLoanItemDetail`, {
        params: {
          item_master_idx: props.match.params.item_master_idx,
        },
      })
      .then((res) => {
        console.log(res);
        setItemMaster(Map(res.data).toJS());
      })
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <div>
      <MenuGuide
        title={`일반 물품 상세`}
        subTitle={`일반 물품 대여 상태를 관리할 수 있습니다.`}
      />
      <Divider>
        <Text style={{ fontWeight: 'bold', fontFamily: 'NanumGothic' }}>
          {`${itemMaster.item_master_subject} 정보`}
        </Text>
      </Divider>
      <ItemLoanDetailMasterComponent
        {...props}
        itemMaster={itemMaster}
        setItemMaster={setItemMaster}
      />
      <Divider>
        <Text style={{ fontWeight: 'bold', fontFamily: 'NanumGothic' }}>
          {`${itemMaster.item_master_subject} 목록`}
        </Text>
      </Divider>
      <Space
        direction='vertical'
        style={{ width: '100%', marginBottom: '100px' }}
        size='large'
      >
        {Object.keys(
          Map(itemMaster.itemList)
            .sortBy((item) => item.item_prop_seq)
            .toJS()
        ).map((key, idx) => {
          return (
            <ItemLoanDetailItemComponent
              key={key}
              idx={idx}
              itemDetail={itemMaster.itemList[key]}
              setItemMaster={setItemMaster}
              itemMaster={itemMaster}
            />
          );
        })}
      </Space>
    </div>
  );
};

export default ItemLoanDetailContainer;
