import { Button } from 'antd';
import styled from 'styled-components';

export const Wrapped = styled.div`
  width: 100%;
`;
export const InnerWrapped = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

export const SetupIconWrapped = styled.div`
  font-size: 3rem;
  cursor: pointer;
`;

export const HeaderWrapped = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

type BadgeWrapperProps = {
  isUsed?: 'Y' | 'N';
};
export const BadgeWrapped = styled.div<BadgeWrapperProps>`
  transform: scale(0.9);
  font-size: 0.65rem;
  ${(props) =>
    props.isUsed === 'N' &&
    `color: rgba(1,1,1,0.5);
  `}
`;

export const ButtonWrapped = styled(Button)`
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const HeaderButtonWrapped = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
