import { Tabs } from 'antd';
import React from 'react';
import StatisticEventContainer from './event/StatisticEventContainer';
import StatisticDlsStatisticChartContainer from './StatisticDlsStatisticChartContainer';

const StatisticContainer = () => {
  function callback(key) {
    sessionStorage.setItem('currentPage', 1);
  }
  return (
    <div
      style={{
        height: '100%',
      }}
    >
      <Tabs onChange={callback} type='card'>
        <Tabs.TabPane tab='사서 추천 시스템' key='1'>
          <StatisticDlsStatisticChartContainer />
        </Tabs.TabPane>
        <Tabs.TabPane tab='행사' key='2'>
          <StatisticEventContainer />
        </Tabs.TabPane>
        {/* <Tabs.TabPane tab='토론실' key='2'>
          <StatisticOfficeContainer />
        </Tabs.TabPane>
        <Tabs.TabPane tab='태블릿/일반물품' key='3'>
          <StatisticItemContainer />
        </Tabs.TabPane> */}
        {/* <Tabs.TabPane tab='문자서비스' key='4'></Tabs.TabPane> */}
      </Tabs>
    </div>
  );
};

export default StatisticContainer;
