import Text from "antd/lib/typography/Text";
import React from "react";

const ItemLoanDetailMasterLoanReturnStatusComponent = (props) => {
  const { itemMaster } = props;
  if (itemMaster.item_master_loan_mode === "start") {
    return (
      <Text>
        대여일 기준{" "}
        <Text style={{ color: "#1e90ff" }}>
          {itemMaster.item_master_loan_days}
        </Text>{" "}
        일 후 반납
      </Text>
    );
  } else if (itemMaster.item_master_loan_mode === "end") {
    return (
      <Text>
        반납일 기준{" "}
        <Text style={{ color: "#1e90ff" }}>
          {itemMaster.item_master_end_date}
        </Text>{" "}
        까지 반납
      </Text>
    );
  } else {
    return <Text>제한 없음</Text>;
  }
};

export default ItemLoanDetailMasterLoanReturnStatusComponent;
