import React from 'react';
import { Route, Switch, useRouteMatch } from 'react-router-dom';
import BannerForm from './banner/BannerForm';
import ManagementBook from './book/ManagementBook';
import ManagementBookForm from './book/ManagementBookForm';
import ManagementCaptionForm from './caption/ManagementCaptionForm';
import ManagementDcsFormV2 from './dcs/ManagementDcsFormV2';
import ManagementEquip from './equip/ManagementEquip';
import ManagementEquipForm from './equip/ManagementEquipForm';
import Help from './Help';
import Home from './home/HomeAsan';
import EventDetailContainer from './is-system/event/EventDetailContainer';
import EventFormContainer from './is-system/event/EventFormContainer';
import ItemLoanDetailContainer from './is-system/item-loan/ItemLoanDetailContainer';
import ItemLoanForm from './is-system/item-loan/ItemLoanForm';
import ManagementLssScreen from './lss/ManagementLssScreen';
import ManagementMagForm from './mag/ManagementMagForm';
import ManagementIs from './ManagementIs';
import ManagementScreen from './ManagementScreen';
import ManagementUser from './ManagementUser';
import ManagementPollForm from './poll/ManagementPollForm';
import ManagementPollResult from './poll/ManagementPollResult';
import ManagementStatic from './statistic/ManagementStatic';

export default function MainRouter() {
  const match = useRouteMatch();
  return (
    <Switch>
      <Route exact path={match.path} component={Home} />
      <Route exact path='/help' component={Help} />
      <Route exact path='/equip/form' component={ManagementEquipForm} />
      <Route
        exact
        path={['/equip', `/equip/:key`]}
        component={ManagementEquip}
      />
      <Route
        exact
        path={['/screen', '/screen/:key']}
        component={ManagementScreen}
      />
      <Route exact path='/book' component={ManagementBook} />
      <Route exact path='/statistic' component={ManagementStatic} />
      <Route exact path='/mag/form' component={ManagementMagForm} />
      <Route exact path='/book/form' component={ManagementBookForm} />
      <Route exact path='/poll/form' component={ManagementPollForm} />
      <Route
        exact
        path='/poll/result/:master_id'
        component={ManagementPollResult}
      />
      <Route exact path='/dcs/form' component={ManagementDcsFormV2} />
      <Route exact path='/user/form' component={ManagementUser} />
      <Route exact path='/event/form' component={EventFormContainer} />
      <Route
        exact
        path='/event/form/:event_master_idx'
        component={EventFormContainer}
      />
      <Route
        exact
        path='/event/detail/:event_master_idx'
        component={EventDetailContainer}
      />
      <Route exact path='/loanitem/form/:loan_idx' component={ItemLoanForm} />
      <Route
        exact
        path='/loanitem/detail/:item_master_idx'
        component={ItemLoanDetailContainer}
      />
      <Route
        exact
        path='/lss/form/:equip_idx'
        component={ManagementLssScreen}
      />
      <Route exact path='/caption/form/' component={ManagementCaptionForm} />
      <Route
        exact
        path='/caption/form/:equip_idx'
        component={ManagementCaptionForm}
      />
      <Route exact path={['/is', '/is/:key']} component={ManagementIs} />
      <Route exact path={['/banner/:banner_idx']} component={BannerForm} />
    </Switch>
  );
}
