import React from 'react';
import {
  Bar,
  BarChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';

const StatisticEventAgedChart = ({ agedData = [] }) => {
  return (
    <div style={{ width: '100%', height: '300px' }}>
      <ResponsiveContainer>
        <BarChart data={agedData}>
          <CartesianGrid strokeDasharray='3 3' />
          <XAxis dataKey='연령대' />
          <YAxis domain={[0, 'dataMax + 1']} allowDecimals={false} unit='건' />
          <Tooltip />
          <Bar dataKey='신청자 수' fill='#2980b9' />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default StatisticEventAgedChart;
