import { Table, Button, Badge } from 'antd';
import axios from 'axios';
import * as Constants from '../../Constants';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import uuid from 'react-uuid';
import MenuGuide from '../../MenuGuide';

const ItemLoadTable = () => {
  const [isLoading, setIsLoading] = useState();
  const [dataSource, setDataSource] = useState();
  const [currentPage, setCurrentPage] = useState(
    sessionStorage.getItem('currentPage')
      ? Number.parseInt(sessionStorage.getItem('currentPage'))
      : 1
  );

  useEffect(() => {
    getLoanItemList();
  }, []);

  const getLoanItemList = () => {
    setIsLoading(true);
    axios
      .get(`/is/getLoanItemList`, {})
      .then((res) => {
        setDataSource(res.data.itemLoanList);
        setIsLoading(false);
      })
      .catch((err) => {
        console.error(err);
        setIsLoading(false);
      });
  };

  const columns = [
    {
      title: 'NO',
      dataIndex: '',
      key: uuid(),
      width: 80,
      align: 'center',
      render: (item, record, idx) => {
        var currentPage = Number.parseInt(
          sessionStorage.getItem('currentPage')
        );
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: '물품 분류',
      dataIndex: 'item_master_subject',
      key: uuid(),
      align: 'center',
      render: (item, record, index) => {
        return (
          <Link to={{ pathname: `/loanitem/detail/${record.item_master_idx}` }}>
            {item}
          </Link>
        );
      },
    },
    {
      title: '상태',
      dataIndex: 'eqp_name',
      key: uuid(),
      align: 'center',
      render: (item, record, index) => {
        if (
          record.item_master_is_block_origin === 'Y' ||
          (record.item_master_is_block_origin === 'N' &&
            record.item_master_is_block === 'Y')
        ) {
          return <Badge status='default' text='대여 불가' />;
        } else {
          return <Badge status='processing' text='정상 운영' />;
        }
      },
    },
    {
      title: '적용 IS',
      dataIndex: 'eqp_name',
      key: uuid(),
      align: 'center',
    },
    {
      title: '수량',
      children: [
        {
          title: '전체 물품 수',
          dataIndex: 'item_prop_total_cnt',
          key: uuid(),
          align: 'center',
          width: 150,
        },
        {
          title: '대여 물품 수',
          dataIndex: 'item_status_loaning_cnt',
          key: uuid(),
          align: 'center',
          width: 150,
        },
        {
          title: '점검 물품 수',
          dataIndex: 'item_status_block_cnt',
          key: uuid(),
          align: 'center',
          width: 150,
        },
      ],
    },
  ];
  return (
    <div>
      <MenuGuide
        title={'일반물품'}
        subTitle={'일반 물품 등록 / 수정 / 상태를 관리할 수 있습니다.'}
      />

      <Table
        rowKey={(dataSource) => uuid()}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        size='small'
        bordered
        pagination={{
          pageSize: 10,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', e);
            setCurrentPage(e);
          },
        }}
      />
      <div id='regBtn' style={{ width: '100%', textAlign: 'center' }}>
        <Link
          to={{
            pathname: '/loanitem/form/new',
          }}
        >
          <Button type='primary' style={{ width: '100px' }}>
            등 록
          </Button>
        </Link>
      </div>
    </div>
  );
};

export default ItemLoadTable;
