import { Table } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import uuid from 'react-uuid';
import { setEqpName } from '../../slice/LssSlice';
import * as Constants from '../Constants';
import MenuGuide from '../MenuGuide';

const ManagementLss = (props) => {
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState();
  const [dataSource, setDataSource] = useState();
  const [currentPage, setCurrentPage] = useState(sessionStorage.getItem('currentPage') ? Number.parseInt(sessionStorage.getItem('currentPage')) : 1);

  useEffect(() => {
    getLssIndex();
  }, []);

  const getLssIndex = () => {
    axios
      .get(`/lss/getLssIndex`, {})
      .then((res) => {
        console.log(res);
        setDataSource(res.data.LssIndex);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const columns = [
    {
      title: 'NO',
      dataIndex: '',
      key: uuid(),
      width: 80,
      align: 'center',
      render: (item, record, idx) => {
        var currentPage = Number.parseInt(sessionStorage.getItem('currentPage'));
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: '시스템명',
      dataIndex: 'eqpName',
      key: uuid(),
      align: 'center',
      render: (item, record, index) => {
        return (
          <Link
            onClick={() => {
              dispatch(
                setEqpName({
                  eqpName: item,
                })
              );
            }}
            to={{
              pathname: `/lss/form/${record.equip_idx}`,
            }}
          >
            {item}
          </Link>
        );
      },
    },
    {
      title: '모드',
      dataIndex: 'mode',
      key: uuid(),
      width: 100,
      align: 'center',
      render: (item, record, index) => {
        if (item === 'LOAN') {
          return '대출안내';
        } else if (item === 'CLOSURE') {
          return '휴관안내';
        } else {
          return '공지안내';
        }
      },
    },
    {
      title: '공지 수',
      key: uuid(),
      dataIndex: 'lss_notice_cnt',
      width: 110,
      align: 'center',
    },
  ];
  return (
    <div>
      <MenuGuide title={'화면 관리 ▶ LSS'} subTitle={'대출일 / 휴관일 / 공지를 설정할 수 있습니다.'} />

      <Table
        rowKey={(dataSource) => dataSource.equip_idx}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        size="small"
        bordered
        pagination={{
          pageSize: 10,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', e);
            setCurrentPage(e);
          },
        }}
      />
    </div>
  );
};

export default ManagementLss;
