import { Tabs } from 'antd';
import React from 'react';
import { useHistory, useRouteMatch } from 'react-router';
import { MatchParams } from '../types/Common';
import BlackListContainer from './blacklist/BlackListContainer';
import EventTableContaner from './is-system/event/EventTableContaner';
import ItemLoadTable from './is-system/item-loan/ItemLoanTable';

const ManagementIs = () => {
  const { TabPane } = Tabs;
  const match = useRouteMatch<MatchParams>();
  const history = useHistory();

  function callback(key: string) {
    sessionStorage.setItem('currentPage', '1');
    history.push(`/is/${key}`);
  }

  return (
    <Tabs
      onChange={callback}
      type='card'
      defaultActiveKey='1'
      activeKey={match.params.key}
    >
      <TabPane tab='행사' key='event'>
        <EventTableContaner />
      </TabPane>

      {/* <TabPane tab='토론실' key='2'>
        <OfficeDetailContainer {...props} />
      </TabPane>
      <TabPane tab='태블릿' key='3'>
        <TabletDetailContainer {...props} />
      </TabPane> */}
      <TabPane tab='일반물품' key='item'>
        <ItemLoadTable />
      </TabPane>
      <TabPane tab='블랙리스트' key='blacklist'>
        <BlackListContainer />
      </TabPane>
    </Tabs>
  );
};

export default ManagementIs;
