import React, { useState, useEffect } from "react";
import { Row, Col } from "antd";
import DcsPreviewContentViewerComponent from "../DcsPreviewContentViewerComponent";

const Layout_m_h_9 = (props) => {
  const {
    fileInfo = {
      screen1: {
        fileList: [],
      },
      screen2: {
        fileList: [],
      },
      screen3: {
        fileList: [],
      },
      screen4: {
        fileList: [],
      },
      screen5: {
        fileList: [],
      },
      screen6: {
        fileList: [],
      },
    },
  } = props;

  return (
    <Row style={{ width: "100%", height: "480px" }}>
      <Col span={24}>
        <Row>
          <Col span={24}>
            <div style={{ width: "100%", height: "240px" }}>
              <DcsPreviewContentViewerComponent
                fileList={fileInfo.screen1.fileList}
              />
            </div>
          </Col>
          <Col span={24}>
            <div style={{ width: "100%", height: "240px" }}>
              <DcsPreviewContentViewerComponent
                fileList={fileInfo.screen2.fileList}
              />
            </div>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default Layout_m_h_9;
