import {
  Divider,
  InputNumber,
  Modal,
  Popconfirm,
  Radio,
  RadioChangeEvent,
  SelectProps,
  Tooltip,
  Typography,
} from 'antd';
import Paragraph from 'antd/lib/typography/Paragraph';
import moment from 'moment';
import React, {
  FC,
  memo,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  fetchPutChangeHoliday,
  LSS_DETAIL,
  selectLssDetail,
} from '../../../../../slice/LssSlice';
import {
  DayOfTheWeek,
  HolidayInfoType,
  PutHolidayType,
} from '../../../../../types/Lss';
import {
  ArticleWrapped,
  BaseDateWrapped,
  ChangeButton,
  DatePickerWrapped,
  ExplanWrapped,
  FooterWrapped,
  FortnightlyWrapped,
  MainWrapped,
  MonthWrapped,
  RadioAntWrapped,
  RadioButtonAntWrapped,
  RadioGroupWrapped,
  RadioTitle,
  RadioWrapped,
  SelectGroupWrapped,
  SelectWrapped,
  TitleTextWrapped,
  TitleWrapped,
  WeeklyWrapped,
} from './HolidaySettingModal.styled';
import styles from './HolidaySettingModal.module.scss';

import options from './options';
import useGenerateDayOfTheWeekKey from './hooks/useGenerateDayOfTheWeekKey';

type HolidaySettingModalType = {
  visible: boolean;
  //외부에서 visible을 바꿀수있는 이벤트를 추가해야함.
  setIsVisible: React.Dispatch<React.SetStateAction<boolean>>;
};
const HolidaySettingModal: FC<HolidaySettingModalType> = ({
  visible,
  setIsVisible,
}) => {
  const dispatch = useDispatch();
  const state = useSelector(selectLssDetail);

  //정규 휴무일을 위한 상태값.
  const [holidayInfo, setHolidayInfo] = useState<HolidayInfoType>({
    type: '임시휴일',
    date: 'month',
    period1: 'day',
    period2: moment().day(0).format('YYYY-MM-DD'),
    value: ['MONDAY'],
  });
  const [prevHolidayInfo, setPrevHolidayInfo] = useState<HolidayInfoType>({
    type: '임시휴일',
    date: 'month',
    period1: 'day',
    period2: moment().day(0).format('YYYY-MM-DD'),
    value: ['MONDAY'],
  });
  const [daily, setDaily] = useState(10);
  //월요일,화요일 ... 일요일 배열을 의미하는 상태값
  const [dayOfWeek, setDayOfWeek] = useState<DayOfTheWeek>(['MONDAY']);

  //격주표시를위한 상태값
  const WeekCnt = useRef(0);
  const OddEven = useRef<'Odd' | 'Even'>('Odd');

  //요일에 맞는 키값을 생성
  const keyOfWeek = useGenerateDayOfTheWeekKey(holidayInfo.value);
  const { Text } = Typography;

  const onCancelHolidayInfo = useCallback(() => {
    setHolidayInfo(prevHolidayInfo);
  }, [holidayInfo, prevHolidayInfo]);
  const getPopupContainer = useCallback(
    (e: HTMLElement) => e.parentElement as HTMLElement,
    []
  );
  /**
   * 설정값이 변경되면 새롭게 상태값을 갱신한다.
   */
  useEffect(() => {
    const result: HolidayInfoType = {
      type: '임시휴일',
      date: state[LSS_DETAIL].date,
      period1: state[LSS_DETAIL].period1,
      period2: moment().day(0).format('YYYY-MM-DD'), //기준일을 뜻하는 값입니다.
      value: state[LSS_DETAIL].value,
    };
    if (state[LSS_DETAIL].date === 'month') {
      setDaily(state[LSS_DETAIL].period2 as number);
    } else {
      setDayOfWeek(state[LSS_DETAIL].value);
    }
    setHolidayInfo(result);
    setPrevHolidayInfo(result);
  }, [
    state[LSS_DETAIL].date,
    state[LSS_DETAIL].period1,
    state[LSS_DETAIL].period2,
    state[LSS_DETAIL].value,
  ]);

  const handleCancel = useCallback(() => {
    setIsVisible(false);
  }, []);

  /**
   * 주간 단위로 선택시 매주 / 격주 선택하는 컴포넌트이다.
   */
  const SelectComponent: FC = () => {
    const onChange = (e: DayOfTheWeek) => {
      if (e.length === 0) {
        const init = [
          'SUNDAY',
          'MONDAY',
          'TUESDAY',
          'WEDNESDAY',
          'THURSDAY',
          'FRIDAY',
          'SATURDAY',
        ];
        setDayOfWeek(init);
        setHolidayInfo({ ...holidayInfo, value: init });
        return;
      }
      //
      setDayOfWeek(e);
      setHolidayInfo({ ...holidayInfo, value: e });
    };

    return (
      <>
        {/* any로 일단 처리함..  antd를 styled로 감쌀 경우 아래와 같은 상황이 발생함 */}
        <SelectWrapped<FC<SelectProps<any>>>
          getPopupContainer={getPopupContainer}
          mode='multiple'
          size={'large'}
          placeholder='휴관일을 선택해주세요'
          onChange={(e) => {
            if (e.length === 0) {
              const init = [
                'SUNDAY',
                'MONDAY',
                'TUESDAY',
                'WEDNESDAY',
                'THURSDAY',
                'FRIDAY',
                'SATURDAY',
              ];
              setDayOfWeek(init);
              setHolidayInfo({ ...holidayInfo, value: init });
              return;
            }
            //
            setDayOfWeek(e);
            setHolidayInfo({ ...holidayInfo, value: e });
          }}
          value={holidayInfo.value}
          options={options}
        ></SelectWrapped>
      </>
    );
  };

  /**
   * 매주 / 격주 라디오버튼으로 구분짓는 컴포넌트.
   */
  const RadioComponent: FC = () => {
    return (
      <Radio.Group
        onChange={(e) => {
          setHolidayInfo({ ...holidayInfo, period1: e.target.value });
        }}
        defaultValue={holidayInfo.period1}
      >
        <RadioButtonAntWrapped value={'weekly'}>매주</RadioButtonAntWrapped>
        <RadioButtonAntWrapped value={'fortnightly'}>
          격주
        </RadioButtonAntWrapped>
      </Radio.Group>
    );
  };

  /**
   * 격주 선택시 나오는 미니 달력
   */
  const BaseDayComponent: FC = () => {
    const onChange = (e: moment.Moment | null) => {
      if (e) {
        setHolidayInfo({ ...holidayInfo, period2: e?.format('YYYY-MM-DD') });
      }
    };

    /**
     * 일요일이 아니면 비활성화 시킨다.
     */
    const onDisabledDate = (current: moment.Moment) => {
      return current.day() === 0 ? false : true;
    };

    /**
     * 격주 선택시 랜더링되는 함수
     */
    const onDateRender = (current: moment.Moment): JSX.Element => {
      const date = current.date();
      const day = current.day();
      const MONDAY = 0;

      // 일주일간격으로 even | odd로 변경하는 로직
      if (WeekCnt.current >= 7) {
        OddEven.current = 'Even';
      } else {
        OddEven.current = 'Odd';
      }
      WeekCnt.current = WeekCnt.current + 1;
      WeekCnt.current = WeekCnt.current % 14;
      if (WeekCnt.current >= 14) {
        OddEven.current = 'Odd';
      }
      // 일요일이면
      if (day === MONDAY) {
        return (
          <div
            key={current.format('YYYY-MM-DD')}
            className={`ant-picker-cell-inner ${styles.sunday} ${styles.day}`}
            data-oddeven={OddEven.current}
            //마우스가 올라오면 odd면 파란색
            onMouseOver={(e) => {
              const target = e.target as HTMLDivElement;
              const oddOrEven = target.dataset.oddeven;
              const color = oddOrEven === 'Odd' ? '#82baee' : '#e45a5a';
              const $el = document.querySelectorAll(
                `.ant-picker-cell-inner[data-oddeven=${oddOrEven}`
              );
              $el.forEach((item) => {
                const target = item as HTMLElement;
                //일요일에 휴무일이 존재하면 색을 그린다.
                const isExistSunday = keyOfWeek.includes(day);
                if (isExistSunday) {
                  target && target.style.setProperty('background-color', color);
                  return;
                }
                //일요일은 색상을 그리지 않는다.
                if (target.classList.contains(styles.sunday)) {
                  return;
                }
                //백그라운드 색을 그린다
                target && target.style.setProperty('background-color', color);
              });
            }}
            //마우스를 나가면 다시 컬러를 원색으로 돌림
            onMouseOut={() => {
              const $el2 = document.querySelectorAll(
                '.ant-picker-cell-inner[data-oddeven=Odd],.ant-picker-cell-inner[data-oddeven=Even]'
              );
              $el2.forEach((item) => {
                const result = item as HTMLDivElement;
                result &&
                  result.style.setProperty('background-color', '#f5f5f5');
                result && result.style.setProperty('color', 'black');
              });
            }}
          >
            {date}
          </div>
        );
      } else {
        //기준일에서 선택한 요일이 존재한다면
        //ex) 월요일, 화요일이 존재하면 그려진다.
        const isExist = keyOfWeek.includes(day);
        if (isExist) {
          return (
            <div
              key={current.format('YYYY-MM-DD')}
              className='ant-picker-cell-inner'
              data-oddeven={OddEven.current}
            >
              {date}
            </div>
          );
        }
      }
      return (
        // 평범한 셀을 렌더링한다.
        <div
          key={current.format('YYYY-MM-DD')}
          className='ant-picker-cell-inner'
        >
          {date}
        </div>
      );
    };
    return (
      <Tooltip placement='top' title={'기준일 설정'}>
        <BaseDateWrapped>
          <DatePickerWrapped
            getPopupContainer={getPopupContainer}
            className={styles.picker}
            defaultValue={moment().day(0)}
            value={moment(holidayInfo.period2)}
            onChange={onChange}
            disabledDate={onDisabledDate}
            dateRender={onDateRender}
          />
        </BaseDateWrapped>
      </Tooltip>
    );
  };

  const TitleComponent: FC = memo(() => {
    return (
      <TitleWrapped>
        <TitleTextWrapped>휴관일 설정</TitleTextWrapped>
      </TitleWrapped>
    );
  });

  const onRadioChange = (e: RadioChangeEvent) => {
    const date = e.target.value;
    const period1 = date === 'month' ? 'day' : 'weekly';
    const value = date === 'week' ? dayOfWeek : [daily.toString()];
    setHolidayInfo({
      ...holidayInfo,
      date: date,
      period1: period1,
      value: value,
    });
  };
  const onChangeConfirm = () => {
    const result: Partial<PutHolidayType> = {};
    result.type = '정기휴일';
    result.date = holidayInfo.date; //month, week
    result.period1 = holidayInfo.period1;
    result.period2 = '';
    if (holidayInfo.period1 === 'day') {
      result.period2 = '';
      result.value = [daily.toString()]; // 월간으로 설정되면 1부터 31일이 설정된다.
    } else {
      result.period2 = holidayInfo.period2; //격주로 설정시 들어가는 기준값.
      result.value = holidayInfo.value;
    }
    if (result) {
      dispatch(fetchPutChangeHoliday(result as PutHolidayType));
      setPrevHolidayInfo(holidayInfo);
      setIsVisible(false);
    }
  };
  return (
    <Modal
      title={<TitleComponent />}
      visible={visible}
      onCancel={handleCancel}
      footer={[]}
    >
      <MainWrapped>
        {/* 월간, 주간으로 조건부 랜더링되는 컴포넌트 */}
        <ArticleWrapped>
          {/* 월간 주간을 나누는 라디오 컴포넌트 */}
          <RadioWrapped>
            <RadioGroupWrapped
              onChange={onRadioChange}
              value={holidayInfo.date}
            >
              <RadioAntWrapped value={'month'}>
                <RadioTitle>월간</RadioTitle>
              </RadioAntWrapped>
              <RadioAntWrapped value={'week'}>
                <RadioTitle>주간</RadioTitle>
              </RadioAntWrapped>
            </RadioGroupWrapped>
          </RadioWrapped>
          <Divider />
          {/* 월간/주간 값으로 조건부 랜더링되는 설명 부분 컴포넌트 */}
          <ExplanWrapped>
            {/* 월간 일 경우 */}
            {holidayInfo.date === 'month' && (
              <>
                <Paragraph>
                  매월{' '}
                  <Text keyboard strong={true}>
                    {daily}
                  </Text>
                  일을 휴관일로 설정합니다.
                </Paragraph>
                <Paragraph>
                  그 달에 존재하지 않는 일은 해당 월의{' '}
                  <Text underline>마지막 일</Text>로 설정됩니다.
                </Paragraph>
              </>
            )}
            {/* 주간이면서 매주 일 경우 */}
            {holidayInfo.date === 'week' && holidayInfo.period1 === 'weekly' && (
              <>
                <Paragraph>
                  설정한 요일을 <Text keyboard>매주</Text> 휴관일로 설정합니다.
                </Paragraph>
                <Paragraph>
                  요일은 일요일 ~ 토요일 단일, 복수 선택이 가능합니다
                </Paragraph>
              </>
            )}
            {/* 주간이면서 격주 일 경우 */}
            {holidayInfo.date === 'week' &&
              holidayInfo.period1 === 'fortnightly' && (
                <>
                  <Paragraph>
                    설정한 요일을 <Text keyboard>격주</Text> 휴관일로
                    설정합니다.
                  </Paragraph>
                  <Paragraph>
                    달력에서 격주 휴관일을 미리 확인하고, 시작일(기준일)을{' '}
                    <Text keyboard>설정</Text>해주세요
                  </Paragraph>
                </>
              )}
          </ExplanWrapped>
          <Divider />
          <SelectGroupWrapped>
            {holidayInfo.date === 'month' && (
              <MonthWrapped>
                <Text strong>휴관일을 설정해주세요</Text>
                <InputNumber
                  onChange={(value) => {
                    setDaily(value);
                  }}
                  size='large'
                  min={1}
                  max={31}
                  defaultValue={daily}
                />
              </MonthWrapped>
            )}
            {holidayInfo.date === 'week' && holidayInfo.period1 === 'weekly' && (
              <WeeklyWrapped>
                <RadioComponent />
                <SelectComponent />
              </WeeklyWrapped>
            )}
            {holidayInfo.date === 'week' &&
              holidayInfo.period1 === 'fortnightly' && (
                <FortnightlyWrapped>
                  <RadioComponent />
                  <SelectComponent />
                  <BaseDayComponent />
                </FortnightlyWrapped>
              )}
          </SelectGroupWrapped>
          <Divider />
        </ArticleWrapped>

        {/* 변경 버튼이 있는 Footer */}
        <FooterWrapped>
          <Popconfirm
            title='변경 하시겠습니까?'
            okText='변경'
            onCancel={onCancelHolidayInfo}
            onConfirm={onChangeConfirm}
            okType='primary'
          >
            <ChangeButton
              type={holidayInfo !== prevHolidayInfo ? 'primary' : 'default'}
            >
              변경
            </ChangeButton>
          </Popconfirm>
        </FooterWrapped>
      </MainWrapped>
    </Modal>
  );
};

export default HolidaySettingModal;
