import React, { useState, useEffect } from 'react';
import { Upload, message, Modal } from 'antd';
import { PlusCircleOutlined } from '@ant-design/icons';
import './ManagementMagImageUpload.css';
import * as Constants from '../Constants';
import { noImg } from '../CommonImage';

const ManagementMagImageUpload = (props) => {
  const [previewVisible, setPreviewVisible] = useState(false);
  const [previewImage, setPreviewImage] = useState();
  const { setParentImageList } = props;
  const [imageList, setImageList] = useState([]);
  // const [filteredFileList, setFilteredFileList] = useState([]);

  const { Dragger } = Upload;

  useEffect(() => {
    setImageList(props.fileList);
  }, [props.fileList]);

  const getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const beforeUpload = (file) => {
    // const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    // if (!isJpgOrPng) {
    //     message.error('JPG/PNG 파일만 가능합니다.');
    // }
    // const isLt2M = file.size / 1024 / 1024 < 2;
    // if (!isLt2M) {
    //     message.error('이미지는 2MB를 초과할 수 없습니다.');
    // }
    // return isJpgOrPng && isLt2M;

    return false;
  };

  const dummyRequest = ({ file, onSuccess }) => {
    setTimeout(() => {
      onSuccess('ok');
    }, 0);
  };

  const handlePreview = async (file) => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    setPreviewImage(file.url || file.preview);
    setPreviewVisible(true);
  };

  const handleCancel = () => {
    setPreviewVisible(false);
  };

  const draggerProps = {
    name: 'file',
    multiple: true,
    listType: 'picture',
    customRequest: dummyRequest,
    beforeUpload: beforeUpload,
    onPreview: handlePreview,
    fileList: imageList,
    showUploadList: {
      showPreviewIcon: true,
      showDownloadIcon: true,
      showRemoveIcon: true,
    },
    onChange: (info) => {
      const { status } = info.file;
      //  Array.from(info.fileList);
      var fileList = info.fileList.filter((file) => {
        return file.type.includes('image');
      });

      if (status !== 'uploading') {
        // setImageList(info.fileList);

        setParentImageList(fileList);
        setImageList(fileList);
      }
    },
    // className: 'upload-list-inline',
  };

  return (
    <>
      <Dragger {...draggerProps}>
        <p className='ant-upload-drag-icon'>
          <PlusCircleOutlined />
        </p>
        <p className='ant-upload-text'>
          클릭 또는 파일을 드래그해 이미지를 업로드해주세요
        </p>
      </Dragger>
      <Modal
        className='magPreviewModal'
        visible={previewVisible}
        footer={null}
        onCancel={handleCancel}
      >
        <img
          alt='example'
          style={{ width: '100%' }}
          src={previewImage}
          onError={(error) => {
            error.currentTarget.src = noImg;
          }}
        />
      </Modal>
    </>
  );
};

export default ManagementMagImageUpload;
