import { CheckOutlined, EditOutlined } from '@ant-design/icons';
import { Badge, Popconfirm } from 'antd';
import { TextAreaRef } from 'antd/lib/input/TextArea';
import React, { FC, memo, useEffect, useRef, useState } from 'react';
import { ContentsType } from '../../../../../types/Lss';
import { BaddgeWrapped, CardWrapped, DeleteIcon, IconButton, RedIconButton, TextFieldsWrapped } from './NoticeCards.styled';
import { useDispatch, useSelector } from 'react-redux';
import { fetchDeleteNoticeContents, fetchPutNoticeContents, LSS_DETAIL, selectLssDetail } from '../../../../../slice/LssSlice';
import styles from './NoticeCards.module.scss';

type inputState = 'default' | 'changing';
type changeType = 'isUsed' | 'content';

const NoticeCards: FC<ContentsType> = ({ is_used, lss_content_idx, lss_master_idx, content }) => {
  const dispatch = useDispatch();
  const state = useSelector(selectLssDetail);

  const [modeState, setModeState] = useState<inputState>('default');
  const [isDisabled, setIsDisabled] = useState(true);
  const [isUsed, setIsUsed] = useState(is_used === 'Y' ? true : false);
  const [lssContentIdx, setLssContentIdx] = useState(lss_content_idx);
  const [lssMasterIdx, setLssMasterIdx] = useState(lss_master_idx);
  const [mContent, setmContent] = useState(content);
  const [prevContent, setPrevContent] = useState(content);
  const textRef = useRef<TextAreaRef>(null);

  useEffect(() => {
    //텍스트값이 변경되는지 확인하는 로직
    if (modeState === 'default' && prevContent !== mContent) {
      setModeState('changing');
    }
  }, [mContent]);

  useEffect(() => {
    //idx와 used가 바뀌면 다시 설정함.
    setLssContentIdx(lss_content_idx);
    setIsUsed(is_used === 'Y' ? true : false);
  }, [lss_content_idx, is_used]);

  const onNoticeContentPutChange = (content: ContentsType, type: changeType) => {
    const data = JSON.parse(JSON.stringify(state[LSS_DETAIL].notice));
    const fixedData: Array<ContentsType> = data.map((item: ContentsType) => {
      if (type === 'isUsed') {
        //표시할 공지사항을 선택할 경우 다른 아이템들의 is_used는 N으로 설정하고, 선택된 아이템의 isused만 Y로 변경하는 로직
        if (item.lss_content_idx === content.lss_content_idx) {
          item.is_used = content.is_used;
        } else {
          item.is_used = 'N';
        }
        return item;
      } else {
        if (item.lss_content_idx === content.lss_content_idx) {
          item.content = content.content;
        }
        return item;
      }
    });
    dispatch(fetchPutNoticeContents(fixedData));
  };
  const onNoticeDelete = (content: ContentsType) => {
    content.lss_master_idx = state[LSS_DETAIL].idx;
    dispatch(fetchDeleteNoticeContents({ ...content }));
  };

  const onChangeTextFields = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const target = e.target as HTMLTextAreaElement;
    if (e.target.value.split('\n').length > 6) {
      return false;
    }
    setmContent(target.value);
  };
  const onChangeUsed = (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => {
    isUsed ? setIsUsed(false) : setIsUsed(true);
    const data: ContentsType = {
      type: 'NOTICE',
      content: mContent,
      lss_content_idx: lssContentIdx,
      lss_master_idx: lssMasterIdx,
      //상태는 나중에 바뀌기 때문에 반전된 값을넣음
      is_used: isUsed ? 'N' : 'Y',
    };
    onNoticeContentPutChange(data, 'isUsed');
  };
  const onChangeText = (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => {
    // 현재 입력상태를 변경한다.
    if (modeState === 'default') {
      isDisabled ? setIsDisabled(false) : setIsDisabled(true);
    }
  };
  const onCancel = (e: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => {
    if (modeState === 'changing') {
      //취소하면 이전상태로 돌아옴
      setModeState('default');
      setmContent(prevContent);
      isDisabled ? setIsDisabled(false) : setIsDisabled(true);
    }
  };
  const onSaveText = (e: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => {
    const target = textRef.current as TextAreaRef;
    const inputText = target.resizableTextArea?.props.value as string;
    setPrevContent(inputText);
    setmContent(inputText);
    setModeState('default');
    setIsDisabled(true);
    const data: ContentsType = {
      type: 'NOTICE',
      content: mContent,
      lss_content_idx: lssContentIdx,
      lss_master_idx: lssMasterIdx,
      is_used: isUsed ? 'Y' : 'N',
    };
    onNoticeContentPutChange(data, 'content');
  };
  const onDeleteIcon = (e: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => {
    const data: ContentsType = {
      type: 'NOTICE',
      content: mContent,
      lss_content_idx: lssContentIdx,
      lss_master_idx: lssMasterIdx,
      //상태는 나중에 바뀌기 때문에 반전된 값을넣음
      is_used: isUsed ? 'Y' : 'N',
    };
    onNoticeDelete(data);
  };

  const Textfields: React.ReactNode = (
    <div className={styles.text}>
      <TextFieldsWrapped
        ref={textRef}
        maxLength={25 * 6}
        autoSize={{
          maxRows: 6,
        }}
        disabled={isDisabled}
        cols={1}
        rows={6}
        onChange={onChangeTextFields}
        value={mContent}
      />
    </div>
  );

  //#region 아이콘 버튼
  const BadgeIcon: FC = () => {
    return (
      <BaddgeWrapped>
        <IconButton>
          <Badge status={isUsed ? 'processing' : 'warning'} />
        </IconButton>
      </BaddgeWrapped>
    );
  };
  const ModifyIconBtn: FC = () => {
    useEffect(() => {
      textRef.current?.focus({
        cursor: 'end',
      });
    });
    return modeState === 'changing' ? (
      <Popconfirm title="저장 하시겠습니까?" okText="저장" okType="primary" onConfirm={onSaveText} onCancel={onCancel}>
        <RedIconButton onClick={onChangeText}>
          <EditOutlined key="edit" />
        </RedIconButton>
      </Popconfirm>
    ) : (
      <IconButton onClick={onChangeText}>
        <EditOutlined key="edit" />
      </IconButton>
    );
  };
  const UsedIconBtn: FC = () => {
    return (
      <IconButton disabled={modeState === 'changing' ? true : false} onClick={onChangeUsed}>
        <CheckOutlined key="ellipsis" />
      </IconButton>
    );
  };
  const DeleteIconBtn: FC = () => {
    return (
      <Popconfirm title="삭제 하시겠습니까?" okText="삭제" onConfirm={onDeleteIcon} okType="primary">
        <IconButton>
          <DeleteIcon />
        </IconButton>
      </Popconfirm>
    );
  };
  //#endregion
  return (
    <CardWrapped className={styles.card} cover={Textfields} actions={[<BadgeIcon />, <ModifyIconBtn />, <UsedIconBtn />, <DeleteIconBtn />]}></CardWrapped>
  );
};

export default NoticeCards;
