import React, { useState, useEffect } from 'react';
import axios from 'axios';
import * as Constants from '../Constants';
import { Row, Col, Radio, Checkbox, Button } from 'antd';
import TextArea from 'antd/lib/input/TextArea';
import uuid from 'react-uuid';
import './ManagementPollDetail.css';

const TitleCard = (props) => {
  const [pollTitle, setPollTitle] = useState('');
  const [pollDescription, setPollDescription] = useState('');

  // 오른쪽 클릭 방지
  oncontextmenu = (e) => {
    if (e.button === 2) {
      e.preventDefault();
      return false;
    }
  };

  useEffect(() => {
    setPollTitle(props.pollTitle);
    setPollDescription(props.pollDescription);
    return () => {
      // 오른쪽 클릭 방지 해제
      oncontextmenu = (e) => {};
    };
  }, []);

  return (
    <div
      style={{
        width: '100%',
        border: '1px solid #7f8c8d',
        borderTop: '0px',
        borderRadius: '8px',
        minHeight: '120px',
        backgroundColor: 'white',
      }}
    >
      <div
        style={{
          backgroundColor: '#576574',
          borderTopRightRadius: '8px',
          borderTopLeftRadius: '8px',
          width: '100%',
          height: '15px',
        }}
      ></div>
      <div
        style={{
          width: '100%',
          padding: '10px 30px 20px 30px',
        }}
      >
        <p style={{ fontSize: '2em', fontFamily: 'NotoSansKR-Bold' }}>
          {props.pollTitle}
        </p>
        <pre style={{ fontFamily: 'NotoSansKR-Medium' }}>
          {props.pollDescription}
        </pre>
      </div>
    </div>
  );
};

const ManagementPollDetail = (props) => {
  const [masterId, setMasterId] = useState(props.match.params.master_id);
  const [pollTitle, setPollTitle] = useState('');
  const [pollDescription, setPollDescription] = useState('');
  const [questions, setQuestions] = useState([]);
  const [answers, setAnswers] = useState([]);
  const [userId, setUserId] = useState(uuid());

  useEffect(() => {
    getPollDetail();
  }, []);

  const getPollDetail = () => {
    axios
      .get('/poll/detail', {
        params: {
          master_id: masterId,
        },
      })
      .then((res) => {
        setPollTitle(res.data.pollMaster.title);
        setPollDescription(res.data.pollMaster.description);
        setQuestions(res.data.questions);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const cardStyle = {
    width: '100%',
    border: '1px solid #7f8c8d',
    borderRadius: '8px',
    minHeight: '120px',
    padding: '20px',
    marginTop: '10px',
    backgroundColor: 'white',
  };

  const radioStyle = {
    display: 'block',
    height: '30px',
    lineHeight: '30px',
    width: '100%',
  };

  const checkboxStyle = {
    display: 'block',
    height: '30px',
    lineHeight: '30px',
    width: '100%',
  };

  const onChangeAnswer = (e, type = '', question_id = '') => {
    var theAnswer = {
      master_id: masterId,
      question_id: question_id,
      user_id: userId,
      answer: '',
    };
    var copiedAnswers = Array.from(answers);

    if (type === 'single') {
      var removeTargetIdx = null;

      copiedAnswers.forEach((answer, idx) => {
        if (answer.question_id === theAnswer.question_id) {
          removeTargetIdx = idx;
        }
      });

      if (removeTargetIdx === 0) {
        copiedAnswers.shift();
      } else if (removeTargetIdx) {
        copiedAnswers.splice(removeTargetIdx, 1);
      }

      if (removeTargetIdx) {
        copiedAnswers.splice(removeTargetIdx, 1);
      }

      theAnswer['answer'] = e.target.value;
      copiedAnswers.push(theAnswer);
    } else if (type === 'multi') {
      var removeTargetIdx = null;
      copiedAnswers.forEach((answer, idx) => {
        if (answer.answer === e.target.value) {
          removeTargetIdx = idx;
        }
      });

      if (removeTargetIdx === 0) {
        copiedAnswers.shift();
      } else if (removeTargetIdx) {
        copiedAnswers.splice(removeTargetIdx, 1);
      }

      if (e.target.checked) {
        theAnswer['answer'] = e.target.value;
        copiedAnswers.push(theAnswer);
      }
    } else if (type === 'narrative') {
      var removeTargetIdx = null;
      copiedAnswers.forEach((answer, idx) => {
        if (answer.question_id === theAnswer.question_id) {
          removeTargetIdx = idx;
        }
      });

      if (removeTargetIdx === 0) {
        copiedAnswers.shift();
      } else if (removeTargetIdx) {
        copiedAnswers.splice(removeTargetIdx, 1);
      }

      theAnswer['answer'] = e.target.value ? e.target.value : '답변없음';
      copiedAnswers.push(theAnswer);
    }

    setAnswers(copiedAnswers);
  };

  const onSubmit = (e) => {
    e.preventDefault();

    axios
      .post(`/poll/submit`, { answers })
      .then((res) => {
        props.history.push({
          pathname: '/poll/complete',
          masterId: masterId,
          pollTitle: pollTitle,
        });
      })
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <div style={{ padding: '20px', width: '100%', height: '100%' }}>
      <div style={{ width: '50%', minWidth: '350px', margin: 'auto' }}>
        <Row>
          <Col span={24}>
            <TitleCard
              pollTitle={pollTitle}
              pollDescription={pollDescription}
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            {questions.map((question, idx) => {
              var items = question.items ? question.items : [];

              if (question.type === 'single') {
                return (
                  <div key={question.question_id} style={cardStyle}>
                    <pre
                      style={{
                        fontSize: '1.1em',
                        fontFamily: 'NotoSansKR-Bold',
                      }}
                    >
                      {question.question_title}
                    </pre>
                    <Radio.Group
                      name={question.question_id}
                      style={{ width: '100%' }}
                      onChange={(e) => {
                        onChangeAnswer(e, question.type, question.question_id);
                      }}
                    >
                      {items.map((item, idx) => {
                        return (
                          <Radio
                            key={item.item_id}
                            value={item.item_id}
                            style={radioStyle}
                          >
                            {item.content}
                          </Radio>
                        );
                      })}
                    </Radio.Group>
                  </div>
                );
              } else if (question.type === 'multi') {
                return (
                  <div key={question.question_id} style={cardStyle}>
                    <pre
                      style={{
                        fontSize: '1.1em',
                        fontFamily: 'NotoSansKR-Bold',
                      }}
                    >
                      {question.question_title}
                    </pre>
                    <Checkbox.Group
                      name={question.question_id}
                      style={{ width: '100%' }}
                    >
                      {items.map((item, idx) => {
                        return (
                          <Checkbox
                            key={item.item_id}
                            value={item.item_id}
                            style={radioStyle}
                            onChange={(e) => {
                              onChangeAnswer(
                                e,
                                question.type,
                                question.question_id
                              );
                            }}
                          >
                            {item.content}
                          </Checkbox>
                        );
                      })}
                    </Checkbox.Group>
                  </div>
                );
              } else {
                return (
                  <div key={question.question_id} style={cardStyle}>
                    <p
                      style={{
                        fontSize: '1.1em',
                        fontFamily: 'NotoSansKR-Bold',
                      }}
                    >
                      {question.question_title}
                    </p>
                    <TextArea
                      autoSize={true}
                      placeholder='설명'
                      style={{
                        border: '0',
                        resize: 'none',
                      }}
                      onChange={(e) => {
                        onChangeAnswer(e, question.type, question.question_id);
                      }}
                    />
                  </div>
                );
              }
            })}
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ paddingTop: '10px' }}>
            <Button
              type='primary'
              style={{ width: '100px', backgroundColor: 'rgb(87, 101, 116)' }}
              onClick={onSubmit}
            >
              제출
            </Button>
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default ManagementPollDetail;
