import { Button, Form, Input, message, Radio, Select, Spin } from 'antd';
import TextArea from 'antd/lib/input/TextArea';
import axios from 'axios';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { Prompt } from 'react-router-dom';
import { noImg } from '../CommonImage';
import FormGuideComponent from '../FormGuideComponent';
import MenuGuide from '../MenuGuide';
import _ from 'lodash';
import { useDispatch, useSelector } from 'react-redux';
import {
  fetchSmartSearch,
  resetBooks,
  selectSmartSearch,
  setKeyword,
  SMART_SEARCH,
} from '../../slice/SmartSearchSlice';
import SmartSearch from './SmartSearch';

const ManagementBookForm = (props: any) => {
  const dispatch = useDispatch();
  const state = useSelector(selectSmartSearch)[SMART_SEARCH];

  const [record, setRecord] = useState(props.location.record);
  const [mode, setMode] = useState(
    props.location.mode ? props.location.mode : 'regist'
  );
  const [visible, setVisible] = useState(false);

  const [book, setBook] = useState(
    props.location.record ? props.location.record : null
  );
  const [genre, setGenre] = useState([]);
  const [spinning, setSpinning] = useState(false);

  const [isBack, setIsBack] = useState(true);

  const [totalCount, setTotalCount] = useState(1);
  const inputKeyword = useRef('');

  const [form] = Form.useForm();

  const validateMessages = {
    required: '필수 항목',
    types: {
      email: 'Not a validate email!',
      number: 'Not a validate number!',
    },
    number: {
      range: 'Must be between ${min} and ${max}',
    },
  };

  useEffect(() => {
    dispatch(setKeyword({ keyword: '' }));
    dispatch(resetBooks());
    document.body.className = 'form-body';
    genreSelectGenerator();
    if (mode === 'modify') {
      inputKeyword.current = record.title;
      form.setFieldsValue({
        spcCode: record.title,
        title: record.title,
        genre: record.genre,
        author: record.author,
        publisher: record.publisher,
        publication_year: record.publication_year,
        description: record.description,
        useYN: record.useYN,
        librarian_cmt: record.librarian_cmt,
        shelf_loc_name: record.shelf_loc_name,
      });
    } else {
      form.setFieldsValue({
        useYN: 'Y',
      });
    }
  }, []);

  const inputStyle = {
    width: '100%',
  };

  const onSearch = (e: any) => {
    e.preventDefault();
    if (mode === 'modify' && inputKeyword.current !== state.keyword) {
      delayedQuery(inputKeyword.current);
    }

    setVisible(true);
  };

  const genreSelectGenerator = () => {
    axios
      .get<any, any>('/book/genre', {})
      .then((res) => {
        setGenre(res.data.genre);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const onChangeTitle = (e: any) => {
    const keyword = e.target.value;
    // 도서 제목(input)이 변경될때마다 useref에 값을 넣음
    inputKeyword.current = keyword;
    if (keyword === '') {
      // 입력된 책 제목이 없으면 값들을 초기화하고 검색창을 닫음
      form.setFieldsValue({
        title: '',
        genre: '',
        author: '',
        publisher: '',
        publication_year: '',
        description: '',
        useYN: 'Y',
        librarian_cmt: '',
      });
      setBook(null);
      setVisible(false);
      // 단어가없으면 보여주는것을 끔
      dispatch(
        setKeyword({
          keyword: '',
        })
      );
      dispatch(resetBooks());
    }
    // 디바운싱 적용
    delayedQuery(keyword);
  };

  const onSubmit = (values: any) => {
    setSpinning(true);
    axios
      .post('/book/recommend', {
        master_no: book.master_no,
        genre: values.genre,
        useYN: values.useYN,
        description: values.description,
        librarian_cmt: values.librarian_cmt,
      })
      .then((res) => {
        setTimeout(() => {
          setSpinning(false);
          message.success('등록 완료');
          setIsBack(false);
          props.history.goBack();
        }, 1000);
      })
      .catch((err) => {
        setSpinning(false);
        message.error('등록 실패');
        console.error(err);
      });
  };

  const AutoSearch = async (mKeyword: string) => {
    if (mKeyword.trim() !== '') {
      // 키워드가 있다면 검색창을 보여주며 키워드를 리덕스에 저장하고 검색을 진행함.
      setVisible(true);
      await dispatch(
        setKeyword({
          keyword: mKeyword,
        })
      );
      await dispatch(
        fetchSmartSearch({
          keyword: mKeyword,
          current_page: 1,
        })
      );
    } else if (mKeyword === '') {
      setVisible(false);
    }
  };

  const delayedQuery = useRef(
    _.debounce((q: string) => AutoSearch(q), 500)
  ).current;
  const onClickSearchLI = useCallback(
    (
      e: React.MouseEventHandler<HTMLLIElement> &
        React.MouseEvent<HTMLLIElement, MouseEvent>
    ) => {
      const spcCode = e.currentTarget.dataset?.spccode;
      // const title = e.currentTarget.dataset?.keyword;
      setSpinning(true);
      // if (title) {
      //   inputKeyword.current = title;
      //   form.setFieldsValue({
      //     spcCode: title,
      //   });
      // }
      axios
        .get<any, any>('/bookV2/searchForSpcCode', {
          params: {
            spcCode,
          },
        })
        .then((res) => {
          console.log(res);
          var resBook = res.data.response_body;
          if (res.data.description !== 'Success') {
            message.error('등록되지 않은 도서입니다.');
            return;
          }
          setBook(resBook);
          form.setFieldsValue({
            title: resBook.title,
            genre: resBook.genre ? resBook.genre : '',
            author: resBook.author,
            publisher: resBook.publisher,
            publication_year: resBook.publication_year,
            description: resBook.description,
            librarian_cmt: resBook.librarian_cmt,
            shelf_loc_name: resBook.shelf_loc_name,
          });
          // title && dispatch(setKeyword({ keyword: title }));
          setVisible(false);
          setSpinning(false);
        })
        .catch((err) => {
          setVisible(false);
          setSpinning(false);
          console.error(err);
        });
    },
    []
  );
  return (
    <div style={{ width: '100%' }} data-smartsearch={'smartsearch'}>
      <Prompt
        when={isBack}
        message='변경사항이 저장되지 않았습니다. 뒤로가시겠습니까?'
      />
      <div style={{ width: '50%', margin: 'auto' }}>
        <MenuGuide
          title='사서 추천 도서'
          subTitle='사서 추천 도서를 등록해주세요'
        />
        <Spin spinning={spinning} size='large' tip='처리 중...'>
          <Form
            form={form}
            validateMessages={validateMessages}
            onFinish={onSubmit}
            style={{
              border: '1px solid #dfe6e9',
              padding: '50px',
            }}
          >
            <FormGuideComponent
              title='검색 도서'
              guide='등록할 도서의 제목을 입력해주세요'
            />
            <Form.Item
              style={{
                position: 'relative',
              }}
            >
              <Form.Item name='spcCode' noStyle rules={[{ required: true }]}>
                <Input
                  autoComplete='off'
                  style={{
                    minWidth: '150px',
                    width: '360px',
                  }}
                  onChange={onChangeTitle}
                />
              </Form.Item>
              <Button
                style={{
                  marginLeft: '10px',
                  position: 'relative',
                  top: '-2px',
                }}
                onClick={onSearch}
              >
                찾기
              </Button>
              <SmartSearch
                visible={visible}
                setVisible={setVisible}
                totalCount={totalCount}
                onClick={onClickSearchLI}
              />
            </Form.Item>

            <FormGuideComponent title='도서명' />
            <Form.Item name='title' rules={[{ required: true }]}>
              <Input
                style={inputStyle}
                placeholder='도서 검색 시 자동으로 기입됩니다'
              />
            </Form.Item>
            <FormGuideComponent title='도서 위치' />
            <Form.Item name='shelf_loc_name' rules={[{ required: true }]}>
              <div>{book?.shelf_loc_name}</div>
              {/* <Radio.Group disabled>
                <Radio value='[미리내]어린이자료실'>어린이자료실</Radio>
                <Radio value='[미리내]일반자료실'>일반자료실</Radio>
              </Radio.Group> */}
            </Form.Item>
            <FormGuideComponent title='표지' />
            <Form.Item name='cover'>
              <img
                alt={book ? book.imageUrl : 'empty'}
                src={book ? book.imageUrl : 'empty'}
                // src={
                //   book
                //     ? `/files/cover/stream/${book.org_name}`
                //     : 'empty'
                // }
                onError={(error) => {
                  error.currentTarget.src = noImg;
                }}
                style={{
                  maxWidth: '250px',
                  display: `${book ? 'block' : 'none'}`,
                  borderRadius: '5px',
                  border: '1px solid #e8e8e8',
                }}
              />
            </Form.Item>
            {/* 
            <FormGuideComponent title='장르' />
            <Form.Item name='genre'>
              <Select style={inputStyle} onChange={onSelectChange}>
                <Option key='no'>장르를 선택하세요</Option>
                {genre.map((item) => {
                  return <Option key={item.idx}>{item.genre}</Option>;
                })}
              </Select>
            </Form.Item> */}

            <FormGuideComponent title='저자' />
            <Form.Item name='author' rules={[{ required: true }]}>
              <Input style={inputStyle} readOnly />
            </Form.Item>

            <FormGuideComponent title='출판사' />
            <Form.Item name='publisher'>
              <Input style={inputStyle} readOnly />
            </Form.Item>

            <FormGuideComponent title='출판일' />
            <Form.Item name='publication_year'>
              <Input style={inputStyle} readOnly />
            </Form.Item>

            <FormGuideComponent title='설명' />
            <Form.Item name='description'>
              <TextArea rows={4} style={inputStyle} readOnly />
            </Form.Item>

            <FormGuideComponent
              title='추천사'
              guide='사서의 추천 이유를 작성해주세요'
            />
            <Form.Item name='librarian_cmt'>
              <TextArea rows={4} style={inputStyle} maxLength={348} />
            </Form.Item>

            <FormGuideComponent title='사용여부' />
            <Form.Item name='useYN'>
              <Radio.Group>
                <Radio value={'Y'}>Y</Radio>
                <Radio value={'N'}>N</Radio>
              </Radio.Group>
            </Form.Item>
            <Form.Item>
              <Button type='primary' htmlType='submit' block>
                등 록
              </Button>
            </Form.Item>
          </Form>
        </Spin>
      </div>
    </div>
  );
};

export default ManagementBookForm;
