import React, { FC, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchPostCaptionContents, LSS_DETAIL, selectLssDetail } from '../../../../../slice/LssSlice';
import { CaptionWrapped, DrawerWrapped, IconWrapped, InnerWrapped, SettingIconWrapped, Wrapped } from './Caption.styled';
import DrawerTitle from './CaptionDrawTitle';
import CaptionItems from './CaptionItems';

type CaptionType = {
  isVisible: boolean;
  setIsVisible: React.Dispatch<React.SetStateAction<boolean>>;
};

const Caption: FC<CaptionType> = ({ isVisible, setIsVisible }) => {
  const dispatch = useDispatch();
  const state = useSelector(selectLssDetail);

  const onClickAddIcon = useCallback(
    (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      dispatch(
        fetchPostCaptionContents({
          lss_master_idx: state[LSS_DETAIL].idx,
          lss_content_idx: 0,
          is_used: 'N',
          type: 'CAPTION',
          content: '자막',
        })
      );
    },
    [state[LSS_DETAIL].idx]
  );
  /**
   * drawer 관련 이벤트핸들러
   */
  const onClick = useCallback(() => {
    setIsVisible(true);
  }, []);
  /**
   * drawer 관련 이벤트핸들러
   */
  const onClose = useCallback(() => {
    setIsVisible(false);
  }, []);

  return (
    <Wrapped>
      <InnerWrapped>
        <IconWrapped isActive={isVisible} onClick={onClick}>
          <SettingIconWrapped twoToneColor={'#3b80ff'} />
        </IconWrapped>
        <CaptionWrapped>등록된 자막 : {state[LSS_DETAIL].caption.length}</CaptionWrapped>
      </InnerWrapped>
      <DrawerWrapped
        title={<DrawerTitle onClick={onClickAddIcon} />}
        placement="right"
        closable={true}
        visible={isVisible}
        getContainer={false}
        onClose={onClose}
        width={'100%'}
        maskStyle={{
          backgroundColor: 'rgba(0,0,0,0)',
        }}
        data-caption={'caption'}
      >
        <CaptionItems />
      </DrawerWrapped>
    </Wrapped>
  );
};

export default Caption;
