import { Spin } from 'antd';
import React, { MouseEventHandler, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import noImage from '../../images/noimages.png';
import {
  fetchSmartSearch,
  selectSmartSearch,
  SMART_SEARCH,
} from '../../slice/SmartSearchSlice';
import Pagination, { calculateTotalPages } from './Pagination';
import {
  CloseButton,
  NotFoundResult,
  SmartSearchCallNum,
  SmartSearchLi,
  SmartSearchText,
  SmartSearchUl,
  SmartSearchWrapped,
  ViewImage,
} from './SmartSearch.styled';

const SmartSearch = ({
  totalCount,
  visible,
  setVisible,
  onClick,
}: {
  totalCount: number;
  visible: boolean;
  onClick: MouseEventHandler<HTMLLIElement>;
  setVisible: React.Dispatch<React.SetStateAction<boolean>>;
}) => {
  const dispatch = useDispatch();
  const state = useSelector(selectSmartSearch);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  const handlePage = (e: number) => {
    setPage(e);
    console.log(e);
    const { keyword } = state.smartSearch;
    dispatch(fetchSmartSearch({ keyword, current_page: e }));
  };

  useEffect(() => {
    setPage(1);
    setTotalPages(calculateTotalPages(state.smartSearch.totalCount, 10));
  }, [state.smartSearch.totalCount]);

  return (
    <SmartSearchWrapped visible={visible}>
      <div
        style={{
          height: `100%`,
        }}
      >
        <SmartSearchUl>
          {/**/}
          <Spin
            spinning={state.smartSearch.isLoading}
            size='large'
            tip={`'${state.smartSearch.keyword}' 검색중...`}
            style={{
              position: 'inherit',
            }}
          >
            <div
              style={{
                height: '450px',
              }}
            >
              {state.smartSearch?.books.length > 0 ? (
                ''
              ) : (
                <NotFoundResult>
                  '{state.smartSearch.keyword}'로 검색한 도서가 없습니다
                </NotFoundResult>
              )}
              {state.smartSearch?.books.map((item, index) => {
                return item.mediaType === 'media' ? (
                  <SmartSearchLi
                    onClick={onClick}
                    key={index}
                    data-spccode={item.spc_code}
                    data-keyword={item.title}
                  >
                    <SmartSearchText>
                      {item.mediaType === 'media' ? '[DVD]' : ''}
                      {item.imageUrl && <ViewImage src={item.imageUrl} />}
                      {(item.imageUrl === null || '') && (
                        <ViewImage noImage={true} src={noImage} />
                      )}
                      {item.title}
                    </SmartSearchText>

                    <SmartSearchCallNum>({item.call_no})</SmartSearchCallNum>
                  </SmartSearchLi>
                ) : (
                  <SmartSearchLi
                    onClick={onClick}
                    key={index}
                    data-spccode={item.spc_code}
                    data-keyword={item.title}
                  >
                    <SmartSearchText>
                      [도서]{item.imageUrl && <ViewImage src={item.imageUrl} />}
                      {item.imageUrl === null && (
                        <ViewImage noImage={true} src={noImage} />
                      )}
                      {item.title}
                    </SmartSearchText>

                    <SmartSearchCallNum>({item.call_no})</SmartSearchCallNum>
                  </SmartSearchLi>
                );
              })}
            </div>
          </Spin>
        </SmartSearchUl>
        <CloseButton
          onClick={() => {
            setVisible(false);
          }}
        >
          X
        </CloseButton>
        <Pagination
          isLoading={state[SMART_SEARCH].isLoading}
          currentPage={page}
          totalPages={totalPages}
          onChange={handlePage}
          bottom='-50px'
        />
      </div>
    </SmartSearchWrapped>
  );
};

export default SmartSearch;
