import { Button, DatePicker, Divider, Input, Modal, Popconfirm, Space, Switch } from 'antd';
import moment from 'moment';
import React, { FC, memo, useCallback, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { fetchDeleteHoliday, fetchPatchChangeHoliday } from '../../../../../slice/LssSlice';
import { HolidayProcessType, PatchHolidayType } from '../../../../../types/Lss';
import FormGuideComponent from '../../../../FormGuideComponent';
import { FooterWrapped, TitileDescWrapped, TitleWrapped } from './HolidayDetailModal.styled';

type HolidayDetailModalType = {
  visible: boolean;
  //외부에서 visible을 바꿀수있는 이벤트를 추가해야함.
  setIsVisible: React.Dispatch<React.SetStateAction<boolean>>;
  data: DataType;
};

type DataType = Partial<HolidayProcessType>;

const HolidayDetailModal: FC<HolidayDetailModalType> = ({ data, setIsVisible, visible }) => {
  const dispatch = useDispatch();

  const { RangePicker } = DatePicker;
  const dateFormat = 'YYYY/MM/DD';

  //변경을 위한 상태값 정보들
  const [desc, setDesc] = useState(data.desc);
  const [startDate, setStartDate] = useState(data.startDate);
  const [endDate, setEndDate] = useState(data.endDate);
  const [isUsed, setIsUsed] = useState(data.isUsed);

  const handleCancel = useCallback(() => {
    setIsVisible(false);
  }, []);

  /**
   * 부모로 부터 전달받은 상태값으로 설정한다.
   */
  useEffect(() => {
    setDesc(data.desc);
    setStartDate(data.startDate);
    setEndDate(data.endDate);
    setIsUsed(data.isUsed);
  }, [data]);

  const Footer: FC = () => {
    const onChangeConfirm = useCallback(() => {
      const result: Partial<PatchHolidayType> = {};
      result.idx = data.idx;
      result.type = data.type;
      result.desc = desc;
      result.startDate = startDate;
      result.endDate = endDate;
      result.isUsed = isUsed;
      dispatch(fetchPatchChangeHoliday(result as PatchHolidayType));
      setIsVisible(false);
    }, [data]);
    const onDeleteConfirm = useCallback(() => {
      dispatch(fetchDeleteHoliday(data.idx as number));
      setIsVisible(false);
    }, [data]);
    return (
      <FooterWrapped>
        <Popconfirm title="변경 하시겠습니까?" okText="변경" onConfirm={onChangeConfirm} okType="primary">
          <Button>변경</Button>
        </Popconfirm>
        <Popconfirm title="삭제 하시겠습니까?" okText="삭제" okType="danger" disabled={data.type === '임시휴일' ? false : true} onConfirm={onDeleteConfirm}>
          <Button disabled={data.type === '임시휴일' ? false : true}>삭제</Button>
        </Popconfirm>
      </FooterWrapped>
    );
  };
  const Title: FC = memo(() => {
    return (
      <TitleWrapped>
        <TitileDescWrapped>{data.desc}</TitileDescWrapped>
      </TitleWrapped>
    );
  });

  const onUsedChange = useCallback((e: boolean) => {
    e ? setIsUsed('Y') : setIsUsed('N');
  }, []);
  const onDescChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setDesc(e.target.value);
  }, []);
  const getPopupContainer = useCallback((e: HTMLElement) => e.parentElement as HTMLElement, []);
  return (
    <Modal key={`${data.uuid}`} title={<Title />} visible={visible} onCancel={handleCancel} footer={<Footer />}>
      <FormGuideComponent title="휴무일" guide="휴무일을 변경 할 수 있습니다." />
      <RangePicker
        getPopupContainer={getPopupContainer}
        format={dateFormat}
        disabled={data.type === '임시휴일' ? false : true}
        defaultValue={[moment(data.startDate, dateFormat), moment(data.endDate, dateFormat)]}
        onCalendarChange={(days) => {
          if (days) {
            if (days.length > 1) {
              setStartDate(days[0]?.format('YYYY-MM-DD'));
              setEndDate(days[1]?.format('YYYY-MM-DD'));
            }
          }
        }}
      />
      <Divider />
      <FormGuideComponent title="설명" guide="등록될 임시공휴일에 대한 설명을 적어주세요(최대 9글자)" />
      <Input disabled={data.type === '임시휴일' ? false : true} maxLength={9} defaultValue={data.desc} onChange={onDescChange}></Input>
      <Divider />
      <FormGuideComponent title="활성화" guide="휴무일을 활성화하거나 비활성화 합니다" />
      <span>적용 여부 : </span>
      <Switch defaultChecked={data.isUsed === 'Y' ? true : false} onChange={onUsedChange}></Switch>
      <Space></Space>
    </Modal>
  );
};

export default HolidayDetailModal;
