import { Table } from 'antd';
import React, { useState } from 'react';
import styled from 'styled-components';
import uuid from 'react-uuid';

const BlackTableWrapped = styled.div`
  width: 100%;
`;

const columns = [
  {
    title: 'NO',
    dataIndex: '',
    align: 'center',
    width: 50,
    key: uuid(),
    render: (item, record, idx) => {
      var currentPage = Number.parseInt(sessionStorage.getItem('currentPage'));
      return idx + (currentPage - 1) * 10 + 1;
    },
  },
  {
    title: 'ID',
    dataIndex: 'cSubject',
    align: 'center',
    width: 200,
    key: uuid(),
  },
  {
    title: '이름',
    dataIndex: 'eqpName',
    align: 'center',
    width: 200,
    key: uuid(),
  },
  {
    title: '전화번호',
    dataIndex: 'question_cnt',
    width: 80,
    align: 'center',
    key: uuid(),
  },
  {
    title: '블랙기한',
    dataIndex: 'end_dttm',
    width: 80,
    align: 'center',
    key: uuid(),
  },
  {
    title: '행동',
    dataIndex: 'action',
    width: 80,
    align: 'center',
    key: uuid(),
  },
];

const BlackTable = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [dataSource, setDataSource] = useState([]);
  return (
    <BlackTableWrapped>
      <div>블랙리스트</div>
      <Table
        rowKey={(dataSource) => dataSource.dcs_idx}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        bordered
        size='small'
      />
    </BlackTableWrapped>
  );
};

export default BlackTable;
