/* eslint-disable jsx-a11y/alt-text */

import { Tabs } from 'antd';
import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react';
import styled from 'styled-components';
import DCS_S_line_2f_02 from '../../images/asan/DCS-S_line_2f.png';
import DCS_S_line_2f_02_ from '../../images/asan/DCS-S_line_2f_.png';
import DLS_line_1f_01 from '../../images/asan/DLS_line_1f_01.png';
import DLS_line_1f_01_ from '../../images/asan/DLS_line_1f_01_.png';
import DLS_line_2f_01 from '../../images/asan/DLS_line_2f_02.png';
import DLS_line_2f_01_ from '../../images/asan/DLS_line_2f_02_.png';
import DRS_line_1f from '../../images/asan/DRS_line_1f.png';
import DRS_line_1f_ from '../../images/asan/DRS_line_1f_.png';
import map1f from '../../images/asan/map_1f.png';
import map2f from '../../images/asan/map_2f.png';
import MBT_line_1f from '../../images/asan/MBT_line_1f.png';
import MBT_line_1f_ from '../../images/asan/MBT_line_1f_.png';
import SSB from '../../images/asan/SSB.png';
import SSB_ from '../../images/asan/SSB_.png';
import SSB_line_1f_01 from '../../images/asan/SSB_line_1f_01.png';
import SSB_line_1f_01_ from '../../images/asan/SSB_line_1f_01_.png';
import SSB_line_2f_02 from '../../images/asan/SSB_line_2f_02.png';
import SSB_line_2f_02_ from '../../images/asan/SSB_line_2f_02_.png';
import SSB_line_2f_03 from '../../images/asan/SSB_line_2f_03.png';
import SSB_line_2f_03_ from '../../images/asan/SSB_line_2f_03_.png';
import SSB_line_2f_04 from '../../images/asan/SSB_line_2f_04.png';
import SSB_line_2f_04_ from '../../images/asan/SSB_line_2f_04_.png';
import DCS_S from '../../images/nonhyeon/DCS-S.png';
import DCS_S_ from '../../images/nonhyeon/DCS-S_.png';
import DLS from '../../images/nonhyeon/DLS.png';
import DLS_ from '../../images/nonhyeon/DLS_.png';
import DRS from '../../images/nonhyeon/DRS.png';
import DRS_ from '../../images/nonhyeon/DRS_.png';
import infoImg from '../../images/nonhyeon/info.png';
import MBT from '../../images/nonhyeon/MBT.png';
import MBT_ from '../../images/nonhyeon/MBT_.png';
import libLogo from '../../images/asan/logo_asan.png';
import EquipUsageContentComponent from '../main/EquipUsageContentComponent';
import EquipUsageTitleComponent from '../main/EquipUsageTitleComponent';
import classNames from 'classnames';

const MapImg = styled.div`
  width: 100%;
  height: 600px;
  margin-top: 0px;
  background-image: url(${(props) => props.map});
  background-repeat: no-repeat;
  background-size: contain;
`;

const StatusCard = styled.div`
  position: absolute;
  min-width: 220px;
  max-width: 235px;
  height: 50px;
  box-shadow: 6px 6px 12px -5px rgba(0, 0, 0, 0.75);
  border-radius: 12px;
  background-color: white;
  background-image: url(${(props) => props.src});
  background-repeat: no-repeat;
  background-position: left;
  background-size: contain;
`;
const Line = styled.img`
  position: relative;
`;

const Home = (props) => {
  const [equipments, setEquipments] = useState([]);

  function callback(key) {
    sessionStorage.setItem('currentPage', 1);
  }

  const getEquipments = () => {
    axios
      .get(`/equip/index`, {})
      .then((res) => {
        console.log(res);
        setEquipments(res.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const equipmentCard = (equipment, img, position, minWidth) => {
    return (
      <>
        <StatusCard
          style={{
            top: `${position.s.top}px`,
            left: `${position.s.left}px`,
            minWidth: minWidth ? `${minWidth}px` : '220px',
          }}
          src={img.s}
        >
          <Line
            style={{
              top: `${position.l.top}px`,
              left: `${position.l.left}px`,
            }}
            src={img.l}
          />
          <EquipUsageTitleComponent equipInfo={equipment} />
          <EquipUsageContentComponent equipInfo={equipment} />
        </StatusCard>
      </>
    );
  };

  useEffect(() => {
    document.body.className = 'full-width-body';
    getEquipments();
    let setIntevalVariable = setInterval(() => {
      getEquipments();
    }, 5000);

    return () => {
      document.body.className = '';
      clearInterval(setIntevalVariable);
    };
  }, []);

  return (
    <div
      className={classNames('bg-transparent')}
      style={{
        position: 'relative',
      }}
    >
      <img
        src={libLogo}
        alt=''
        style={{
          width: '220px',
          filter: 'hue-rotate(108deg) contrast(0)',
        }}
      />
      <Tabs type='card' defaultActiveKey='1' onChange={callback}>
        <Tabs.TabPane tab='1층' key='1'>
          <MapImg map={map1f}>
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'MBT-M') {
                const position = {
                  s: { top: 75, left: 233 },
                  l: { top: 50, left: -46 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? MBT : MBT_,
                  l: equipment.powerYN === 'Y' ? MBT_line_1f : MBT_line_1f_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'DLS') {
                const position = {
                  s: { top: 75, left: 562 },
                  l: { top: 50, left: -28 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? DLS : DLS_,
                  l:
                    equipment.powerYN === 'Y'
                      ? DLS_line_1f_01
                      : DLS_line_1f_01_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'SSB1') {
                const position = {
                  s: { top: 554, left: 109 },
                  l: { top: -101, left: -39 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? SSB : SSB_,
                  l:
                    equipment.powerYN === 'Y'
                      ? SSB_line_1f_01
                      : SSB_line_1f_01_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'DRS') {
                const position = {
                  s: { top: 561, left: 536 },
                  l: { top: -179, left: -38 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? DRS : DRS_,
                  l: equipment.powerYN === 'Y' ? DRS_line_1f : DRS_line_1f_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
          </MapImg>
        </Tabs.TabPane>
        <Tabs.TabPane tab='2층' key='2'>
          <MapImg map={map2f}>
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'DLS-W') {
                const position = {
                  s: { top: 75, left: 397 },
                  l: { top: 24, left: -199 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? DLS : DLS_,
                  l:
                    equipment.powerYN === 'Y'
                      ? DLS_line_2f_01
                      : DLS_line_2f_01_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'DCS-S') {
                const position = {
                  s: { top: 75, left: 707 },
                  l: { top: 50, left: -70 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? DCS_S : DCS_S_,
                  l:
                    equipment.powerYN === 'Y'
                      ? DCS_S_line_2f_02
                      : DCS_S_line_2f_02_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'SSB2') {
                const position = {
                  s: { top: 556, left: 65 },
                  l: { top: -147, left: 84 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? SSB : SSB_,
                  l:
                    equipment.powerYN === 'Y'
                      ? SSB_line_2f_02
                      : SSB_line_2f_02_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'SSB3') {
                const position = {
                  s: { top: 566, left: 323 },
                  l: { top: -59, left: 47 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? SSB : SSB_,
                  l:
                    equipment.powerYN === 'Y'
                      ? SSB_line_2f_03
                      : SSB_line_2f_03_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
            {equipments.map((equipment) => {
              if (equipment.eqpUniqueID === 'SSB4') {
                const position = {
                  s: { top: 566, left: 586 },
                  l: { top: -57, left: -55 },
                };
                const img = {
                  s: equipment.powerYN === 'Y' ? SSB : SSB_,
                  l:
                    equipment.powerYN === 'Y'
                      ? SSB_line_2f_04
                      : SSB_line_2f_04_,
                };
                return equipmentCard(equipment, img, position);
              }
              return null;
            })}
          </MapImg>
        </Tabs.TabPane>
      </Tabs>
      <div
        style={{
          position: 'absolute',
          bottom: 0,
          right: 0,
        }}
      >
        <img src={infoImg} />
      </div>
    </div>
  );
};

export default Home;
