import axios from 'axios';
import { orderBanners } from '../slice/BannerSlice';
import { BannerBody } from '../types/Banner';
import { ResponseVo } from '../types/ResponseVo';
import { BannerDtoList } from './../types/Banner';

export const getBanners = async ({ cur_page }: { cur_page: number }) => {
  const response = await axios.get<ResponseVo<BannerBody>>(`/banners`, {
    params: {
      per_page: 10,
      cur_page,
      client_type: 'cms',
    },
  });
  return response.data;
};

export const getBanner = async ({ banner_idx }: { banner_idx: string }) => {
  const response = await axios.get<ResponseVo<BannerDtoList>>(
    `/banners/${banner_idx}`
  );
  return response.data;
};

export const createBanner = async ({ banner }: { banner?: BannerDtoList }) => {
  if (!banner) return;

  const response = await axios.post<ResponseVo<any>>(`/banners`, banner);

  return response;
};

export const modifyBanner = async ({ banner }: { banner?: BannerDtoList }) => {
  if (!banner) return;

  const response = await axios.put<ResponseVo<any>>(
    `/banners/${banner.banner_idx}`,
    banner
  );

  return response;
};

export const deleteBanner = async ({ banner_idx }: { banner_idx: number }) => {
  const response = await axios.delete<ResponseVo<any>>(
    `/banners/${banner_idx}`
  );
  return response;
};
