import { Divider } from 'antd';
import React from 'react';
import FormGuideComponent from '../FormGuideComponent';
import StatisticDlsAgeStatisticChartComponent from './StatisticDlsAgeStatisticChartComponent';
import StatisticDlsAgeStatisticSheetComponent from './StatisticDlsAgeStatisticSheetComponent';

const StatisticDlsStatisticChartContainer = () => {
  return (
    <div
      style={{
        padding: '20px',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
      }}
    >
      <div className='priodChartWrap'>
        <FormGuideComponent
          title='연령별 스마트 추천 도서 데이터 차트'
          guide='최근 30일 간 연령 스마트 추천 도서 조회 데이터'
        />
        <StatisticDlsAgeStatisticChartComponent />
      </div>
      <Divider />
      <div className='priodChartWrap'>
        <FormGuideComponent
          title='연령별 스마트 추천 도서 데이터 차트'
          guide='기간별 연령 스마트 추천 도서 조회 데이터'
        />
        <StatisticDlsAgeStatisticSheetComponent />
      </div>
    </div>
  );
};

export default StatisticDlsStatisticChartContainer;
