import axios from 'axios';
import React, { useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import { noImg } from '../CommonImage';


const DcsPreviewContentViewerComponent = (props) => {
  const [fileList, setFileList] = useState([]);
  const [fileName, setFileName] = useState();
  const [fileType, setFileType] = useState();
  
  var isPreviewModal = false;
  var timeoutObj = null;

  useEffect(() => {
    setFileList(props.fileList);
    isPreviewModal = true;
    play(props.fileList ? props.fileList : []);
    return () => {
      clearTimeout(timeoutObj);
      isPreviewModal = false;
      setFileName(undefined);
    };
  }, [props]);

  const delay = (length, item, idx) => {
    return new Promise((resolve) => {
      // 보여줘야 할 부분
      setFileName(item.saveName);
      setFileType(item.type);

      // 텀 주는 부분
      timeoutObj = setTimeout(() => {
        var resetIdx = idx;
        if (length === resetIdx + 1) {
          resetIdx = -1;
        }
        resolve(resetIdx + 1);
        // resolve(idx + 1);
      }, item.rotationTime * 1000);
    });
  };

  var play = async (array) => {
    var i = 0;
    if (array.length != 0) {
      while (true) {
        i = await delay(array.length, array[i], i);
        clearTimeout(timeoutObj);
        if (isPreviewModal === false) {
          break;
        }
      }
    } else {
      return;
    }
  };

  return (
    <div style={{ height: '100%' }}>
      <div
        style={{
          width: '100%',
          height: '100%',
          display: fileType === 'image' ? 'block' : 'none',
        }}
      >
        <img
          alt='fileNamel'
          src={
            typeof fileName !== 'undefined'
              ? `${axios.defaults.baseURL}/files/dcs/stream/${fileName}`
              : ''
          }
          onError={(error) => {
            error.currentTarget.src = noImg;
          }}
          style={{
            width: '100%',
            height: '100%',
          }}
        />
      </div>
      <div
        style={{
          width: '100%',
          height: '100%',
          display: fileType === 'video' ? 'block' : 'none',
        }}
      >
        {fileType === 'video' ? (
          <ReactPlayer
            className='dcs-preview-video'
            width='100%'
            height='100%'
            loop={fileList.length === 1}
            config={{
              file: {
                attributes: {
                  // preload: 'none',
                  controlsList: 'nodownload',
                },
              },
            }}
            url={
              typeof fileName !== 'undefined'
                ? `${axios.defaults.baseURL}/files/dcs/stream/video/${fileName}`
                : ''
            }
            playing={fileName ? true : false}
            muted
          />
        ) : (
          <> </>
        )}
      </div>
    </div>
  );
};

export default DcsPreviewContentViewerComponent;
