import { PlusCircleOutlined } from '@ant-design/icons';
import React, { FC, memo } from 'react';
import { IconWrapped, Wrapped } from './CaptionDrawTitle.styled';

type DrawerTitleType = {
  onClick?: (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void | undefined;
};

const DrawerTitle: FC<DrawerTitleType> = memo(({ onClick }) => {
  return (
    <>
      <Wrapped>
        <IconWrapped onClick={onClick}>
          <PlusCircleOutlined />
        </IconWrapped>
        <span>자막을 등록하거나 관리하세요</span>
      </Wrapped>
    </>
  );
});

export default DrawerTitle;
