import { Col, Row } from "antd";
import React from "react";
import DcsPreviewContentViewerComponent from "../DcsPreviewContentViewerComponent";

const Layout_s_v_10 = (props) => {
  const {
    fileInfo = {
      screen1: {
        fileList: [],
      },
      screen2: {
        fileList: [],
      },
      screen3: {
        fileList: [],
      },
      screen4: {
        fileList: [],
      },
      screen5: {
        fileList: [],
      },
      screen6: {
        fileList: [],
      },
    },
  } = props;

  return (
    <div style={{ width: "100%", height: "700px" }}>
      <Row style={{ width: "100%", height: "40%" }}>
        <Col span={12} style={{ width: "100%", height: "100%" }}>
          <div style={{ height: "100%" }}>
            <DcsPreviewContentViewerComponent
              fileList={fileInfo.screen1.fileList}
            />
          </div>
        </Col>
        <Col span={12} style={{ width: "100%", height: "100%" }}>
          <div style={{ height: "100%" }}>
            <DcsPreviewContentViewerComponent
              fileList={fileInfo.screen2.fileList}
            />
          </div>
        </Col>
      </Row>
      <Row style={{ width: "100%", height: "40%" }}>
        <Col span={24} style={{ width: "100%", height: "100%" }}>
          <div style={{ height: "100%" }}>
            <DcsPreviewContentViewerComponent
              fileList={fileInfo.screen3.fileList}
            />
          </div>
        </Col>
      </Row>
      <Row style={{ width: "100%", height: "20%" }}>
        <Col span={24} style={{ width: "100%", height: "100%" }}>
          <div style={{ height: "100%" }}>
            <DcsPreviewContentViewerComponent
              fileList={fileInfo.screen4.fileList}
            />
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default Layout_s_v_10;
