export const requestEventJoinStatisticForPeriod = (period = 7) => {
  console.log('execute getEventJoinStatisticForPeriod');
  return axios.get(`/ay/static/getEventJoinStatisticForPeriod`, {
    params: {
      period: period,
    },
  });
};

