import { Divider, Table, Select, Radio } from 'antd';
import Text from 'antd/lib/typography/Text';
import axios from 'axios';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import uuid from 'react-uuid';
import { formatPhoneNumber } from '../../CommonUtils';
import * as Constants from '../../Constants';

const EventDetailEnrolleeListComponent = (props) => {
  const { event_master_idx, getEvent } = props;
  const [enrolleeList, setEnrolleeList] = useState([]);

  const [eventApplicantState, setEventApplicantState] = useState('APPLY');
  const [isLoading, setIsLoading] = useState();
  const [currentPage, setCurrentPage] = useState(
    sessionStorage.getItem('currentPage')
      ? Number.parseInt(sessionStorage.getItem('currentPage'))
      : 1
  );
  const columns = [
    {
      title: '번호',
      dataIndex: 'IDX',
      key: uuid(),
      width: 80,
      align: 'center',
      render: (item, record, idx) => {
        var currentPage = Number.parseInt(
          sessionStorage.getItem('currentPage')
        );
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: '신청자 이름',
      dataIndex: 'is_user_nm',
      key: uuid(),
      width: 250,
      align: 'center',
    },

    {
      title: '신청 인원',
      dataIndex: 'event_group_applicant_cnt',
      key: uuid(),
      align: 'center',
      width: 100,
      render: (item, record, idx) => {
        return `${item}명`;
      },
    },
    {
      title: '연락처',
      dataIndex: 'is_user_phone',
      key: uuid(),
      width: 150,
      align: 'center',
      render: (item, record, idx) => {
        return formatPhoneNumber(item);
      },
    },
    {
      title: '신청상태',
      dataIndex: 'event_applicant_state',
      width: 150,
      align: 'center',
      key: uuid(),
      render: (item, record, idx) => {
        return (
          <Select
            value={item}
            onChange={(e) => {
              console.log(e);
              console.log(record);
              axios
                .post(`/event/modify/applicant`, {
                  eventValues: {
                    event_applicant_state: e,
                    event_applicant_idx: record.event_applicant_idx,
                    event_master_idx: event_master_idx,
                  },
                })
                .then((res) => {
                  console.log(res);
                  getEnrolleeList(eventApplicantState);
                })
                .catch((err) => {
                  console.error(err);
                });
            }}
          >
            <Select.Option value={'APPLY'}>신청완료</Select.Option>
            <Select.Option value={'CANCEL'}>신청취소</Select.Option>
          </Select>
        );
      },
    },
    {
      title: '등록일',
      dataIndex: 'modify_date',
      key: uuid(),
      align: 'center',
      render: (item, record, idx) => {
        return item;
      },
    },
  ];
  const getEnrolleeList = (event_applicant_state) => {
    axios
      .get(`/event/applicant`, {
        params: {
          event_master_idx: event_master_idx,
          event_applicant_state: event_applicant_state,
        },
      })
      .then((res) => {
        console.log(res);
        setEnrolleeList(res.data.response_body);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  useEffect(() => {
    getEnrolleeList(eventApplicantState);
  }, [eventApplicantState]);

  return (
    <div>
      <Divider>
        <Text style={{ fontWeight: 'bold', fontFamily: 'NanumGothic' }}>
          신청자 목록
        </Text>
      </Divider>
      <div>
        <Radio.Group
          value={eventApplicantState}
          onChange={(e) => {
            setEventApplicantState(e.target.value);
          }}
          style={{ margin: '10px' }}
        >
          <Radio value={'APPLY'}>신청완료</Radio>
          <Radio value={'CANCEL'}>신청취소</Radio>
        </Radio.Group>
      </div>
      <Table
        rowKey={(dataSource) => uuid()}
        loading={isLoading}
        dataSource={enrolleeList}
        columns={columns}
        size='small'
        bordered
        pagination={{
          pageSize: 50,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', e);
            setCurrentPage(e);
          },
        }}
      />
    </div>
  );
};

export default EventDetailEnrolleeListComponent;
