import { PlusCircleOutlined } from '@ant-design/icons';
import React, { FC } from 'react';
import { IconWrapped, Wrapped } from './NoticeDrawTitle.styled';

type DrawerTitleType = {
  onClick?: (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void | undefined;
};

const DrawerTitle: FC<DrawerTitleType> = ({ onClick }) => {
  return (
    <>
      <Wrapped>
        <IconWrapped onClick={onClick}>
          <PlusCircleOutlined />
        </IconWrapped>
        <span>메모를 등록하거나 관리하세요</span>
      </Wrapped>
    </>
  );
};

export default DrawerTitle;
