import { useEffect, useState } from 'react';
import { ContentsType } from '../../../../../../types/Lss';

/**
 * 사용중인 공지사항을 반환합니다.
 */
export const useGetUsedNotice = (notice: Array<ContentsType>) => {
  const [selectedContents, setSelectedContents] = useState<ContentsType | undefined>({
    seq: 0,
    type: 'NOTICE',
    content: 'undefined',
    lss_content_idx: 1,
    lss_master_idx: 1,
    is_used: 'N',
    uuid: '',
  });
  useEffect(() => {
    const result = notice.find((o) => o.is_used === 'Y');
    setSelectedContents(result);
  }, [notice]);
  return selectedContents;
};
