import Layout101 from '../dcs-layout/Layout101';
import Layout102 from '../dcs-layout/Layout102';
import Layout103 from '../dcs-layout/Layout103';
import Layout104 from '../dcs-layout/Layout104';
import Layout105 from '../dcs-layout/Layout105';
import Layout106 from '../dcs-layout/Layout106';
import Layout107 from '../dcs-layout/Layout107';
import Layout108 from '../dcs-layout/Layout108';
import Layout109 from '../dcs-layout/Layout109';
import Layout110 from '../dcs-layout/Layout110';

type ApplyLaoutType = {
  layoutType: string;
  fileInfo: any;
  onChangeFileInfo: (fileInfo: { id: string; fileList: any[] }) => void;
  onSelectScreen: (screen: any) => void;
};

export const ApplyLayout = ({
  fileInfo,
  layoutType,
  onChangeFileInfo,
  onSelectScreen,
}: ApplyLaoutType) => {
  switch (layoutType) {
    case 'layout101':
      return (
        <Layout101
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout102':
      return (
        <Layout102
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout103':
      return (
        <Layout103
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout104':
      return (
        <Layout104
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout105':
      return (
        <Layout105
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout106':
      return (
        <Layout106
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout107':
      return (
        <Layout107
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout108':
      return (
        <Layout108
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout109':
      return (
        <Layout109
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    case 'layout110':
      return (
        <Layout110
          fileInfo={fileInfo}
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      );
    default:
      return <></>;
  }
};
export default ApplyLayout;
