import { DeleteOutlined, QuestionCircleOutlined } from '@ant-design/icons';
import { Button, Checkbox, Modal, Popconfirm, Table } from 'antd';
import { ColumnsType } from 'antd/lib/table';
import axios from 'axios';
import classNames from 'classnames';
import React, { useEffect, useState } from 'react';
import { IoMdCreate } from 'react-icons/io';
import { Link, useHistory } from 'react-router-dom';
import { v4 as uuid } from 'uuid';
import DcsPreviewComponent from '../dcs-preview/DcsPreviewComponent';
import MenuGuide from '../MenuGuide';

const ManagementDcs = (props: any) => {
  const history = useHistory();
  const [isLoading, setIsLoading] = useState(false);
  const [dataSource, setDataSource] = useState([]);
  const [isUsingEquip, setIsUsingEquip] = useState(false);
  const [isPreviewModal, setIsPreviewModal] = useState(false);
  const [selectedRowRecord, setSelectedRowRecord] = useState<any>();
  const [currentPage, setCurrentPage] = useState(
    sessionStorage.getItem('currentPage')
      ? Number.parseInt(sessionStorage.getItem('currentPage')!)
      : 1
  );

  useEffect(() => {
    getDcsIndex();
  }, [isUsingEquip]);

  const columns: ColumnsType<any> = [
    {
      title: 'NO',
      dataIndex: '',
      align: 'center',
      width: 50,
      key: uuid(),
      render: (item: any, record: any, idx: number) => {
        var currentPage = Number.parseInt(
          sessionStorage.getItem('currentPage')!
        );
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: '제목',
      dataIndex: 'cSubject',
      align: 'center',
      width: 200,
      key: uuid(),
      render: (
        text:
          | boolean
          | React.ReactChild
          | React.ReactFragment
          | React.ReactPortal
          | null
          | undefined,
        record: any,
        index: any
      ) => {
        return (
          <Link
            to={{
              pathname: '/dcs/form',
              state: { mode: 'modify', record: record },
            }}
          >
            {text}
          </Link>
        );
      },
    },
    {
      title: '시스템명',
      dataIndex: 'eqpName',
      align: 'center',
      width: 200,
      key: uuid(),
      render: (
        text:
          | boolean
          | React.ReactChild
          | React.ReactFragment
          | React.ReactPortal
          | null
          | undefined,
        record: any,
        index: any
      ) => {
        return (
          <Link
            to={{
              pathname: '/dcs/form',
              state: { mode: 'modify', record: record },
            }}
          >
            {text}
          </Link>
        );
      },
    },

    {
      title: '등록일',
      dataIndex: 'updateDate',
      width: 180,
      align: 'center',
      key: uuid(),
    },
    {
      title: '삭제',
      dataIndex: 'delete',
      width: 80,
      align: 'center',
      key: uuid(),
      render: (text: any, record: { master_idx: any }, index: any) => {
        return (
          <Popconfirm
            title='삭제하시겠습니까?'
            icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
            onConfirm={() => {
              deleteDcs(record.master_idx);
            }}
            onCancel={() => {
              return;
            }}
            okText='예'
            cancelText='아니오'
          >
            <DeleteOutlined
              style={{ fontSize: '1.5em', color: '#c0392b' }}
              onClick={(e) => {
                e.preventDefault();
              }}
            />
          </Popconfirm>
        );
      },
    },
  ];

  const deleteDcs = (cmsMasterIdx: any) => {
    axios
      .post(`/dcs/delete`, {
        idx: cmsMasterIdx,
      })
      .then((res) => {
        getDcsIndex();
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const getDcsIndex = () => {
    setIsLoading(true);
    axios
      .get<any, any>(`/dcs/index`, {
        params: {
          isUsingEquip: isUsingEquip,
        },
      })
      .then((res) => {
        setTimeout(() => {
          console.log(res);
          setDataSource(res.data.dcsIndex);
          setIsLoading(false);
        }, 1000);
      })
      .catch((err) => {
        console.error(err);
        setIsLoading(false);
      });
  };

  const onChangeUsingEquip = () => {
    setIsUsingEquip(!isUsingEquip);
  };

  return (
    <div className={classNames('w-full h-full')}>
      <MenuGuide
        title='화면 관리 ▶ DCS'
        subTitle='D-큐레이션 시스템(DCS) 시나리오 등록이 가능합니다.'
      />
      <div
        className={classNames('w-full flex justify-between items-end', 'my-3')}
      >
        <Button
          type='primary'
          className='text-white flex items-center'
          onClick={() => {
            history.push('/dcs/form', { mode: 'regist' });
          }}
        >
          <IoMdCreate />
          <div className='mx-3'>등 록</div>
        </Button>
        <div>
          <Checkbox
            onChange={(e) => {
              onChangeUsingEquip();
            }}
            disabled={isLoading}
            checked={isUsingEquip}
          >
            사용중인 DCS만 보기
          </Checkbox>
        </div>
      </div>

      <Table
        rowKey={(dataSource) => uuid()}
        loading={isLoading}
        columns={columns}
        bordered
        size='small'
        pagination={{
          pageSize: 10,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', `${e}`);
            setCurrentPage(e);
          },
        }}
        dataSource={dataSource}
      />

      <Modal
        className={
          selectedRowRecord
            ? selectedRowRecord?.layout_type?.includes('-v-')
              ? 'dcsPreviewModal-v'
              : 'dcsPreviewModal'
            : 'dcsPreviewModal'
        }
        visible={isPreviewModal}
        footer={null}
        onCancel={(e) => {
          setIsPreviewModal(false);
          setSelectedRowRecord(null);
        }}
      >
        {isPreviewModal ? (
          <DcsPreviewComponent selectedRowRecord={selectedRowRecord} />
        ) : null}
      </Modal>
    </div>
  );
};

export default ManagementDcs;
