import styled from 'styled-components';

export const Wrapped = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const IconWrapped = styled.div`
  position: absolute;
  font-size: 1.5rem;
  left: 0rem;
  transition: transform 0.45s;
  cursor: pointer;
  :hover {
    transform: scale(1.3);
    color: blue;
    cursor: pointer;
  }
  > span:hover {
    cursor: pointer;
  }
`;
