import axios from 'axios';
import React, { useEffect, useState } from 'react';
import * as Constants from '../Constants';
import Layout_s_h_2 from './dcs-preview-layout-frame/Layout_s_h_2';
import Layout_s_h_1 from './dcs-preview-layout-frame/Layout_s_h_1';
import Layout_s_h_3 from './dcs-preview-layout-frame/Layout_s_h_3';
import Layout_s_h_4 from './dcs-preview-layout-frame/Layout_s_h_4';
import Layout_s_h_5 from './dcs-preview-layout-frame/Layout_s_h_5';
import Layout_s_h_6 from './dcs-preview-layout-frame/Layout_s_h_6';
import Layout_s_h_7 from './dcs-preview-layout-frame/Layout_s_h_7';
import Layout_s_h_8 from './dcs-preview-layout-frame/Layout_s_h_8';
import Layout_s_h_9 from './dcs-preview-layout-frame/Layout_s_h_9';
import Layout_s_h_10 from './dcs-preview-layout-frame/Layout_s_h_10';
import Layout_s_h_11 from './dcs-preview-layout-frame/Layout_s_h_11';
import Layout_s_h_12 from './dcs-preview-layout-frame/Layout_s_h_12';
import Layout_s_h_13 from './dcs-preview-layout-frame/Layout_s_h_13';
import Layout_s_h_14 from './dcs-preview-layout-frame/Layout_s_h_14';
import Layout_s_h_15 from './dcs-preview-layout-frame/Layout_s_h_15';
import Layout_m_h_9 from './dcs-preview-layout-frame/Layout_m_h_9';
import Layout_m_h_11 from './dcs-preview-layout-frame/Layout_m_h_11';
import Layout_m_h_12 from './dcs-preview-layout-frame/Layout_m_h_12';
import Layout_m_h_13 from './dcs-preview-layout-frame/Layout_m_h_13';
import Layout_s_v_1 from './dcs-preview-layout-frame/Layout_s_v_1';
import Layout_s_v_2 from './dcs-preview-layout-frame/Layout_s_v_2';
import Layout_s_v_3 from './dcs-preview-layout-frame/Layout_s_v_3';
import Layout_s_v_4 from './dcs-preview-layout-frame/Layout_s_v_4';
import Layout_s_v_5 from './dcs-preview-layout-frame/Layout_s_v_5';
import Layout_s_v_6 from './dcs-preview-layout-frame/Layout_s_v_6';
import Layout_s_v_7 from './dcs-preview-layout-frame/Layout_s_v_7';
import Layout_s_v_8 from './dcs-preview-layout-frame/Layout_s_v_8';
import Layout_s_v_9 from './dcs-preview-layout-frame/Layout_s_v_9';
import Layout_s_v_10 from './dcs-preview-layout-frame/Layout_s_v_10';
import Layout_s_v_11 from './dcs-preview-layout-frame/Layout_s_v_11';
import Layout_s_v_12 from './dcs-preview-layout-frame/Layout_s_v_12';
import Layout_s_v_13 from './dcs-preview-layout-frame/Layout_s_v_13';
import Layout_s_v_14 from './dcs-preview-layout-frame/Layout_s_v_14';
import Layout_s_v_15 from './dcs-preview-layout-frame/Layout_s_v_15';
import Layout_s_v_16 from './dcs-preview-layout-frame/Layout_s_v_16';
import Layout_s_v_17 from './dcs-preview-layout-frame/Layout_s_v_17';

const DcsPreviewComponent = (props) => {
  const [fileInfo, setFileInfo] = useState();

  const [layoutType, setLayoutType] = useState();

  const { selectedRowRecord } = props;

  useEffect(() => {
    // setLayoutType(props.selectedRowRecord.layout_type);
  }, []);

  useEffect(() => {
    if (selectedRowRecord) {
      setLayoutType(props.selectedRowRecord.layout_type);
      getFileInfo();
    }

    return () => {
      setFileInfo(null);
      setLayoutType(null);
    };
  }, [props]);

  const getFileInfo = () => {
    axios
      .get(`/dcs/fileinfo`, {
        params: {
          cms_master_idx: selectedRowRecord.master_idx,
        },
      })
      .then((res) => {
        setFileInfo(res.data.fileInfo);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const ViewDistributer = (viewDistributerProps) => {
    const [fileInfo, setFileInfo] = useState(viewDistributerProps.fileInfo);
    const { selectedRowRecord } = viewDistributerProps;
    const [layoutType, setLayoutType] = useState(
      viewDistributerProps.layoutType
    );

    useEffect(() => {
      setFileInfo(
        'fileInfo' in viewDistributerProps ? viewDistributerProps.fileInfo : {}
      );
      setLayoutType(viewDistributerProps.layoutType);
    }, [viewDistributerProps]);

    if (
      layoutType === 'layout-s-h-1' ||
      layoutType === 'layout-m-h-1' ||
      layoutType === 'layout-m-h-10'
    ) {
      return <Layout_s_h_1 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-2' || layoutType === 'layout-m-h-2') {
      return <Layout_s_h_2 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-3' || layoutType === 'layout-m-h-3') {
      return <Layout_s_h_3 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-4' || layoutType === 'layout-m-h-4') {
      return <Layout_s_h_4 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-5' || layoutType === 'layout-m-h-5') {
      return <Layout_s_h_5 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-6' || layoutType === 'layout-m-h-6') {
      return <Layout_s_h_6 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-7' || layoutType === 'layout-m-h-7') {
      return <Layout_s_h_7 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-8' || layoutType === 'layout-m-h-8') {
      return <Layout_s_h_8 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-9') {
      return <Layout_s_h_9 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-10') {
      return <Layout_s_h_10 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-11') {
      return <Layout_s_h_11 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-12') {
      return <Layout_s_h_12 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-13') {
      return <Layout_s_h_13 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-14') {
      return <Layout_s_h_14 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-h-15') {
      return <Layout_s_h_15 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-m-h-9') {
      return <Layout_m_h_9 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-m-h-11') {
      return <Layout_m_h_11 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-m-h-12') {
      return <Layout_m_h_12 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-m-h-13') {
      return <Layout_m_h_13 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-1') {
      return <Layout_s_v_1 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-2') {
      return <Layout_s_v_2 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-3') {
      return <Layout_s_v_3 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-4') {
      return <Layout_s_v_4 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-5') {
      return <Layout_s_v_5 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-6') {
      return <Layout_s_v_6 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-7') {
      return <Layout_s_v_7 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-8') {
      return <Layout_s_v_8 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-9') {
      return <Layout_s_v_9 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-10') {
      return <Layout_s_v_10 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-11') {
      return <Layout_s_v_11 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-12') {
      return <Layout_s_v_12 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-13') {
      return <Layout_s_v_13 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-14') {
      return <Layout_s_v_14 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-15') {
      return <Layout_s_v_15 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-16') {
      return <Layout_s_v_16 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else if (layoutType === 'layout-s-v-17') {
      return <Layout_s_v_17 fileInfo={selectedRowRecord ? fileInfo : null} />;
    } else {
      return <></>;
    }
  };

  return (
    <div style={{ textAlign: 'center', padding: '10px' }}>
      <ViewDistributer
        layoutType={layoutType}
        fileInfo={fileInfo}
        selectedRowRecord={selectedRowRecord}
      />
    </div>
  );
};

export default DcsPreviewComponent;
