import React, { useState, useEffect } from 'react';
import { Input, Col, Row, Select, Radio, Checkbox } from 'antd';
import RadioButtonCheckedIcon from '@material-ui/icons/RadioButtonChecked';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import RadioButtonUncheckedIcon from '@material-ui/icons/RadioButtonUnchecked';
import DragHandleIcon from '@material-ui/icons/DragHandle';
import DragIndicatorIcon from '@material-ui/icons/DragIndicator';
import CloseIcon from '@material-ui/icons/Close';
import { Droppable, Draggable, DragDropContext } from 'react-beautiful-dnd';
import uuid from 'react-uuid';
import styled from 'styled-components';

const ManagementPollContentCard = (props) => {
  const { Option } = Select;
  const { onChangeQuestion, onChangeItemType, onDeleteItem } = props;
  const questionId = props.id;
  const [type, setType] = useState(props.type ? props.type : 'single');
  const [questionTitle, setQuestionTitle] = useState(
    props.questionTitle ? props.questionTitle : ''
  );
  const [items, setItems] = useState(props.items ? props.items : []);
  const dragHandleProps = props.dragHandleProps;
  const [mode, setMode] = useState(
    props.mode === 'regist' ? 'regist' : 'modify'
  );

  useEffect(() => {
    if (mode === 'regist') {
      addQuestions();
    }
  }, []);

  useEffect(() => {
    onChangeQuestion(questionId, questionTitle, items);
  }, [questionTitle, items]);

  const closeIconStyle = {
    verticalAlign: 'middle',
    cursor: 'pointer',
    marginLeft: '5px',
  };

  const onDeleteQuestion = (questionId) => {
    var copiedQuestions = Array.from(items);
    var targetIdx = 0;
    copiedQuestions.forEach((question, idx) => {
      if (question.item_id === questionId) {
        targetIdx = idx;
      }
    });
    copiedQuestions.splice(targetIdx, 1);
    setItems(copiedQuestions);
  };
  const grid = 2;
  const getItemStyle = (isDragging, draggableStyle) => ({
    // some basic styles to make the items look a bit nicer
    userSelect: 'none',
    padding: grid * 2,
    margin: `0 0 ${grid}px 0`,

    // change background colour if dragging
    background: isDragging ? '#ecf0f188' : 'white',
    borderRadius: '8px',

    // styles we need to apply on draggables
    ...draggableStyle,
  });

  // 보기 추가하기 버튼 - start
  const StyledAddElementButton = styled.span`
    color: #3498db;
    cursor: pointer;
    font-family: NotoSansKR-Bold;
    &:hover {
      color: #2980b9;
    }
    margin-top: 5px;
    display: ${type !== 'narrative' ? 'block' : 'none'};
  `;
  const AddElementButton = (addProps) => {
    return (
      <div style={{ margin: '5px', width: '100%' }}>
        <StyledAddElementButton onClick={addQuestions}>
          보기 추가하기
        </StyledAddElementButton>
      </div>
    );
  };

  // 보기 추가 함수
  const addQuestions = () => {
    var copiedQuestions = Array.from(items);
    var uid = uuid();
    copiedQuestions.push({
      question_id: questionId,
      item_id: uid,
      content: '',
      seq: items.length,
    });
    setItems(copiedQuestions);
  };
  // 보기 추가하기 버튼 - end

  const onDelete = (id, e) => {
    onDeleteItem(id);
  };

  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
    return result;
  };

  const onDragEnd = (result) => {
    // dropped outside the list
    if (!result.destination) {
      return;
    }

    const fixedItems = reorder(
      items,
      result.source.index,
      result.destination.index
    );

    fixedItems.forEach((item, idx) => {
      item['seq'] = idx;
    });

    setItems(fixedItems);
  };

  return (
    <div
      style={{
        width: '100%',
        border: '1px solid #7f8c8d',
        borderRadius: '8px',
        minHeight: '120px',
        padding: '5px',
      }}
    >
      <div {...dragHandleProps} style={{ textAlign: 'center' }}>
        <DragHandleIcon style={{ fontSize: '30' }} />
      </div>
      <Row>
        <Col span={20} style={{ padding: '5px' }}>
          <Input
            placeholder='질문'
            style={{
              backgroundColor: '#f1f3f4',
              border: '0',
            }}
            value={questionTitle}
            onChange={(e) => {
              setQuestionTitle(e.target.value);
            }}
          />
        </Col>
        <Col span={4} style={{ padding: '5px' }}>
          <Select
            style={{ width: '100%' }}
            defaultValue={type}
            onChange={(value) => {
              setType(value);
              onChangeItemType(questionId, value);
            }}
          >
            <Option value='single'>
              <RadioButtonCheckedIcon
                style={{ fontSize: '1.2em', verticalAlign: 'middle' }}
              />
              <span style={{ verticalAlign: 'middel', marginLeft: '5px' }}>
                단일선택
              </span>
            </Option>
            <Option value='multi'>
              <CheckCircleOutlineIcon
                style={{ fontSize: '1.2em', verticalAlign: 'middle' }}
              />
              <span style={{ verticalAlign: 'middel', marginLeft: '5px' }}>
                다중선택
              </span>
            </Option>
            <Option value='narrative'>
              <QuestionAnswerIcon
                style={{ fontSize: '1.2em', verticalAlign: 'middle' }}
              />
              <span style={{ verticalAlign: 'middel', marginLeft: '5px' }}>
                주관식
              </span>
            </Option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col span={24} style={{ padding: '5px' }}>
          <DragDropContext onDragEnd={onDragEnd}>
            <Droppable droppableId={uuid()}>
              {(provided, snapshot) => {
                return (
                  <div {...provided.droppableProps} ref={provided.innerRef}>
                    {/* 타입을 분별하기 위한 삼항연산자 - start */}
                    {type !== 'narrative' ? (
                      items.map((item, index) => {
                        return (
                          <Draggable
                            key={item.item_id}
                            draggableId={item.item_id}
                            index={index}
                          >
                            {(provided, snapshot) => (
                              <div
                                ref={provided.innerRef}
                                {...provided.draggableProps}
                                style={getItemStyle(
                                  snapshot.isDragging,
                                  provided.draggableProps.style
                                )}
                              >
                                <span {...provided.dragHandleProps}>
                                  <DragIndicatorIcon
                                    style={{
                                      color: '#dfe6e9',
                                      verticalAlign: 'middle',
                                    }}
                                  />
                                </span>
                                {/* 아이콘 변경을 위한삼항연산자 - start */}
                                {type === 'single' ? (
                                  <RadioButtonUncheckedIcon
                                    style={{
                                      color: '#dfe6e9',
                                      verticalAlign: 'middle',
                                    }}
                                  />
                                ) : (
                                  <CheckBoxOutlineBlankIcon
                                    style={{
                                      color: '#dfe6e9',
                                      verticalAlign: 'middle',
                                    }}
                                  />
                                )}
                                {/* 아이콘 변경을 위한 삼항연산자 - end */}
                                <Input
                                  key={item.item_id}
                                  placeholder='보기'
                                  style={{
                                    border: '0',
                                    marginLeft: '10px',
                                    maxWidth: '90%',
                                  }}
                                  onChange={(e) => {
                                    e.preventDefault();
                                    var copiedQuestions = Array.from(items);
                                    var targetIdx = 0;
                                    for (
                                      var i = 0;
                                      i < copiedQuestions.length;
                                      i++
                                    ) {
                                      if (
                                        copiedQuestions[i].item_id ===
                                        item.item_id
                                      ) {
                                        targetIdx = i;
                                        break;
                                      }
                                    }
                                    copiedQuestions[targetIdx].content =
                                      e.target.value;
                                    setItems(copiedQuestions);
                                  }}
                                  value={item.content}
                                />
                                <CloseIcon
                                  onClick={(e) => {
                                    e.preventDefault();
                                    onDeleteQuestion(item.item_id);
                                  }}
                                  style={closeIconStyle}
                                />
                              </div>
                            )}
                          </Draggable>
                        );
                      })
                    ) : (
                      <div
                        style={{
                          border: '0',
                          borderBottom: '1px dotted #dfe6e9',
                          width: '90%',
                          color: '#7f8c8d',
                          margin: '10px',
                        }}
                      >
                        <span style={{ color: '#dfe6e9' }}>주관식 입력</span>
                      </div>
                    )}
                    {/* 타입을 분별하기 위한 삼항연산자 - end */}
                    {provided.placeholder}
                  </div>
                );
              }}
            </Droppable>
          </DragDropContext>
          <AddElementButton />
        </Col>
      </Row>
      <Row>
        <Col span={24} style={{ textAlign: 'right' }}>
          <DeleteOutlineIcon
            style={{ fontSize: '2.5em', cursor: 'pointer' }}
            onClick={(e) => {
              onDelete(questionId, e);
            }}
          />
        </Col>
      </Row>
    </div>
  );
};

export default ManagementPollContentCard;
