import React from 'react';
import styled from 'styled-components';
import TextArea from 'antd/lib/input/TextArea';
import { Input, Switch } from 'antd';

const ManagementPollTitleCard = (props) => {
  const { pollTitle, setPollTitle } = props;
  const { pollDesc, setPollDesc } = props;
  const { useYn, setUseYn } = props;

  const UnderLineInput = styled.input`
    width: 100%;
    height: 40px;
    font-size: 1.3em;
    color: #2c3e50;
    font-family: NotoSansKR-Bold;
    outline: none;
    border: 0px;
    border-bottom: 1px dash rgba(255, 0, 0, 1);
    &:focus {
      animation-duration: 0.5s;
      animation-name: move;
      animation-fill-mode: both;
      @keyframes move {
        from {
          border-bottom: 1px dotted rgba(255, 0, 0, 0);
        }

        to {
          border-bottom: 2px solid rgba(255, 0, 0, 1);
        }
      }
    }
  `;

  return (
    <div
      style={{
        width: '100%',
        border: '1px solid #7f8c8d',
        borderTop: '0px',
        borderRadius: '8px',
        minHeight: '120px',
      }}
    >
      <div
        style={{
          backgroundColor: '#576574',
          borderTopRightRadius: '8px',
          borderTopLeftRadius: '8px',
          width: '100%',
          height: '15px',
        }}
      />
      <div
        style={{
          width: '100%',
          padding: '10px 30px 20px 30px',
        }}
      >
        {/* <UnderLineInput placeholder='제목' /> */}
        <div style={{ display: 'flex' }}>
          <Input
            placeholder='제목'
            style={{
              border: '0',
              resize: 'none',
              fontSize: '1.3em',
              color: '#2c3e50',
              fontFamily: 'NotoSansKR-Bold',
            }}
            value={pollTitle}
            onChange={(e) => setPollTitle(e.target.value)}
          />
          <div
            style={{
              width: '150px',
              display: 'flex',
              justifyContent: 'space-evenly',
              alignItems: 'center',
            }}
          >
            <div style={{ fontFamily: 'NanumGothic' }}>사용여부 : </div>
            <div>
              <Switch
                checked={useYn === 'Y'}
                onChange={(e) => {
                  setUseYn(e ? 'Y' : 'N');
                }}
              />
            </div>
          </div>
        </div>
        <TextArea
          autoSize={true}
          placeholder='설명'
          style={{
            marginTop: '10px',
            border: '0',
            resize: 'none',
          }}
          value={pollDesc}
          onChange={(e) => setPollDesc(e.target.value)}
        />
      </div>
    </div>
  );
};

export default ManagementPollTitleCard;
