import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Spin, Tabs, Divider } from 'antd';
import {
  fetchGetContents,
  fetchGetDefaultInfo,
  fetchGetHoliday,
  fetchGetHolidayInfo,
  fetchGetReturnDate,
  LSS_DETAIL,
  selectLssDetail,
} from '../../slice/LssSlice';

import MenuGuide from '../MenuGuide';
import FormGuideComponent from '../FormGuideComponent';
import RadioMode from './pages/components/RadioMode/RadioMode';
import ReturnDateComponent from './pages/components/ReturnDate/ReturnDate';
import {
  CalendarIconWrapped,
  InnerWrapped,
  ProgressWrapped,
  SettingIconWrapped,
  SpanWrapped,
  TabsWrapped,
  TabWrapped,
  Wrapped,
} from './ManagementLssScreen.styled';

import Notice from './pages/components/Notice/Notice';
import Caption from './pages/components/Caption/Caption';
import Holiday from './pages/components/Holiday/Holiday';
import { useHistory } from 'react-router-dom';

const ManagementLssScreen = (props: any) => {
  const state = useSelector(selectLssDetail);
  const TabPane = Tabs.TabPane;
  const [tabKey, setTabKey] = useState('1');
  const [isVisibleNotice, setIsVisibleNotice] = useState(false);
  const [isVisibleCaption, setIsVisibleCaption] = useState(false);

  const dispatch = useDispatch();
  const history = useHistory();
  const Reg = /(?:\/lss)?(?:\/form)?[0-9]{1,99999}/g;
  const myeqid = Reg.exec(history.location.pathname);
  const equipIdx = myeqid !== null ? parseInt(myeqid.toString()) : 1;
  const EquipIdx = {
    equip_idx: equipIdx,
  };
  useEffect(() => {
    dispatch(fetchGetDefaultInfo(EquipIdx));
    dispatch(fetchGetReturnDate());
    dispatch(fetchGetContents(EquipIdx));
    dispatch(fetchGetHoliday());
    dispatch(fetchGetHolidayInfo());
  }, [equipIdx]);

  function callback(key: string) {
    sessionStorage.setItem('currentPage', key);
    setTabKey(key);
  }

  //drawer의 창닫기를 위한 이벤트 핸들러
  const onDrawCloseHandler = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    const target = e.target as HTMLElement;
    const dataId = target.dataset.id as 'wrapper';
    if (dataId === 'wrapper') {
      isVisibleNotice && setIsVisibleNotice(false);
      isVisibleCaption && setIsVisibleCaption(false);
    }
  };

  return (
    <Wrapped onClick={onDrawCloseHandler} data-id={'wrapper'}>
      <InnerWrapped>
        <MenuGuide title={state[LSS_DETAIL].eqpName && `LSS( ${state[LSS_DETAIL].eqpName} )`} />
        <Spin spinning={state[LSS_DETAIL].isSpin} size="large" tip="처리 중...">
          <ProgressWrapped isProgress={state[LSS_DETAIL].isProgress}>
            <TabsWrapped>
              <TabWrapped onChange={callback} defaultActiveKey="1" activeKey={tabKey}>
                <TabPane
                  tab={
                    <SpanWrapped>
                      <SettingIconWrapped />
                    </SpanWrapped>
                  }
                  key="1"
                >
                  {/* 모드 */}
                  <FormGuideComponent title="모드변경" guide="대출일 안내, 휴관일 안내, 메시지 안내 중 하나를 선택해주세요." />
                  <RadioMode value={state[LSS_DETAIL].mode} />
                  <Divider />
                  {/* 메모 */}
                  <FormGuideComponent title="메모" guide="메모를 관리하고 표시할 항목을 선택하세요" />
                  <Notice isVisible={isVisibleNotice} setIsVisible={setIsVisibleNotice} notice={state[LSS_DETAIL].notice} />
                  <Divider />
                  {/* 자막 */}
                  <FormGuideComponent title="자막" guide="자막을 관리하고 순서를 변경하세요" />
                  <Caption isVisible={isVisibleCaption} setIsVisible={setIsVisibleCaption} />
                  <Divider />
                  {/* 대여일 */}
                  <FormGuideComponent title="대여일 변경" guide="대여일을 설정하세요." />
                  <ReturnDateComponent value={parseInt(state[LSS_DETAIL].returnDate)} />
                </TabPane>
                <TabPane
                  tab={
                    <SpanWrapped>
                      <CalendarIconWrapped />
                    </SpanWrapped>
                  }
                  key="2"
                >
                  <Holiday />
                </TabPane>
              </TabWrapped>
            </TabsWrapped>
          </ProgressWrapped>
        </Spin>
      </InnerWrapped>
    </Wrapped>
  );
};

export default ManagementLssScreen;
