import React from 'react';
import Text from 'antd/lib/typography/Text';

const EquipUsageTitleComponent = (props) => {
  const {
    equipInfo = { powerYN: 'N', cpuNumber: '-', memNumber: '-', eqpName: '' },
  } = props;
  return (
    <Text
      style={{
        fontFamily: 'bmjua',
        fontSize: '1em',
        color: equipInfo.powerYN === 'Y' ? 'rgb(14,139,266)' : 'red',
        position: 'absolute',
        top: '7px',
        left: '59px',
        marginLeft: '10px',
      }}
      ellipsis
    >
      {equipInfo.eqpName}
    </Text>
  );
};

export default EquipUsageTitleComponent;
