import {
  DeleteOutlined,
  EyeOutlined,
  PieChartOutlined,
  QuestionCircleOutlined,
} from '@ant-design/icons';
import { Button, Popconfirm, Table } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import * as Constants from '../Constants';
import MenuGuide from '../MenuGuide';

const ManagementPoll = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [dataSource, setDataSource] = useState([]);

  const [currentPage, setCurrentPage] = useState(
    sessionStorage.getItem('currentPage')
      ? Number.parseInt(sessionStorage.getItem('currentPage'))
      : 1
  );

  const columns = [
    {
      title: 'NO',
      dataIndex: '',
      width: 80,
      align: 'center',
      render: (item, record, idx) => {
        var currentPage = Number.parseInt(
          sessionStorage.getItem('currentPage')
        );
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: '설문명',
      dataIndex: 'title',
      align: 'center',
      render: (text, record, index) => {
        return (
          <Link
            to={{
              pathname: '/poll/form',
              mode: 'modify',
              record: record,
              // masterId:
            }}
          >
            {text ? text : '제목없음'}
          </Link>
        );
      },
    },
    {
      title: '설문수',
      dataIndex: 'question_cnt',
      width: 80,
      align: 'center',
    },
    {
      title: '응답자수',
      dataIndex: 'response_cnt',
      width: 100,
      align: 'center',
      render: (text, record, index) => {
        return (
          <Link
            to={{
              pathname: `/poll/result/${record.master_id}`,
              masterId: record.master_id,
              record: record,
            }}
          >
            <PieChartOutlined /> {text}
          </Link>
        );
      },
    },
    { title: '등록일', dataIndex: 'regDate', width: 180, align: 'center' },
    { title: '사용여부', dataIndex: 'useYN', width: 80, align: 'center' },
    {
      title: '미리보기',
      dataIndex: 'preview',
      width: 80,
      align: 'center',
      render: (text, record, index) => {
        return (
          <Link
            target='_blank'
            to={{
              pathname: `/poll/detail/${record.master_id}`,
            }}
            style={{ color: 'rgb(0,0,0,0.65)', cursor: 'pointer' }}
          >
            <EyeOutlined style={{ fontSize: '1.5em' }} />
          </Link>
        );
      },
    },
    {
      title: '삭제',
      dataIndex: 'delete',
      width: 80,
      align: 'center',
      render: (text, record, index) => {
        return (
          <Popconfirm
            title='삭제하시겠습니까?'
            icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
            onConfirm={() => {
              deletePoll(record.master_id);
            }}
            onCancel={() => {
              return;
            }}
            okText='예'
            cancelText='아니오'
          >
            <DeleteOutlined
              style={{ fontSize: '1.5em', color: '#c0392b' }}
              onClick={(e) => {
                e.preventDefault();
              }}
            />
          </Popconfirm>
        );
      },
    },
  ];

  const deletePoll = (master_id) => {
    axios
      .post('/poll/delete', {
        master_id: master_id,
      })
      .then((res) => {
        getPollIndex();
      })
      .catch((err) => {
        console.error(err);
      });
  };

  useEffect(() => {
    getPollIndex();
  }, []);

  const getPollIndex = () => {
    setIsLoading(true);
    axios
      .get('/poll/index', {})
      .then((res) => {
        console.log(res);
        setTimeout(() => {
          setDataSource(res.data.pollIndex);
          setIsLoading(false);
        }, 1000);
      })
      .catch((err) => {
        console.error(err);
        setIsLoading(false);
      });
  };
  return (
    <>
      <MenuGuide
        title='설문 관리'
        subTitle='설문지 질문/답변 등록 및 통계 확인 가능합니다.'
      />
      <Table
        rowKey={(dataSource) => dataSource.idx}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        size='small'
        bordered
        pagination={{
          pageSize: 10,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', e);
            setCurrentPage(e);
          },
        }}
      />
      <div id='regBtn' style={{ width: '100%', textAlign: 'center' }}>
        <Link
          to={{
            pathname: '/poll/form',
            state: {
              mode: 'regist',
            },
          }}
        >
          <Button type='primary' style={{ width: '100px' }}>
            등 록
          </Button>
        </Link>
      </div>
    </>
  );
};

export default ManagementPoll;
