import { DeleteOutlined } from '@ant-design/icons';
import { Button, Card } from 'antd';
import styled from 'styled-components';
import TextArea, { TextAreaRef } from 'antd/lib/input/TextArea';

export const IconButton = styled(Button)`
  border: 0;
  background-color: white;

  &[disabled] {
    background-color: white;
  }
  &:hover[disabled] {
    background-color: white;
    cursor: default;
  }
  &:hover {
    cursor: default;
  }
`;

export const RedIconButton = styled(IconButton)`
  color: red;
`;
export const BaddgeWrapped = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;
export const CardWrapped = styled(Card)`
  width: 96%;
  margin: 0.5rem;
  padding: 0 0.5rem;
`;

export const IconWrapped = styled.div`
  position: absolute;
`;

export const DeleteIcon = styled(DeleteOutlined)`
  color: #ce1b1b;
`;

export const TextFieldsWrapped = styled(TextArea)`
  cursor: default;
`;
