import { AnimationControls } from 'framer-motion/types/animation/types';
import { useEffect } from 'react';

/**
 *
 * @param framterControl 애니메이션 컨트롤러 객체
 * @param y y축으로 이동할 좌표값
 * @param trigger 적용될 트리거 (날짜 문자열로 날짜가 변경되면 동작함)
 * @returns
 */
const useMotionMovingAxisY = (framterControl: AnimationControls, y: number, trigger: string) => {
  useEffect(() => {
    framterControl.set({
      y: y,
      transition: { duration: 0.3 },
    });
    framterControl.start({
      y: 0,
      transition: { duration: 0.3 },
    });
  }, [trigger]);

  return null;
};

export default useMotionMovingAxisY;
