/* eslint-disable eqeqeq */
import React, { useState, useEffect } from 'react';
import { Popover, Badge, Space, Modal } from 'antd';
import * as Constants from '../../Constants';
import axios from 'axios';
import confirm from 'antd/lib/modal/confirm';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import moment from 'moment';

const ItemLoanDetailPropStatusComponent = (props) => {
  const { itemDetail, setItemMaster, isVisible, setIsVisible } = props;

  useEffect(() => {
    return () => {};
  });

  const modifyIsItemPropState = (itemDetailParam) => {
    axios
      .post(`/is/modifyIsItemPropState`, itemDetailParam)
      .then((res) => {
        setItemMaster(res.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const isBlockPopOver = () => {
    return (
      <Space direction='vertical'>
        <Badge
          className='status-ready-badge'
          status='processing'
          text='대여 가능'
          onClick={(e) => {
            if (
              itemDetail.item_status_state === 'block' ||
              itemDetail.item_status_state === 'inspection'
            ) {
              itemDetail.item_status_state = 'ready';
              modifyIsItemPropState(itemDetail);
            } else if (itemDetail.item_status_state === 'loaning') {
              Modal.error({
                title: '대여 중인 물품입니다.',
                content: '반납 버튼을 사용해주세요.',
              });
            } else if (itemDetail.item_status_state === 'reserve') {
              confirm({
                title: '예약 중인 물품 입니다.',
                icon: <ExclamationCircleOutlined style={{ color: 'red' }} />,
                content: '대여 가능한 상태로 변경하시겠습니까?',
                okText: '확인',
                cancelText: '취소',
                onOk() {
                  itemDetail.item_status_state = 'ready';
                  modifyIsItemPropState(itemDetail);
                },
                onCancel() {},
              });
            }
            setIsVisible(false);
          }}
        />
        <Badge
          className='status-inspection-badge'
          status='processing'
          text='점검 중'
          onClick={(e) => {
            if (
              itemDetail.item_status_state === 'block' ||
              itemDetail.item_status_state === 'ready'
            ) {
              itemDetail.item_status_state = 'inspection';
              modifyIsItemPropState(itemDetail);
            } else if (itemDetail.item_status_state === 'loaning') {
              Modal.error({
                title: '대여 중인 물품입니다.',
                content: '반납 버튼을 사용해주세요.',
              });
            } else if (itemDetail.item_status_state === 'reserve') {
              Modal.error({
                title: `예약 중인 물품입니다.`,
                content: '대여 가능 상태로 변경해주세요.',
              });
            }
            setIsVisible(false);
          }}
        />
        <Badge
          className='status-block-badge'
          status='processing'
          text='대여 불가'
          onClick={(e) => {
            if (
              itemDetail.item_status_state === 'inspection' ||
              itemDetail.item_status_state === 'ready'
            ) {
              itemDetail.item_status_state = 'block';
              modifyIsItemPropState(itemDetail);
            } else if (itemDetail.item_status_state === 'loaning') {
              Modal.error({
                title: '대여 중인 물품입니다.',
                content: '반납 버튼을 사용해주세요.',
              });
            } else if (itemDetail.item_status_state === 'reserve') {
              Modal.error({
                title: '예약 중인 물품입니다.',
                content: '대여 가능 상태로 변경해주세요.',
              });
            }
            setIsVisible(false);
          }}
        />
      </Space>
    );
  };

  const calcLeftTime = () => {
    let startTime = moment(
      itemDetail.item_prop_subject.split('-')[0],
      'HH:mm'
    ).format('HHmm');
    let endTime = moment(
      itemDetail.item_prop_subject.split('-')[1],
      'HH:mm'
    ).format('HHmm');
    let nowTime = moment().format('HHmm');

    if (
      itemDetail.item_master_idx == Constants.OFFICE_ITEM_MASTER_IDX &&
      itemDetail.reserve_left_time < itemDetail.reserve_left_time_for_office
    ) {
      let hour = Math.floor(itemDetail.reserve_left_time_for_office / 60);
      let minutes = itemDetail.reserve_left_time_for_office % 60;
      return `${hour !== 0 ? `${hour}시간 ` : ''}${minutes}분`;
    } else {
      return `${itemDetail.reserve_left_time}분`;
    }
  };

  const PropsStateComponent = () => {
    if (itemDetail.item_status_state === 'ready') {
      return (
        <Badge
          className='status-ready-badge'
          status='processing'
          text='대여 가능'
          onClick={() => {
            setIsVisible(!isVisible);
          }}
        />
      );
    } else if (itemDetail.item_status_state === 'inspection') {
      return (
        <Badge
          className='status-inspection-badge'
          status='processing'
          text='점검 중'
          onClick={() => {
            setIsVisible(!isVisible);
          }}
        />
      );
    } else if (itemDetail.item_status_state === 'block') {
      return (
        <Badge
          className='status-block-badge'
          status='processing'
          text='대여 불가'
          onClick={() => {
            setIsVisible(!isVisible);
          }}
        />
      );
    } else if (itemDetail.item_status_state === 'reserve') {
      return (
        <Badge
          className='status-reserve-badge'
          status='processing'
          text={`예약 중 (${calcLeftTime()} 남음)`}
          onClick={() => {
            setIsVisible(!isVisible);
          }}
        />
      );
    } else if (itemDetail.item_status_state === 'loaning') {
      return (
        <Badge
          status='processing'
          text='대여 중'
          onClick={() => {
            setIsVisible(!isVisible);
          }}
        />
      );
    } else {
      return <></>;
    }
  };

  return (
    <Popover
      content={isBlockPopOver}
      title='물품 상태 변경'
      trigger='click'
      className='isBlockSetting'
      visible={isVisible}
    >
      <PropsStateComponent />
    </Popover>
  );
};

export default ItemLoanDetailPropStatusComponent;
