import { DeleteOutlined, QuestionCircleOutlined } from '@ant-design/icons';
import { Button, Input, Popconfirm, Radio, Space, Table } from 'antd';
import Text from 'antd/lib/typography/Text';
import axios from 'axios';
import React, { useEffect, useRef, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import uuid from 'react-uuid';
import { noImg } from '../../CommonImage';
import MenuGuide from '../../MenuGuide';

const EventTableContaner = () => {
  const history = useHistory();
  const [isLoading, setIsLoading] = useState();
  const [dataSource, setDataSource] = useState([]);
  const [eventStatus, setEventStatus] = useState('total');
  const keywordInput = useRef();

  const [currentPage, setCurrentPage] = useState(
    sessionStorage.getItem('currentPage')
      ? Number.parseInt(sessionStorage.getItem('currentPage'))
      : 1
  );

  useEffect(() => {
    getPrograms(keywordInput.current.clearableInput.props.value, eventStatus);
  }, [eventStatus]);

  const getPrograms = (keyword = '', filter = 'total') => {
    setIsLoading(true);
    axios
      .get(`/event/index`, {
        params: { keyword, filter },
      })
      .then((res) => {
        setDataSource(res.data.response_body);
        setIsLoading(false);
        keywordInput.current.focus();
      })
      .catch((err) => {
        console.error(err);
        setIsLoading(false);
      });
  };

  const deleteEvent = (event_master_idx) => {
    setIsLoading(true);
    axios
      .delete(`/event/delete/${event_master_idx}`)
      .then((res) => {
        getPrograms();
      })
      .catch((err) => {
        console.error(err);
        setIsLoading(false);
      });
  };

  const columns = [
    {
      title: '등록번호',
      dataIndex: 'event_master_idx',
      key: uuid(),
      width: 80,
      align: 'center',
    },
    {
      title: '포스터',
      dataIndex: 'event_poster',
      key: uuid(),
      width: 80,
      align: 'center',
      render: (item, record, index) => {
        return (
          <img
            alt='chungnam_logo'
            src={`${axios.defaults.baseURL}/files/event/${record.event_poster.event_poster_file_name}`}
            onError={(error) => {
              error.currentTarget.src = noImg;
            }}
            style={{
              width: '80px',
            }}
          />
        );
      },
    },
    {
      title: '행사명',
      dataIndex: 'event_title',
      key: uuid(),
      align: 'center',
    },
    {
      title: '신청수 / 정원수',
      dataIndex: 'lmit',
      key: uuid(),
      width: 150,
      align: 'center',
      render: (item, record, index) => {
        return `${record.event_applicant_total_cnt}/${record.event_applicant_limit_cnt}`;
      },
    },
    {
      title: '등록일',
      dataIndex: 'reg_date',
      key: uuid(),
      width: 200,
      align: 'center',
    },
    {
      title: '삭제',
      dataIndex: 'delete',
      width: 80,
      align: 'center',
      key: uuid(),
      render: (text, record, index) => {
        return (
          <Popconfirm
            title='삭제하시겠습니까?'
            icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
            onConfirm={(e) => {
              e.stopPropagation();
              deleteEvent(record.event_master_idx);
            }}
            onCancel={(e) => {
              e.stopPropagation();
              return;
            }}
            okText='예'
            cancelText='아니오'
          >
            <DeleteOutlined
              style={{ fontSize: '1.5em', color: '#c0392b' }}
              onClick={(e) => {
                e.stopPropagation();
              }}
            />
          </Popconfirm>
        );
      },
    },
  ];
  return (
    <div>
      <MenuGuide
        title={'행사 관리'}
        subTitle={'행사 등록을 관리할 수 있습니다.'}
      />
      <Space direction='vertical' style={{ margin: '10px' }}>
        <Space style={{ margin: '10px 0px' }}>
          <Text>행사명 검색 : </Text>
          <Input
            ref={keywordInput}
            onPressEnter={(e) => {
              getPrograms(e.target.value);
            }}
          />
          <Button
            onClick={(e) => {
              getPrograms(keywordInput.current.clearableInput.props.value);
            }}
            type='primary'
          >
            {`검 색`}
          </Button>
        </Space>

        <Space>
          <Text>행사 상태 검색 : </Text>
          <Radio.Group
            value={eventStatus}
            onChange={(e) => {
              setEventStatus(e.target.value);
            }}
          >
            <Radio value='total'>전체</Radio>
            <Radio value='recruit'>모집 중</Radio>
            <Radio value='progress'>진행 중</Radio>
            <Radio value='close'>종료</Radio>
          </Radio.Group>
        </Space>
      </Space>
      <Table
        rowKey={(dataSource) => uuid()}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        size='small'
        bordered
        onRow={(record, rowIndex) => {
          return {
            onClick: (event) => {
              history.push(`/event/detail/${record.event_master_idx}`);
            }, // click row
            onDoubleClick: (event) => {}, // double click row
            onContextMenu: (event) => {}, // right button click row
            onMouseEnter: (event) => {}, // mouse enter row
            onMouseLeave: (event) => {}, // mouse leave row
          };
        }}
        pagination={{
          pageSize: 10,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', e);
            setCurrentPage(e);
          },
        }}
      />
      <div id='regBtn' style={{ width: '100%', textAlign: 'center' }}>
        <Link
          to={{
            pathname: '/event/form',
          }}
        >
          <Button type='primary' style={{ width: '100px' }}>
            등 록
          </Button>
        </Link>
      </div>
    </div>
  );
};

export default EventTableContaner;
