import { Col, Row, Tabs } from 'antd';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { layout101, layout102, layout103, layout104, layout105, layout106, layout107, layout108, layout109, layout110 } from '../CommonImage';
import Layout108 from '../dcs-layout/Layout108';

const ManagementDcslayout = (props: any) => {
  const { onChangeLayout } = props;

  const [dcsType, setDcsType] = useState(props.dcsType);

  useEffect(() => {
    setDcsType(props.dcsType);
  }, [props]);

  const HorizontalLayoutComponent = styled.img`
    max-width: 100%;
    max-height: 100%;
    box-shadow: 2px 2px 2px 2px lightgrey;
    :hover {
      box-shadow: 2px 2px 2px 2px #2980b9;
      filter: opacity(0.5) drop-shadow(0 0 0 #2980b9);
    }
  `; // HorizontalLayoutComponent -- end

  const colStyle = {
    padding: '20px',
    cursor: 'pointer',
  };

  const callback = (key: any) => {};

  return (
    <>
      <Tabs defaultActiveKey="H" onChange={callback}>
        <Tabs.TabPane tab="가로형" key="H">
          <Row>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout101}
                onClick={(e) => {
                  onChangeLayout(e, 'layout101', 'H');
                }}
              />
            </Col>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout102}
                onClick={(e) => {
                  onChangeLayout(e, 'layout102', 'H');
                }}
              />
            </Col>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout103}
                onClick={(e) => {
                  onChangeLayout(e, 'layout103', 'H');
                }}
              />
            </Col>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout104}
                onClick={(e) => {
                  onChangeLayout(e, 'layout104', 'H');
                }}
              />
            </Col>
          </Row>
          <Row>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout105}
                onClick={(e) => {
                  onChangeLayout(e, 'layout105', 'H');
                }}
              />
            </Col>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout106}
                onClick={(e) => {
                  onChangeLayout(e, 'layout106', 'H');
                }}
              />
            </Col>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout107}
                onClick={(e) => {
                  onChangeLayout(e, 'layout107', 'H');
                }}
              />
            </Col>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout108}
                onClick={(e) => {
                  onChangeLayout(e, 'layout108', 'H');
                }}
              />
            </Col>
            <Col span={6} style={colStyle}>
              <HorizontalLayoutComponent
                src={layout109}
                onClick={(e) => {
                  onChangeLayout(e, 'layout109', 'H');
                }}
              />
            </Col>
            <Col span={6} style={colStyle}>
              6:4
              <HorizontalLayoutComponent
                src={layout110}
                onClick={(e) => {
                  onChangeLayout(e, 'layout110', 'H');
                }}
              />
            </Col>
          </Row>
        </Tabs.TabPane>
        {/* <Tabs.TabPane
          tab='세로형'
          key='V'
          disabled={dcsType === 'single' ? false : true}
        ></Tabs.TabPane> */}
      </Tabs>
    </>
  );
};

export default ManagementDcslayout;
