import React, { FC, useCallback, useEffect, useState } from 'react';
import { Calendar, Badge } from 'antd';
import { v4 as uuid } from 'uuid';
import { PlusCircleTwoTone, SettingTwoTone } from '@ant-design/icons';
import HolidaySettingModal from './HolidaySettingModal';
import HolidayAddModal from './HolidayAddModal';
import moment from 'moment';
import FormGuideComponent from '../../../../FormGuideComponent';
import { useSelector } from 'react-redux';
import { LSS_DETAIL, selectLssDetail } from '../../../../../slice/LssSlice';
import { HolidayProcessType } from '../../../../../types/Lss';
import useGenerateHolidayUuid from './hooks/useGenerateHolidayUuid';
import { getDatesStartToLast } from '../../../../../util';
import getIncludesHolidayOfDay from '../../../../../util/Date/getIncludesHolidayOfDay';
import styles from './Holiday.module.scss';
import { BadgeWrapped, ButtonWrapped, HeaderButtonWrapped, HeaderWrapped, InnerWrapped, SetupIconWrapped, Wrapped } from './Holiday.styled';
import HolidayDetailModal from './HolidayDetailModal';
const Holiday: FC = () => {
  const state = useSelector(selectLssDetail);
  // 정규휴무일을 설정하는 모달창의 보여짐을 위한 상태값
  const [settingModalVisible, setSettingModalVisible] = useState(false);
  // 휴무일 추가하는 모달창의 보여짐을 위한 상태값
  const [addModalVisible, setAddModalVisible] = useState(false);
  // 선택된 휴무일을 보여주는 모달창의 보여짐을 위한 상태값
  const [detailModalVisible, setDetailModalVisible] = useState(false);
  // 어떤 휴무일이 선택되었는지 저장하는 상태값
  const [selectedModalData, setSelectedModalData] = useState<Partial<HolidayProcessType>>({});

  //렌더링을위한 더미 상태
  const [selectedDate, setSelectedDate] = useState<string>();

  //서버로 송신받은 정규&휴가정보를 하나의 휴가배열로 생성하면서 uuid를 할당함
  const holidayArray = useGenerateHolidayUuid(state[LSS_DETAIL].holiday, state[LSS_DETAIL].LegalHoliday);

  /**
   * 모든 날짜를 순회하면서 휴가가있는날은 빨간색으로 칠한다.
   */
  const DrawCalendarColor = () => {
    holidayArray.forEach((parentItem) => {
      //17부터 20일인 휴가라면 17.18.19,20의 배열을 리턴함
      const ArrayStartAndEnd = getDatesStartToLast(parentItem.startDate, parentItem.endDate);
      if (ArrayStartAndEnd) {
        ArrayStartAndEnd.forEach((Holiday) => {
          const $el = document.querySelector(`td[title$="${Holiday}`) as HTMLElement;
          if ($el) {
            const $title = $el.querySelector('.ant-picker-calendar-date-value') as HTMLElement;
            if ($title) {
              $title.style.setProperty('color', 'red');
            }
          }
        });
      }
    });
  };

  /**
   * 모든 캘린더 cell의 title을 검은색으로 초기화하고, 휴가가 존재하는 cell만 빨간색으로 색칠함
   */
  useEffect(() => {
    //모든 셀 색상 초기화
    const $el = document.querySelectorAll('.ant-picker-calendar-date-value');
    $el.forEach((item) => {
      const result = item as HTMLElement;
      result.style.setProperty('color', 'black');
    });
    //달력이동시 html element의 미스매치가 발생하여 딜레이후 동작되게 구성함
    const timerId = setTimeout(DrawCalendarColor, 16);
    return () => {
      clearTimeout(timerId);
    };
    //휴가정보와 누른 날짜가 변경되면 타이틀 색칠을 다시하게 수정함
  }, [holidayArray, selectedDate]);

  //모달을 위한 이벤트핸들러
  const showAddModal = useCallback(() => {
    setAddModalVisible(true);
  }, []);
  const showModal = useCallback(() => {
    setSettingModalVisible(true);
  }, []);
  /**
   * 강제로 컴포넌트를 갱신하기 위한 상태값 변경
   */
  const onSelect = useCallback(
    (e: moment.Moment) => {
      setSelectedDate(e.format('YYYY-MM-DD'));
    },
    [selectedDate]
  );
  /**
   * 휴가 객체를 클릭하면 모달창에 정보를 전달함
   */
  const onClickHoliday = useCallback((e: React.MouseEvent<HTMLDivElement, MouseEvent>, item) => {
    e.stopPropagation();
    setSelectedModalData({ ...item });
    setDetailModalVisible(true);
  }, []);

  type HolidayBadgeItemType = {
    item: HolidayProcessType;
  };

  const HolidayBadgeItem: FC<HolidayBadgeItemType> = ({ item }) => {
    const onMouseEnter = useCallback(() => {
      const color = 'rgba(110, 38, 143)';
      const $el = document.querySelectorAll(`[data-uuid='${item.uuid}']`);
      $el.forEach((badge) => {
        const $el = badge as HTMLElement;
        $el.style.color = color;
        $el.classList.toggle(`${styles.badgeActive}`);
      });
    }, []);
    const onMouseLeave = useCallback(() => {
      const $el = document.querySelectorAll(`[data-uuid='${item.uuid}']`);
      $el.forEach((badge) => {
        const $el = badge as HTMLElement;
        if (item.isUsed === 'N') {
          $el.style.color = 'rgba(1,1,1,0.5)';
        } else {
          $el.style.color = 'black';
        }
        $el.classList.toggle(`${styles.badgeActive}`);
      });
    }, []);
    return (
      <BadgeWrapped
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        key={`${item.uuid}`}
        data-uuid={item.uuid}
        onClick={(e) => {
          onClickHoliday(e, item);
        }}
        isUsed={item.isUsed}
      >
        <Badge size={'small'} status={item.badgeType} />
        {item.desc}
      </BadgeWrapped>
    );
  };
  /**
   *
   * @param value 달력에서 보여지는 첫날부터 마지막 날까지의 데이터
   * @returns
   */
  function dateCellRender(value: moment.Moment) {
    //당일 존재하는 모든 휴가 정보를 가지고있는 배열
    const holidays = getIncludesHolidayOfDay(value, holidayArray);
    return (
      <div key={uuid()} className="events" style={{ fontSize: '0.5rem' }}>
        {holidays.map((item: HolidayProcessType) => (
          <HolidayBadgeItem key={item.uuid} item={item} />
        ))}
      </div>
    );
  }

  const onPanelChange = useCallback(() => {
    //캘린더에서 연도보기를했을경우 상태를 아무값이나 강제로 할당하여 렌더링을 다시 되게끔 수정하였음.
    const date = moment(selectedDate).add(1, 'days').format('YYYY-MM-DD').toString();
    setSelectedDate(date);
  }, [selectedDate]);

  return (
    <Wrapped>
      <InnerWrapped>
        <HolidayAddModal visible={addModalVisible} setIsVisible={setAddModalVisible} selectedDate={selectedDate} />
        <HolidaySettingModal visible={settingModalVisible} setIsVisible={setSettingModalVisible} />
        <HolidayDetailModal visible={detailModalVisible} setIsVisible={setDetailModalVisible} data={selectedModalData} />

        <HeaderWrapped>
          <FormGuideComponent title="휴관일" guide="휴관일을 설정 할 수 있습니다." />
          <HeaderButtonWrapped>
            <SetupIconWrapped onClick={showAddModal}>
              <ButtonWrapped>
                <PlusCircleTwoTone />
                추가
              </ButtonWrapped>
            </SetupIconWrapped>
            <SetupIconWrapped onClick={showModal}>
              <ButtonWrapped>
                <SettingTwoTone />
                설정
              </ButtonWrapped>
            </SetupIconWrapped>
          </HeaderButtonWrapped>
        </HeaderWrapped>

        <Calendar
          className={styles.calendar}
          fullscreen={true}
          dateCellRender={dateCellRender}
          value={moment(selectedDate)}
          onSelect={onSelect}
          onPanelChange={onPanelChange}
        />
      </InnerWrapped>
    </Wrapped>
  );
};

export default Holiday;
