/**
 *
 * @param startDate
 * @param endDate
 * @returns 시작과 종료일의 요일을 배열의 형태로 반환합니다. (ex ["2021-04-10","2021-04-11","2021-0412"]
 */
function getDatesStartToLast(startDate: string, endDate: string) {
  const regex = RegExp(/^\d{4}-(0[1-9]|1[012])-(0[1-9]|[12][0-9]|3[01])$/);
  if (!(regex.test(startDate) && regex.test(endDate))) return undefined;
  const result = [];
  const curDate = new Date(startDate);
  while (curDate <= new Date(endDate)) {
    result.push(curDate.toISOString().split('T')[0]);
    curDate.setDate(curDate.getDate() + 1);
  }
  return result;
}

export default getDatesStartToLast;
