import React, { useState, useEffect } from 'react';
import {
  Modal,
  DatePicker,
  Table,
  Space,
  Collapse,
  Input,
  Select,
  Button,
} from 'antd';
import moment from 'moment';
import uuid from 'react-uuid';
import * as Constants from '../../Constants';
import axios from 'axios';
import Text from 'antd/lib/typography/Text';
import { FilterOutlined } from '@ant-design/icons';
import { Map } from 'immutable';

const dateFormat = 'YYYY-MM-DD';

class FilterObject {
  constructor() {
    this.item_prop_uuid = '';
    this.start_date = moment(new Date()).add(-7, 'd').format(dateFormat);
    this.end_date = moment(new Date()).format(dateFormat);
    this.user_id = '';
    this.state = 'total';
  }
}

const ItemLoanDetailItemHistoryComponent = (props) => {
  const [isLoading, setIsLoading] = useState();
  const [dataSource, setDataSource] = useState();
  const [currentPage, setCurrentPage] = useState(
    sessionStorage.getItem('currentPage')
      ? Number.parseInt(sessionStorage.getItem('currentPage'))
      : 1
  );
  const [filter, setFilter] = useState(new FilterObject());

  const columns = [
    {
      title: 'NO',
      dataIndex: '',
      key: uuid(),
      width: 80,
      align: 'center',
      render: (item, record, idx) => {
        var currentPage = Number.parseInt(
          sessionStorage.getItem('currentPage')
        );
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: '일자',
      dataIndex: 'item_status_reg_date',
      key: uuid(),
      align: 'center',
    },
    {
      title: '사용자',
      dataIndex: 'user_info',
      key: uuid(),
      align: 'center',
    },
    {
      title: '연락처',
      dataIndex: 'is_user_phone',
      key: uuid(),
      align: 'center',
    },
    {
      title: '처리 상태',
      dataIndex: 'item_status_state',
      key: uuid(),
      align: 'center',
      render: (item, record, idx) => {
        if (item === 'loaning') {
          return (
            <Text
              style={{
                color: '#077707',
              }}
            >
              대여
            </Text>
          );
        }
        if (item === 'return') {
          return (
            <Text
              style={{
                color: '#e74c3c',
              }}
            >
              반납
            </Text>
          );
        }
        return null;
      },
    },
    {
      title: '담당자',
      dataIndex: 'item_status_sys_user',
      key: uuid(),
      align: 'center',
    },
  ];

  const {
    itemDetail = {
      is_user_id: '',
      is_user_info: '',
      is_user_nm: '',
      is_user_phone: '',
      item_master_idx: 0,
      item_prop_idx: 0,
      item_prop_reg_date: '',
      item_prop_seq: '',
      item_prop_subject: '',
      item_prop_uuid: '',
      item_status_is_sys: '',
      item_status_reg_date: '',
      item_status_start_dttm: '',
      item_status_state: '',
      item_status_user: '',
    },
    isItemHistoryModal,
    setIsItemHistoryModal,
  } = props;

  useEffect(() => {
    if (isItemHistoryModal) {
      getItemHistory();
    }

    return () => {};
  }, [isItemHistoryModal]);
  const getItemHistory = () => {
    setIsLoading(true);
    axios
      .get(`/is/getLoanItemHistList`, {
        params: { ...filter, item_prop_uuid: itemDetail.item_prop_uuid },
      })
      .then((res) => {
        setDataSource(res.data.histList);
        setIsLoading(false);
      })
      .catch((err) => {
        console.error(err);
        setIsLoading(false);
      });
  };
  return (
    <Modal
      className='item-history-modal'
      title={`${itemDetail.item_prop_subject} 대여 이력`}
      visible={isItemHistoryModal}
      okButtonProps={{
        style: {
          display: 'none',
        },
      }}
      cancelText={'닫기'}
      onCancel={() => {
        setIsItemHistoryModal(false);
        setDataSource([]);
        setCurrentPage(1);
        setFilter(new FilterObject());
      }}
    >
      <Space direction='vertical' style={{ width: '100%' }}>
        <div>
          <div>
            <Collapse
              defaultActiveKey={1}
              expandIcon={({ isActive }) => (
                <FilterOutlined
                  style={{ fontSize: '1.2em', color: 'rgba(0, 0, 0, 0.65)' }}
                />
              )}
              style={{ marginTop: '10px' }}
            >
              <Collapse.Panel
                header={
                  <Text style={{ cursor: 'pointer' }}>
                    물품 대여 이력 조회 설정
                  </Text>
                }
                key='1'
              >
                <Space
                  direction='horizontal'
                  style={{ width: '100%' }}
                  size='large'
                >
                  <Space>
                    <Text>기 간</Text>
                    <DatePicker.RangePicker
                      value={[
                        moment(filter.start_date),
                        moment(filter.end_date),
                      ]}
                      onChange={(e) => {
                        if (e && e[0] && e[1]) {
                          filter.start_date = e[0].format(dateFormat);
                          filter.end_date = e[1].format(dateFormat);
                          setFilter(Map(filter).toJS());
                        }
                      }}
                      style={{ width: '280px' }}
                      size='small'
                    />
                  </Space>
                  <Space>
                    <Text>사용자 ID </Text>
                    <Input
                      size='small'
                      allowClear
                      style={{ width: '150px' }}
                      value={filter.user_id}
                      onChange={(e) => {
                        filter.user_id = e.target.value;
                        setFilter(Map(filter).toJS());
                      }}
                    />
                  </Space>
                  <Space>
                    <Text>처리 상태</Text>
                    <Select
                      value={filter.state}
                      style={{ width: '150px' }}
                      size='small'
                      onChange={(e) => {
                        filter.state = e;
                        setFilter(Map(filter).toJS());
                      }}
                    >
                      <Select.Option key='total'>전체</Select.Option>
                      <Select.Option key='loaning'>대여</Select.Option>
                      <Select.Option key='return'>반납</Select.Option>
                    </Select>
                  </Space>
                </Space>
                <div
                  style={{
                    width: '100%',
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'flex-end',
                  }}
                >
                  <Space>
                    <Button
                      type='primary'
                      size='small'
                      onClick={() => {
                        getItemHistory();
                      }}
                    >
                      적용
                    </Button>
                    <Button
                      size='small'
                      onClick={async () => {
                        setFilter(new FilterObject());
                      }}
                    >
                      초기화
                    </Button>
                  </Space>
                </div>
              </Collapse.Panel>
            </Collapse>
          </div>
        </div>
        <Table
          rowKey={(dataSource) => uuid()}
          loading={isLoading}
          dataSource={dataSource}
          columns={columns}
          size='small'
          bordered
          pagination={{
            pageSize: 10,
            current: currentPage,
            onChange: (e) => {
              sessionStorage.setItem('currentPage', e);
              setCurrentPage(e);
            },
          }}
        />
      </Space>
    </Modal>
  );
};

export default ItemLoanDetailItemHistoryComponent;
