import {
  DeleteOutlined,
  QuestionCircleOutlined,
  CaretRightOutlined,
} from '@ant-design/icons';
import { Button, DatePicker, List, Space, Popconfirm } from 'antd';
import Text from 'antd/lib/typography/Text';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import * as Constants from '../../Constants';
import { Map } from 'immutable';

const ItemLoanDetailMasterBlockComponent = (props) => {
  const {
    itemMaster = {
      blockScheduleList: [],
    },
    setItemMaster,
  } = props;
  const [datePickerValue, setDatePickerValue] = useState([]);
  useEffect(() => {
    getClosureSchedule();
  }, []);
  useEffect(() => {}, [props]);
  const [closureList, setClosureList] = useState([]);
  const [blockPeriod, setBlockPeriod] = useState({
    block_start_date: '',
    block_end_date: '',
  });
  const getClosureSchedule = () => {
    axios
      .get(`/lib/getLibClosureAfterNow`, {})
      .then((res) => {
        setClosureList(res.data.closureList);
      })
      .catch((err) => {
        console.error(err);
      });
  };
  const disabledDate = (current = moment()) => {
    // Can not select days before today and today

    var closureDate = closureList.find(
      (closure) => closure.closure_date === current.format('YYYY-MM-DD')
    );

    // 지우지 마시오. 휴관일과 달력 매칭하는 로직임
    // var closureDateBoolean =
    //   current.format("YYYY-MM-DD") ===
    //   (closureDate ? closureDate.closure_date : null);

    return (
      current.startOf('day') < moment().startOf('day') //|| closureDateBoolean // 이것도 지우지 마시오
    );
  };

  const addBlockPeriod = () => {
    if (!blockPeriod.block_start_date || !blockPeriod.block_end_date) return;

    axios
      .post(`/is/modifyLoanItemBlockSchedule`, {
        ...itemMaster,
        ...blockPeriod,
      })
      .then((res) => {
        setItemMaster(res.data.itemMaster);
        blockPeriod.block_start_date = '';
        blockPeriod.block_end_date = '';
        setBlockPeriod(Map(blockPeriod).toJS());
        setDatePickerValue([]);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const deleteBlockPeriod = (blockPeriod) => {
    axios
      .post(`/is/deleteLoanItemBlockSchedule`, blockPeriod)
      .then((res) => {
        setItemMaster(res.data.itemMaster);
      })
      .catch((err) => {
        console.error(err);
      });
  };
  return (
    <div className='ItemLoanDetailMasterBlockComponentWrap'>
      <Space>
        <DatePicker.RangePicker
          value={datePickerValue}
          disabledDate={disabledDate}
          renderExtraFooter={() => '신청 불가 시작일과 종료일을 선택해주세요'}
          onChange={(e) => {
            setDatePickerValue(e);
            blockPeriod.block_start_date = e[0].format('YYYY-MM-DD');
            blockPeriod.block_end_date = e[1].format('YYYY-MM-DD');
            setBlockPeriod(Map(blockPeriod).toJS());
          }}
          style={{ width: '360px' }}
        />
        <Button
          type='primary'
          onClick={() => {
            addBlockPeriod();
          }}
        >
          추 가
        </Button>
      </Space>
      <div>
        <List
          className='blockList'
          size='small'
          bordered={false}
          style={{ marginTop: '10px', width: '426.729px' }}
          dataSource={itemMaster.blockScheduleList}
          renderItem={(item, index) => {
            return (
              <List.Item
                style={{
                  padding: '0',
                  margin: '10px 0px',
                  border: '0',
                }}
              >
                <Text style={{ fontFamily: 'NanumGothic' }}>
                  <CaretRightOutlined />
                  {` ${item.block_start_date} ~ ${item.block_end_date} 신청 불가`}
                </Text>
                <Popconfirm
                  title='삭제하시겠습니까?'
                  icon={<QuestionCircleOutlined style={{ color: 'red' }} />}
                  onConfirm={() => {
                    deleteBlockPeriod(item);
                  }}
                  onCancel={() => {
                    return;
                  }}
                  okText='예'
                  cancelText='아니오'
                >
                  <Button
                    type='dashed'
                    size='small'
                    block
                    style={{
                      width: '58.06px',
                      textAlign: 'center',
                      padding: 0,
                    }}
                  >
                    <DeleteOutlined
                      style={{
                        fontSize: '1.2em',
                        position: 'relative',
                        top: '2px',
                        color: '#c0392b',
                      }}
                    />
                  </Button>
                </Popconfirm>
              </List.Item>
            );
          }}
        />
      </div>
    </div>
  );
};

export default ItemLoanDetailMasterBlockComponent;
