import styled from 'styled-components';
import { Tabs } from 'antd';
import { CalendarOutlined, SettingOutlined } from '@ant-design/icons';

export const Wrapped = styled.div`
  width: 100%;
  font-family: 'bmjua';
  letter-spacing: 0.1rem;
`;

export const InnerWrapped = styled.div`
  width: 65%;
  margin: auto;
`;

type Progress = {
  isProgress: boolean;
};
export const ProgressWrapped = styled.div<Progress>`
  width: 100%;
  margin: auto;
  ${(props) =>
    props.isProgress &&
    `
    filter : blur(8px),
  `};
`;
export const TabsWrapped = styled.div`
  border: 1px solid #e7e7e7;
  padding-left: 2rem;
  padding-right: 2rem;
`;
export const TabWrapped = styled(Tabs)`
  position: relative;
`;

export const SpanWrapped = styled.span`
  cursor: pointer;
  height: 100%;
`;
export const SettingIconWrapped = styled(SettingOutlined)`
  margin: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
`;
export const CalendarIconWrapped = styled(CalendarOutlined)`
  margin: 0.5rem;
  font-size: 1.5rem;
  cursor: pointer;
`;
