import { PlusCircleOutlined } from '@ant-design/icons';
import usePreventLeave from '@nooks/use-prevent-leave';
import { Affix, Button, message } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd';
import { Prompt } from 'react-router-dom';
import uuid from 'react-uuid';
import * as Constants from '../Constants';
import ManagementPollContentCard from './ManagementPollContentCard';
import './ManagementPollForm.css';
import ManagementPollTitleCard from './ManagementPollTitleCard';
import MenuGuide from '../MenuGuide';

const ManagementPollForm = (props) => {
  const [items, setItems] = useState([]); //questions으로 변수명 변경해야되는데...
  const [mode, setMode] = useState(
    props.location.mode ? props.location.mode : 'regist'
  );

  const [masterId, setMasterId] = useState(
    props.location.record ? props.location.record.master_id : uuid()
  );
  const [pollTitle, setPollTitle] = useState(
    mode === 'modify' ? props.location.record.title : ''
  );
  const [pollDesc, setPollDesc] = useState(
    mode === 'modify' ? props.location.record.description : ''
  );

  const [useYn, setUseYn] = useState(
    props.location.record && props.location.record.useYN
  );

  const { enablePrevent, disablePrevent } = usePreventLeave();
  const [isBack, setIsBack] = useState(true);

  useEffect(() => {
    axios
      .post('/poll/useYn', {
        master_id: masterId,
        useYn: useYn,
      })
      .then((res) => {
        console.log(res);
      })
      .catch((err) => {
        console.error(err);
      });
  }, [useYn]);

  useEffect(() => {
    enablePrevent();
    document.body.className = 'form-body';

    if (mode === 'regist') {
      setItems(createQuestion());
    } else if (mode === 'modify') {
      getPollDetail();
    }

    return () => {
      disablePrevent();
    };
  }, []);

  const getPollDetail = () => {
    axios
      .get('/poll/detail', {
        params: {
          master_id: masterId,
        },
      })
      .then((res) => {
        console.log(res);
        setItems(res.data.questions);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  // fake data generator
  const createQuestion = () => {
    var copiedItems = Array.from(items);
    copiedItems.push({
      question_id: uuid(),
      type: 'single',
      questionTitle: '',
      items: [],
    });
    setItems(copiedItems);
    return copiedItems;
  };

  const onChangeItemType = (id, type) => {
    var copiedItems = Array.from(items);
    copiedItems.forEach((item) => {
      if (item.question_id == id) {
        item.type = type;
      }
    });
    setItems(copiedItems);
  };

  const onChangeQuestion = (id, questionTitle, questions) => {
    var copiedItems = Array.from(items);
    var targetIdx = 0;
    for (var i = 0; i < copiedItems.length; i++) {
      if (copiedItems[i].question_id == id) {
        targetIdx = i;
        break;
      }
    }
    copiedItems[targetIdx]['items'] = questions;
    copiedItems[targetIdx]['questionTitle'] = questionTitle;
    copiedItems[targetIdx]['seq'] = targetIdx;
    setItems(copiedItems);
  };

  const onDeleteItem = (id) => {
    var copiedItems = Array.from(items);
    var targetIdx = 0;
    copiedItems.forEach((item, idx) => {
      if (item.question_id == id) {
        targetIdx = idx;
      }
    });
    copiedItems.splice(targetIdx, 1);
    setItems(copiedItems);
  };

  // a little function to help us with reordering the result
  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);

    return result;
  };

  const grid = 2;

  const getItemStyle = (isDragging, draggableStyle) => ({
    // some basic styles to make the items look a bit nicer
    userSelect: 'none',
    padding: grid * 2,
    margin: `0 0 ${grid}px 0`,

    // change background colour if dragging
    background: isDragging ? '#ecf0f188' : 'white',
    borderRadius: '8px',

    // styles we need to apply on draggables
    ...draggableStyle,
  });

  const getListStyle = (isDraggingOver) => ({
    padding: grid,
    width: '100%',
  });

  const onDragEnd = (result) => {
    // dropped outside the list
    if (!result.destination) {
      return;
    }
    const fixedItems = reorder(
      items,
      result.source.index,
      result.destination.index
    );

    fixedItems.forEach((item, idx) => {
      item['seq'] = idx;
    });

    setItems(fixedItems);
  };

  const registPoll = (e) => {
    e.preventDefault();

    if (pollTitle.trim().length === 0) {
      setPollTitle('제목없음');
    }
    if (items.length === 0) {
      message.error('작성된 설문이 없습니다.');
      return;
    }

    axios
      .post('/poll', {
        master_id: masterId,
        title: pollTitle,
        description: pollDesc,
        questions: items,
        useYn: useYn ? useYn : 'Y',
      })
      .then((res) => {
        setIsBack(false);
        props.history.goBack();
      })
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <>
      <Prompt
        when={isBack}
        message='변경사항이 저장되지 않았습니다. 뒤로가시겠습니까?'
      />
      <MenuGuide
        title={'화면 관리 ▶ 설문 조사 등록'}
        subTitle={'설문 조사를 완성해주세요'}
      />
      <ManagementPollTitleCard
        pollTitle={pollTitle}
        setPollTitle={setPollTitle}
        pollDesc={pollDesc}
        setPollDesc={setPollDesc}
        useYn={useYn}
        setUseYn={setUseYn}
      />
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId={uuid()}>
          {(provided, snapshot) => (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              style={getListStyle(snapshot.isDraggingOver)}
            >
              {items.map((item, index) => {
                return (
                  <Draggable
                    key={item.question_id}
                    draggableId={item.question_id}
                    index={index}
                  >
                    {(provided, snapshot) => (
                      <div
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        style={getItemStyle(
                          snapshot.isDragging,
                          provided.draggableProps.style
                        )}
                      >
                        <ManagementPollContentCard
                          id={item.question_id}
                          type={item.type}
                          items={item.items}
                          questionTitle={item.question_title}
                          onChangeItemType={onChangeItemType}
                          onChangeQuestion={onChangeQuestion}
                          onDeleteItem={onDeleteItem}
                          dragHandleProps={{ ...provided.dragHandleProps }}
                        />
                      </div>
                    )}
                  </Draggable>
                );
              })}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
      <div>
        <Affix offsetBottom={60} style={{ float: 'left' }}>
          <Button style={{ width: '87px' }} onClick={registPoll}>
            저장
          </Button>
        </Affix>
        <Affix offsetBottom={60} style={{ float: 'right' }}>
          <Button onClick={createQuestion}>
            <PlusCircleOutlined /> 설문 추가
          </Button>
        </Affix>
      </div>
    </>
  );
};

export default ManagementPollForm;
