import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { getBanner, getBanners } from '../api/banner.api';
import { BannerBody } from '../types/Banner';
import { ResponseVo } from '../types/ResponseVo';
import { BannerDtoList, FileInfo } from './../types/Banner';

export const BANNER = 'banner' as const;

type SliceState = {
  banners: BannerBody;
  banner?: BannerDtoList;
};

type PayloadType = {
  [BANNER]: SliceState;
};

export const fetchBanners = createAsyncThunk(
  `${BANNER}/fetchBanners`,
  getBanners
);

export const fetchBanner = createAsyncThunk(`${BANNER}/fetchBanner`, getBanner);

export const orderBanners = (newOrder?: FileInfo[]) => {
  if (!newOrder) return;
  const returnOrder: FileInfo[] = [];
  newOrder.forEach((order, idx) => {
    returnOrder.push({ ...order, file_seq: idx } as FileInfo);
  });
  return returnOrder;
};

const slice = createSlice({
  name: BANNER,
  initialState: {
    banners: {
      bannerDtoList: [],
      currentPage: 1,
      totalPage: 1,
      totalCount: 0, //
    },
    banner: {
      file_info: [],
    },
  } as SliceState,
  reducers: {
    editBanner: (state: SliceState, action: PayloadAction<BannerDtoList>) => {
      state.banner = {
        ...state.banner,
        ...action.payload,
      };
    },
    addBannerFiles: (state: SliceState, action: PayloadAction<FileInfo[]>) => {
      const files = state.banner?.file_info
        ? [...state.banner.file_info, ...action.payload]
        : [...action.payload];

      state.banner = { ...state.banner, file_info: files };
    },
    emptyBanner: (state: SliceState) => {
      state.banner = undefined;
    },
    emptyBanners: (state: SliceState) => {
      state.banners = {
        bannerDtoList: [],
        currentPage: 1,
        totalPage: 1,
        totalCount: 0,
      };
    },
    deleteFile: (state: SliceState, action: PayloadAction<FileInfo>) => {
      const filtered = state.banner?.file_info?.filter((file) => {
        return file.saved_file_name !== action.payload.saved_file_name;
      });
      state.banner = { ...state.banner, file_info: filtered };
    },
  },
  extraReducers: {
    [fetchBanners.fulfilled.type]: (
      state: SliceState,
      action: PayloadAction<ResponseVo<BannerBody>>
    ) => {
      state.banners = action.payload.response_body;

      if (
        state.banners.bannerDtoList.length === 0 &&
        action.payload.response_body.currentPage > 1
      )
        state.banners.currentPage =
          action.payload.response_body.currentPage - 1;
    },
    [fetchBanner.fulfilled.type]: (
      state: SliceState,
      action: PayloadAction<ResponseVo<BannerDtoList>>
    ) => {
      state.banner = action.payload.response_body;
    },
  },
});

export default slice.reducer;

export const {
  editBanner,
  emptyBanner,
  addBannerFiles,
  emptyBanners,
  deleteFile,
} = slice.actions;

export const selectBanner = (state: PayloadType) => state[BANNER];
