import { message } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import * as Constants from '../../Constants';
import MenuGuide from '../../MenuGuide';
import './Event.css';
import EventFormComponent from './EventFormComponent';
const EventFormContainer = (props) => {
  const [eventIdx, setEventIdx] = useState('');
  const [event, setEvent] = useState(null);
  const [fileList, setFileList] = useState([]);

  useEffect(() => {
    return () => {};
  }, [fileList]);

  const onChange = (e) => {
    const { value } = e.target;
    const reg = /^-?\d*?$/;
    if (!isNaN(value) && reg.test(value)) {
      setEventIdx(value);
    }
  };

  const registerEvent = () => {
    if (!eventIdx) {
      message.error('행사 등록 번호를 입력해주세요.');
    }
    axios
      .post(`/ayevent/registAyEvent`, {
        IDX: eventIdx,
      })
      .then((res) => {
        let { ayEvent = { IDX: 0, event_idx: 0 } } = res.data;
        if (fileList.length > 0) {
          sendImages(fileList, ayEvent.IDX);
        } else {
          props.history.push('/is');
        }
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const sendImages = (imageList, IDX) => {
    var formData = new FormData();
    var info = [];

    imageList.forEach((item, idx) => {
      formData.append('files', item.originFileObj);
      info.push({
        uid: item.uid,
        type: 'AY_EVENT',
        idx: idx,
        ref_idx: IDX,
      });
    });
    formData.append('info', JSON.stringify(info));
    axios
      .post('/file', formData, {
        headers: {
          'content-type': 'multipart/form-data',
        },
      })
      .then((res) => {
        props.history.push('/is');
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const getEvent = () => {
    if (eventIdx) {
      axios
        .get(`/ayevent/getProgram`, {
          params: {
            IDX: eventIdx,
          },
        })
        .then((res) => {
          setEvent(res.data.program);
          setFileList([]);
        })
        .catch((err) => {
          console.error(err);
        });
    }
  };
  return (
    <div style={{ width: '90%', margin: 'auto' }}>
      <MenuGuide
        title={'행사 관리 ▶ 행사 등록'}
        subTitle={'행사 정보를 입력 해주세요.'}
      />
      <EventFormComponent {...props} />
    </div>
  );
};

export default EventFormContainer;
