import {
  DeleteOutlined,
  PlusCircleOutlined,
  QuestionCircleOutlined,
} from "@ant-design/icons";
import { Button, Input, Popconfirm } from "antd";
import { Map } from "immutable";
import React from "react";
import uuid from "react-uuid";

const ItemLoanItemEditor = (props) => {
  const { items, setItems } = props;

  const addItem = () => {
    var item_prop_uuid = uuid();

    items[item_prop_uuid] = {
      item_prop_uuid: item_prop_uuid,
      item_prop_subject: "",
      item_prop_desc: "",
      item_prop_seq: new Date().getTime(),
    };

    setItems(
      Map(items)
        .sortBy((item) => item.item_prop_seq)
        .toJS()
    );
  };

  const deleteItem = (key) => {
    setItems(
      Map(items)
        .delete(key)
        .sortBy((item) => item.item_prop_seq)
        .toJS()
    );
  };

  const deleteAllItem = () => {
    setItems({});
  };

  return (
    <div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-between",
          marginTop: "5px",
        }}
      >
        <Button
          type="dashed"
          onClick={addItem}
          size="large"
          style={{ width: "calc(100% - 48px)" }}
        >
          물품 추가{" "}
          <PlusCircleOutlined
            style={{ fontSize: "1.2em", position: "relative", top: "2px" }}
          />
        </Button>
        <Popconfirm
          title="목록을 전체 삭제하시겠습니까?"
          icon={<QuestionCircleOutlined style={{ color: "red" }} />}
          onConfirm={() => {
            deleteAllItem();
          }}
          onCancel={() => {
            return;
          }}
          okText="예"
          cancelText="아니오"
        >
          <Button
            type="dashed"
            size="large"
            block
            style={{ width: "44px", textAlign: "center", padding: 0 }}
          >
            <DeleteOutlined
              style={{
                fontSize: "1.2em",
                position: "relative",
                top: "2px",
                color: "#c0392b",
              }}
            />
          </Button>
        </Popconfirm>
      </div>
      {Object.keys(items)
        .sort((a, b) => {
          return items[a]["item_prop_seq"] - items[b]["item_prop_seq"];
        })
        .map((key, idx) => {
          return (
            <div
              key={key}
              style={{
                margin: "20px 0",
                display: "row",
                justifyContent: "space-between",
              }}
            >
              <Input
                value={items[key].item_prop_subject}
                onChange={(e) => {
                  items[key].item_prop_subject = e.target.value;
                  setItems(
                    Map(items)
                      .sortBy((item) => item.item_prop_seq)
                      .toJS()
                  );
                }}
                addonBefore={`${idx + 1}번 물품명`}
                addonAfter={
                  <Popconfirm
                    title="삭제하시겠습니까?"
                    icon={<QuestionCircleOutlined style={{ color: "red" }} />}
                    onConfirm={() => {
                      deleteItem(key);
                    }}
                    onCancel={() => {
                      return;
                    }}
                    okText="예"
                    cancelText="아니오"
                  >
                    <DeleteOutlined
                      style={{ fontSize: "1.5em", color: "#c0392b" }}
                      onClick={(e) => {
                        e.preventDefault();
                      }}
                    />
                  </Popconfirm>
                }
              />
            </div>
          );
        })}
    </div>
  );
};

export default ItemLoanItemEditor;
