import {
  Form,
  Input,
  Radio,
  InputNumber,
  Button,
  message,
  Spin,
  Select,
} from 'antd';
import React, { useState, useEffect } from 'react';
import MenuGuide from '../MenuGuide';
import ManagementMagImageUpload from './ManagementMagImageUpload';
import * as Constants from '../Constants';
import axios from 'axios';
import './ManagementMagForm.css';
import FormGuideComponent from '../FormGuideComponent';
import uuid from 'react-uuid';
import usePreventLeave from '@nooks/use-prevent-leave';
import { Prompt } from 'react-router-dom';

const ManagementMagForm = (props) => {
  const [mode, setMode] = useState(
    props.location.mode ? props.location.mode : 'regist'
  );
  const [record, setRecord] = useState(props.location.record);
  const [isSpinning, setIsSpinning] = useState(false);

  const [imageList, setImageList] = useState([]);
  const [equipList, setEquipList] = useState([]);
  const [selectedEquipList, setSelectedEquipList] = useState([]);

  const [isBack, setIsBack] = useState(true);

  const [form] = Form.useForm();

  const { enablePrevent, disablePrevent } = usePreventLeave();

  useEffect(() => {
    console.log(props);
    enablePrevent();
    getMagEquipList();
    if (mode === 'modify') {
      getFileList(record);
      getAppliedEquipList(record.idx);
    }
    return () => {
      disablePrevent();
    };
  }, []);

  useEffect(() => {
    form.setFieldsValue({
      useYN: 'Y',
    });
    if (mode === 'modify') {
      form.setFieldsValue({
        title: record.title,
        equip: selectedEquipList,
        duration: record.duration_sec,
        useYN: record.useYN,
      });
    }
    return () => {};
  }, [props, selectedEquipList]);

  const validateMessages = {
    required: '필수 항목',
    types: {
      email: 'Not a validate email!',
      number: 'Not a validate number!',
    },
    number: {
      range: 'Must be between ${min} and ${max}',
    },
  };

  const getAppliedEquipList = (cmsMasterIdx) => {
    axios
      .get(`/equip/playing`, {
        params: {
          cms_master_idx: cmsMasterIdx,
        },
      })
      .then((res) => {
        setSelectedEquipList(
          res.data.appliedEquipList.map((item) => item.equip_idx)
        );
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const getFileList = (record) => {
    axios
      .get('/files', {
        params: {
          mag_idx: record.mag_idx,
          type: 'MAG',
        },
      })
      .then((res) => {
        var fileList = res.data.fileList;
        fileList.forEach((item, index) => {
          fileList[index]['uid'] = index;
          fileList[index]['url'] =
            Constants.DEV_SERVER + '/files/mag/' + item.saveName;
          fileList[index]['thumbUrl'] =
            Constants.DEV_SERVER + '/files/mag/' + item.saveName;
          fileList[index]['name'] = item.orgName;
          fileList[index]['type'] = 'image/';
          fileList[index]['status'] = 'done';
        });
        setImageList(fileList);
      })
      .catch((err) => {
        console.error(err);
        setIsSpinning(false);
      });
  };

  const handleSubmit = (values) => {
    if (typeof values.equip === 'undefined') {
      values.equip = [];
    }

    if (imageList.length == 0) {
      message.error('이미지를 등록해주세요');
      return;
    }
    setIsSpinning(true);
    if (mode == 'regist') {
      axios
        .post('/mag', {
          cSubject: values.title,
          duration_sec: values.duration,
          useYN: values.useYN,
          equip_idx: selectedEquipList,
        })
        .then((res) => {
          sendImages(imageList, res.data.mag_idx);
          setTimeout(() => {
            setIsBack(false);
            setIsSpinning(false);
            message.success('등록 완료');
            props.history.goBack();
          }, 2000);
        })
        .catch((err) => {
          console.error(err);
          setIsSpinning(false);
        });
    } else if (mode === 'modify') {
      axios
        .put(Constants.DEV_SERVER + '/mag', {
          idx: record.idx,
          cms_master_idx: record.idx,
          equip_idx: selectedEquipList,
          mag_idx: record.mag_idx,
          cSubject: values.title,
          duration_sec: values.duration,
          useYN: values.useYN,
        })
        .then((res) => {
          modifyImages(imageList, res.data.mag_idx);
          setTimeout(() => {
            setIsBack(false);
            setIsSpinning(false);
            message.success('등록 완료');
            props.history.goBack();
          }, 2000);
        })
        .catch((err) => {
          console.error(err);

          setIsSpinning(false);
        });
    }
  };

  const getMagEquipList = () => {
    axios
      .get(`/equip/index`, {
        params: {
          eqpType: 'MAG',
        },
      })
      .then((res) => {
        setEquipList(res.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const modifyImages = (imageList, mag_idx) => {
    var formData = new FormData();
    var doneInfo = [];
    var uploadingInfo = [];

    imageList.forEach((item, idx) => {
      if (item.status === 'done') {
        doneInfo.push({
          type: 'MAG',
          ref_idx: mag_idx,
          saveName: item.saveName,
          idx: idx,
          status: 'done',
        });
      } else {
        formData.append('files', item.originFileObj);
        uploadingInfo.push({
          type: 'MAG',
          uid: item.uid,
          ref_idx: mag_idx,
          idx: idx,
          status: 'uploading',
        });
      }
    });

    formData.append('doneInfo', JSON.stringify(doneInfo));
    formData.append('uploadingInfo', JSON.stringify(uploadingInfo));

    axios
      .put(Constants.DEV_SERVER + '/file' + `/${mag_idx}`, formData)
      .then((res) => {})
      .catch((err) => {
        console.error(err);

        setIsSpinning(false);
      });
  };

  const sendImages = (imageList, mag_idx) => {
    var formData = new FormData();
    var info = [];

    imageList.forEach((item, idx) => {
      formData.append('files', item.originFileObj);
      info.push({
        uid: item.uid,
        type: 'MAG',
        idx: idx,
        ref_idx: mag_idx,
      });
    });
    formData.append('info', JSON.stringify(info));
    axios
      .post('/file', formData)
      .then((res) => {})
      .catch((err) => {
        console.error(err);
        setIsSpinning(false);
      });
  };

  const onChangeEquip = (e) => {
    setSelectedEquipList(e);
  };

  return (
    <div style={{ width: '100%' }}>
      <Prompt
        when={isBack}
        message='변경사항이 저장되지 않았습니다. 뒤로가시겠습니까?'
      />
      <Spin spinning={isSpinning} size='large' tip='처리 중...'>
        <div style={{ width: '70%', margin: 'auto' }}>
          <MenuGuide
            title={'화면 관리 ▶ MAG 등록'}
            subTitle={'이미지를 등록해주세요.'}
          />
          <Form
            form={form}
            validateMessages={validateMessages}
            onFinish={handleSubmit}
            style={{
              border: '1px solid #dfe6e9',
              padding: '50px',
            }}
          >
            <FormGuideComponent title='제목' />
            <Form.Item name='title' rules={[{ required: true }]}>
              <Input />
            </Form.Item>

            <FormGuideComponent title='기기선택' />
            <Form.Item name='equip'>
              <Select
                value={selectedEquipList}
                mode='multiple'
                showArrow
                placeholder='MAG 기기를 선택해주세요'
                style={{ width: '100%' }}
                onChange={(e) => {
                  onChangeEquip(e);
                }}
              >
                {equipList.map((item, idx) => {
                  return (
                    <Select.Option key={uuid()} value={item.idx}>
                      {item.eqpName}
                    </Select.Option>
                  );
                })}
              </Select>
            </Form.Item>

            <FormGuideComponent title='이미지' />
            <Form.Item name='images'>
              <ManagementMagImageUpload
                setParentImageList={setImageList}
                fileList={Array.from(imageList)}
              />
            </Form.Item>

            <FormGuideComponent title='전환시간(초)' />
            <Form.Item name='duration' rules={[{ required: true }]}>
              <InputNumber
                min={1}
                style={{
                  width: '150px',
                }}
              />
            </Form.Item>

            {/* <FormGuideComponent title='사용여부' /> */}
            <Form.Item style={{ display: 'none' }} name='useYN'>
              <Radio.Group>
                <Radio value={'Y'}>Y</Radio>
                <Radio value={'N'}>N</Radio>
              </Radio.Group>
            </Form.Item>

            <Form.Item>
              <Button type='primary' htmlType='submit' block>
                {mode == 'regist' ? '등 록' : '수 정'}
              </Button>
            </Form.Item>
          </Form>
        </div>
      </Spin>
    </div>
  );
};

export default ManagementMagForm;
