import { AnimationControls } from 'framer-motion/types/animation/types';
import { useEffect } from 'react';

/**
 *
 * @param framterControl 애니메이션 컨트롤러 객체
 * @param y y축으로 이동할 좌표값
 * @param trigger 적용될 트리거 boolean
 * @returns
 */
const useMotionOpacity = (framterControl: AnimationControls, trigger: boolean) => {
  useEffect(() => {
    if (trigger) {
      framterControl.start({
        opacity: 1,
        scale: 1,
        transition: { duration: 0.4 },
      });
    } else {
      framterControl.start({
        opacity: 0,
        scale: 0,
        transition: { duration: 0.4 },
      });
    }
  }, [trigger]);

  return null;
};

export default useMotionOpacity;
