import { Button, Table, Modal } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import Text from 'antd/lib/typography/Text';
import axios from 'axios';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import uuid from 'react-uuid';
import * as Constants from '../Constants';
import MenuGuide from '../MenuGuide';
import LgsFileUploadComponent from './LgsFileUploadComponent';
import LgsFilterComponent from './LgsFilterComponent';

const LgsTableContainer = (props) => {
  const [isLoading, setIsLoading] = useState();
  const [dataSource, setDataSource] = useState();
  const [lastModifyDate, setLastModifyDate] = useState('');
  const [currentPage, setCurrentPage] = useState(
    sessionStorage.getItem('currentPage')
      ? Number.parseInt(sessionStorage.getItem('currentPage'))
      : 1
  );

  const [lgsFilterList, setLgsFilterList] = useState([]);

  useEffect(() => {
    getExcelList();
    getLgsFilterList();
  }, []);

  const getExcelList = () => {
    setIsLoading(true);
    axios
      .get(`/lgs/getExcelList`, {})
      .then((res) => {
        setDataSource(res.data.ayExcelList);
        if (res.data.ayExcelList.length) {
          setLastModifyDate(
            moment(res.data.ayExcelList[0].reg_date).format('YYYY년 MM월 DD일')
          );
        }
      })
      .catch((err) => {
        console.error(err);
      })
      .finally(setIsLoading(false));
  };

  const getLgsFilterList = () => {
    axios
      .get(`/lgs/getLgsFilter`, {})
      .then((res) => {
        setLgsFilterList(res.data.filterList);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const onSubmitLgsFilterList = () => {
    Modal.confirm({
      title: '신간 필터 수정',
      icon: <ExclamationCircleOutlined />,
      content: '수정하시겠습니까?',
      okText: '확인',
      cancelText: '취소',
      onOk: () => {
        axios
          .post(`/lgs/registerLgsFilter`, {
            lgsFilterList: lgsFilterList,
          })
          .then((res) => {
            setLgsFilterList(res.data.filterList);
          })
          .catch((err) => {
            console.error(err);
          });
      },
    });
  };

  const columns = [
    {
      title: 'NO',
      dataIndex: '',
      key: uuid(),
      width: 80,
      align: 'center',
      render: (item, record, idx) => {
        var currentPage = Number.parseInt(
          sessionStorage.getItem('currentPage')
        );
        return idx + (currentPage - 1) * 10 + 1;
      },
    },
    {
      title: 'KDC',
      dataIndex: 'kdc',
      key: uuid(),

      align: 'center',
    },
    {
      title: '층수',
      dataIndex: 'floor',
      key: uuid(),
      align: 'center',
    },
    {
      title: '서가',
      dataIndex: 'shelf',
      key: uuid(),
      align: 'center',
    },
    {
      title: '열',
      dataIndex: 'col',
      key: uuid(),
      align: 'center',
    },
    {
      title: '면',
      dataIndex: 'side',
      key: uuid(),
      align: 'center',
    },
    {
      title: '청구기호',
      dataIndex: 'call_no',
      key: uuid(),
      align: 'center',
    },
    {
      title: 'etc',
      dataIndex: 'etc',
      key: uuid(),
      align: 'center',
    },
  ];

  return (
    <div>
      <MenuGuide
        title={'LGS 관리'}
        subTitle={'LGS 위치 데이터를 관리할 수 있습니다.'}
      />

      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'space-between',
        }}
      >
        <LgsFileUploadComponent getExcelList={getExcelList} />
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'flex-end',
          }}
        >
          <Text>{`최종 등록일 : ${lastModifyDate}`}</Text>
        </div>
      </div>

      <div className='lgs-filter-wrap' style={{ margin: '10px 0px' }}>
        <LgsFilterComponent
          lgsFilterList={lgsFilterList}
          setLgsFilterList={setLgsFilterList}
          onSubmitLgsFilterList={onSubmitLgsFilterList}
        />
      </div>

      <Table
        rowKey={(dataSource) => uuid()}
        loading={isLoading}
        dataSource={dataSource}
        columns={columns}
        size='small'
        bordered
        pagination={{
          pageSize: 10,
          current: currentPage,
          onChange: (e) => {
            sessionStorage.setItem('currentPage', e);
            setCurrentPage(e);
          },
        }}
      />
    </div>
  );
};

export default LgsTableContainer;
