import { useEffect, useState } from 'react';
import { DayOfTheWeek } from '../../../../../../types/Lss';

/**
 * 요일에 해당하는 키값을 주입하여 반환합니다.
 */
const useGenerateDayOfTheWeekKey = (dayOfTheWeek: DayOfTheWeek) => {
  const [keyOfWeek, setKeyOfWeek] = useState<Array<number>>([]);

  useEffect(() => {
    if (dayOfTheWeek.length > 0) {
      const weekData: Array<number> = [];
      dayOfTheWeek.forEach((item) => {
        if (item === 'SUNDAY') {
          weekData.push(0);
        } else if (item === 'MONDAY') {
          weekData.push(1);
        } else if (item === 'TUESDAY') {
          weekData.push(2);
        } else if (item === 'WEDNESDAY') {
          weekData.push(3);
        } else if (item === 'THURSDAY') {
          weekData.push(4);
        } else if (item === 'FRIDAY') {
          weekData.push(5);
        } else if (item === 'SATURDAY') {
          weekData.push(6);
        } else {
        }
      });
      setKeyOfWeek(weekData);
    }
  }, [dayOfTheWeek]);

  return keyOfWeek;
};

export default useGenerateDayOfTheWeekKey;
