import React, { useState, useEffect, PureComponent } from 'react';
import * as Constants from '../Constants';
import axios from 'axios';
import { Row, Col, Alert, Modal } from 'antd';
import { MoreOutlined } from '@ant-design/icons';
import {
  PieChart,
  Pie,
  Sector,
  Cell,
  Legend,
  ResponsiveContainer,
  Tooltip,
  BarChart,
  CartesianGrid,
  XAxis,
  YAxis,
  Bar,
} from 'recharts';
import { randomColor } from 'randomcolor';

const ResultTitleCard = (props) => {
  const [record, setRecord] = useState(props.record);

  useEffect(() => {
    setRecord(props.record);
  }, [props]);

  return (
    <div style={{ width: '100%', marginTop: '20px' }}>
      <div style={{ width: '70%', margin: 'auto' }}>
        <div
          style={{
            width: '100%',
            border: '1px solid #7f8c8d',
            borderTop: '0px',
            borderRadius: '8px',
            minHeight: '120px',
            backgroundColor: 'white',
          }}
        >
          <div
            style={{
              backgroundColor: '#576574',
              borderTopRightRadius: '8px',
              borderTopLeftRadius: '8px',
              width: '100%',
              height: '15px',
            }}
          ></div>
          <div
            style={{
              width: '100%',
              padding: '10px 30px 20px 30px',
            }}
          >
            <p style={{ fontSize: '2em', fontFamily: 'NotoSansKR-Bold' }}>
              {record ? record.title : ''}
            </p>
            <pre style={{ fontFamily: 'NotoSansKR-Medium' }}>
              총 응답자 : {record ? record.response_cnt : ''} 명
            </pre>
          </div>
        </div>
      </div>
    </div>
  );
};

// pie settings -- start

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = ({
  cx,
  cy,
  midAngle,
  innerRadius,
  outerRadius,
  percent,
  index,
}) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <text
      x={x}
      y={y}
      fill='white'
      textAnchor={x > cx ? 'start' : 'end'}
      dominantBaseline='central'
    >
      {percent !== 0 ? `${(percent * 100).toFixed(0)}%` : ''}
    </text>
  );
};

// pie settings -- end

const ResultContentCard = (props) => {
  const [questionResults, setQuestionResults] = useState([]);
  const [isNarrativeModal, setIsNarrativeModal] = useState(false);
  const [questionResultsIdx, setQuestionResultsIdx] = useState();
  const [narrativeResultList, setNarrativeResultList] = useState([]);

  useEffect(() => {
    setQuestionResults(props.questionResults);
  }, [props]);

  useEffect(() => {
    if (isNarrativeModal) {
      console.log(questionResults);
      setNarrativeResultList(
        questionResults[questionResultsIdx]['questionResultMap']
      );
    }
  }, [isNarrativeModal]);

  const cardStyle = {
    width: '70%',
    margin: 'auto',
    border: '1px solid #7f8c8d',
    borderRadius: '8px',
    minHeight: '120px',
    padding: '20px',
    marginTop: '10px',
    backgroundColor: 'white',
  };

  const onClickNarrative = (e, item) => {
    e.preventDefault();
    console.log(item);
    setQuestionResultsIdx(item);
    setIsNarrativeModal(true);
  };

  const onCloseNarrativeModal = (e) => {
    e.preventDefault();
    setIsNarrativeModal(false);
  };

  const resultList = questionResultsIdx
    ? questionResults[questionResultsIdx]['questionResultMap']
    : [];

  return (
    <>
      <Modal
        title={questionResults[questionResultsIdx]?.question_title}
        centered
        visible={isNarrativeModal}
        onCancel={onCloseNarrativeModal}
        footer={null}
      >
        {narrativeResultList.map((item, idx) => {
          return (
            <div key={item.user_id} style={{ padding: '5px' }}>
              <Alert message={item.answer} type='info' />
            </div>
          );
        })}
      </Modal>
      {questionResults.map((item, questionResultsIdx) => {
        const resultList = item.questionResultMap;
        const dataList = [];
        var totalCnt = 0;
        if (item.type !== 'narrative') {
          resultList.map((result, idx) => {
            var data = {};
            data.name = result.content;
            data.formattingName = result.content.substring(0, 15) + '...';
            data.value = result.answer_cnt;
            totalCnt += result.answer_cnt;
            dataList.push(data);
          });
        } else {
          totalCnt = resultList.length;
        }

        if (item.type === 'single') {
          return (
            <div key={item.question_id} style={cardStyle}>
              <Row>
                <Col span={24}>
                  <div>
                    <pre>
                      설문 {questionResultsIdx + 1} 번 : {item.question_title}{' '}
                      (총 {totalCnt} 건)
                    </pre>
                  </div>
                  <div style={{ width: '100%', height: 300 }}>
                    <ResponsiveContainer>
                      <PieChart>
                        <Pie
                          data={dataList}
                          cx={150}
                          cy={150}
                          labelLine={false}
                          label={renderCustomizedLabel}
                          outerRadius={120}
                          fill='#8884d8'
                          legendType='square'
                        >
                          {dataList.map((entry, index) => {
                            return (
                              <Cell
                                key={item.question_id}
                                fill={randomColor({
                                  luminosity: 'dark',
                                })}
                              />
                            );
                          })}
                        </Pie>
                        <Tooltip
                          formatter={(value, name, props) => {
                            return (
                              <span style={{ color: 'blue' }}>{value}건</span>
                            );
                          }}
                        />
                        <Legend
                          layout='vetical'
                          verticalAlign='middle'
                          align='right'
                          iconSize={20}
                          iconType='circle'
                          width={400}
                          formatter={(value = '', entry, index) => {
                            if (value.length > 40) {
                              return value.substring(0, 40) + '...';
                            } else {
                              return value;
                            }
                          }}
                        />
                      </PieChart>
                    </ResponsiveContainer>
                  </div>
                </Col>
              </Row>
            </div>
          );
        } else if (item.type === 'multi') {
          return (
            <div key={item.question_id} style={cardStyle}>
              <div>
                <pre>
                  설문 {questionResultsIdx + 1} 번 : {item.question_title} (총{' '}
                  {totalCnt} 건)
                </pre>
              </div>
              <div style={{ width: '100%', height: 300 }}>
                <ResponsiveContainer>
                  <BarChart data={dataList}>
                    <XAxis dataKey='name' name='test' hide={true} />
                    <YAxis />
                    <Tooltip
                      formatter={(value, name, props) => {
                        return <span style={{ color: 'blue' }}>{value}건</span>;
                      }}
                    />
                    <Bar
                      dataKey='value'
                      fill={randomColor({
                        luminosity: 'dark',
                      })}
                    />
                  </BarChart>
                </ResponsiveContainer>
              </div>
            </div>
          );
        } else if (item.type === 'narrative') {
          return (
            <div key={item.question_id} style={cardStyle}>
              <div>
                <pre>
                  설문 {questionResultsIdx + 1} 번 : {item.question_title} (총{' '}
                  {totalCnt} 건)
                </pre>
              </div>

              {resultList.map((item, idx) => {
                if (idx < 5) {
                  return (
                    <div key={item.user_id} style={{ padding: '5px' }}>
                      <Alert message={item.answer} type='info' />
                    </div>
                  );
                } else if (idx === 6) {
                  return (
                    <div
                      key={`key-${idx}`}
                      style={{
                        width: '100%',
                        textAlign: 'center',
                        cursor: 'pointer',
                      }}
                      onClick={(e) => {
                        onClickNarrative(e, questionResultsIdx);
                      }}
                    >
                      <MoreOutlined style={{ fontSize: '2em' }} />
                    </div>
                  );
                } else {
                  return;
                }
              })}
            </div>
          );
        }
      })}
    </>
  );
};

const ManagementPollResult = (props) => {
  const [record, setRecord] = useState();
  const [masterId, setMasterId] = useState(props.match.params.master_id);
  const [questionResults, setQuestionResults] = useState([]);

  useEffect(() => {
    getQuestionResultTitle();
    getQuestionResults();
  }, []);

  const getQuestionResultTitle = () => {
    axios
      .get(`/poll/index`, {
        params: {
          master_id: masterId,
        },
      })
      .then((res) => {
        setRecord(res.data.pollIndex[0]);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const getQuestionResults = () => {
    axios
      .get(`/poll/result`, {
        params: {
          master_id: masterId,
        },
      })
      .then((res) => {
        setQuestionResults(res.data.questionResults);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <div style={{ marginBottom: '100px' }}>
      <ResultTitleCard record={record} />
      <ResultContentCard questionResults={questionResults} />
    </div>
  );
};

export default ManagementPollResult;
