import React from 'react';
import ManagementDcsFileUploadComponent from '../dcs/ManagementDcsFileUploadComponent';
import { LayoutWrapped, Row } from './Layout.styled';
import { LayoutProps } from './LayoutType';

const Layout102 = ({
  fileInfo,
  onChangeFileInfo,
  onSelectScreen,
}: LayoutProps) => {
  return (
    <LayoutWrapped>
      <Row flexGrow={1}>
        <ManagementDcsFileUploadComponent
          id='screen1'
          fileInfoForScreen={
            fileInfo && 'screen1' in fileInfo && fileInfo['screen1']
          }
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      </Row>
      <Row flexGrow={2}>
        <ManagementDcsFileUploadComponent
          id='screen2'
          fileInfoForScreen={
            fileInfo && 'screen2' in fileInfo && fileInfo['screen2']
          }
          onChangeFileInfo={onChangeFileInfo}
          onSelectScreen={onSelectScreen}
        />
      </Row>
    </LayoutWrapped>
  );
};

export default Layout102;
