/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react/jsx-pascal-case */
import usePreventLeave from '@nooks/use-prevent-leave';
import { Button, Col, Input, message, Modal, Row, Select } from 'antd';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link, Prompt } from 'react-router-dom';
import styled from 'styled-components';
import { v4 as uuid } from 'uuid';
import { confirm } from '../ConfirmModal';
import FormGuideComponent from '../FormGuideComponent';
import MenuGuide from '../MenuGuide';
import ApplyLayout from './ApplyLayout';
import ManagementDcsContentsListComponent from './ManagementDcsContentsList';
import './ManagementDcsForm.css';
import ManagementDcsLayout from './ManagementDcsLayout';
import ManagementDcsPlaylistTimeControlPanel from './ManagementDcsPlaylistTimeControlPanel';
import UploadingProgressIndicator from './UploadingProgressIndicator';

const ContentsListHeader = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
`;
const ManagementDcsFormV2 = (props: any) => {
  const [isLayoutModal, setIsLayoutModal] = useState(false);
  const [dcsType, setDcsType] = useState('');
  const [layoutType, setLayoutType] = useState('');
  const [dcsEquipList, setDcsEquipList] = useState([]);
  const [immutableDcsEquipList, setImmutableDcsEquipList] = useState([]);
  const [dcsSelectedEquip, setDcsSelectedEquip] = useState([]);
  const [fileInfo, setFileInfo] = useState<any>();
  const [selectedScreen, setSelectedScreen] = useState('');
  const [selectedFileList, setSelectedFileList] = useState<any[]>([]);
  const [isProgress, setIsProgress] = useState(false);
  const [progressPercentage, setProgressPercentage] = useState(0);
  const [dspType, setDspType] = useState('H');
  const [dcsTitle, setDcsTitle] = useState('');
  const [mode, setMode] = useState(
    props.location.state?.mode ? props.location.state.mode : 'regist'
  );
  const [cmsMasterIdx, setCmsMasterIdx] = useState();
  const [playDuration, setPlayDuration] = useState<number>(10);

  const [isBack, setIsBack] = useState(true);

  const { enablePrevent, disablePrevent } = usePreventLeave();

  // mode : modify
  const [record, setRecord] = useState<any>();

  useEffect(() => {
    if (props.location.state?.mode) {
      setMode(props.location.state.mode);
    }

    document.body.className = 'form-body';
    getDcsEquipList();
    setDcsType('single');
    enablePrevent();
    setDcsEquipList(immutableDcsEquipList);

    return () => {
      disablePrevent();
    };
  }, []);

  useEffect(() => {
    if (fileInfo) {
      const screens: string[] = Object.keys(fileInfo);
      screens
        .sort((a, b) => {
          return a < b ? -1 : 1;
        })
        .forEach((screenId) => {
          if ('fileList' in fileInfo[screenId]) {
            const fileList = fileInfo[screenId].fileList as any[];
            if (fileList && fileList.length > 0) {
              fileList.forEach((file) => {
                const fileType = file.type as string;
                if (file.fileType === 'image' || fileType.startsWith('ima')) {
                  file.duration = playDuration;
                  file.rotationTime = playDuration;
                }
              });
            }
          }
        });
      setFileInfo({ ...fileInfo });
    }
  }, [playDuration]);

  useEffect(() => {
    setDcsEquipList(
      immutableDcsEquipList.filter((item: any) => item.eqpType === 'DCS-S')
    );
  }, [immutableDcsEquipList]);

  useEffect(() => {
    if (props.location.state && 'record' in props.location.state) {
      setLayoutType(props.location.state.record?.layout_type);
      setRecord(props.location.state.record);
      setDcsTitle(props.location.state.record.cSubject);
      setCmsMasterIdx(props.location.state.record.master_idx);

      setDcsType('single');
      setDspType('H');

      getAppliedEquipList(props.location.state.record.master_idx);
      getFileInfo();
    }
  }, [props]);

  const getFileInfo = () => {
    axios
      .get(`/dcs/fileinfo`, {
        params: {
          cms_master_idx: props.location.state.record.master_idx,
        },
      })
      .then((res) => {
        setFileInfo((res.data as any).fileInfo);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const getAppliedEquipList = (cmsMasterIdx: any) => {
    axios
      .get(`/equip/playing`, {
        params: {
          cms_master_idx: cmsMasterIdx,
        },
      })
      .then((res) => {
        setDcsSelectedEquip(
          (res.data as any).appliedEquipList.map((item: any) => item.equip_idx)
        );
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const getDcsEquipList = () => {
    axios
      .get(`/equip/index`, {
        params: {
          eqpType: 'DCS',
        },
      })
      .then((res) => {
        setImmutableDcsEquipList(res.data as any);
        if (mode === 'regist') {
          setDcsType('single');
        }
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const onChangeEquip = (e: any) => {
    setDcsSelectedEquip(e);
  };

  const onSelectScreen = (screen: any) => {
    // fileInfo가 없는데 fileList에 넣으려는 증상이 있어 filInfo가 생성되었을 때 동작되게 수정함
    if (screen !== undefined) {
      if (fileInfo) {
        setSelectedScreen(screen);
        setSelectedFileList(fileInfo[screen] ? fileInfo[screen].fileList : []);
      }
    }

    // setSelectedScreen(screen);
    // setSelectedFileList(fileInfo[screen] ? fileInfo[screen].fileList : []);
  };

  const onChangeLayout = (e: any, layoutTypeParam = '', dspTypeParam = '') => {
    e.preventDefault();
    if (layoutType) {
      confirm({
        title: '레이아웃 변경',
        content:
          '레이아웃 변경시 레이아웃 및 컨텐츠 목록이 초기화 됩니다. 변경하시겠습니까?',
        onOk: () => {
          setLayoutType(layoutTypeParam);
          setFileInfo({});
          setSelectedScreen('');
          setSelectedFileList([]);
          setDspType(dspType);
        },
      });
    } else {
      setLayoutType(layoutTypeParam);
      setDspType(dspTypeParam);
    }
    console.log(layoutTypeParam);
    console.log(fileInfo);
    // setFileInfo({});
    if (layoutTypeParam.includes('101')) {
      setFileInfo({
        screen1: { id: 'screen1', fileList: [] },
      });
    }
    if (layoutTypeParam.includes('104') || layoutTypeParam.includes('107')) {
      setFileInfo({
        screen1: { id: 'screen1', fileList: [] },
        screen2: { id: 'screen2', fileList: [] },
        screen3: { id: 'screen3', fileList: [] },
      });
    }
    if (
      layoutTypeParam.includes('102') ||
      layoutTypeParam.includes('103') ||
      layoutTypeParam.includes('105') ||
      layoutTypeParam.includes('106') ||
      layoutTypeParam.includes('108') ||
      layoutTypeParam.includes('109') ||
      layoutTypeParam.includes('110')
    ) {
      setFileInfo({
        screen1: { id: 'screen1', fileList: [] },
        screen2: { id: 'screen2', fileList: [] },
      });
    }

    setIsLayoutModal(false);
  };

  const onChangeFileInfo = (fileInfoParam: { id: string; fileList: any[] }) => {
    let tempFileInfo: any = { ...fileInfo };
    tempFileInfo[fileInfoParam.id] = {
      id: fileInfoParam.id,
      fileList: fileInfoParam.fileList,
    };
    setFileInfo(tempFileInfo);
    setSelectedFileList(fileInfoParam.fileList);
  };

  const onChangeFileList = (screenId: any, fileList: any) => {
    let copiedFileInfo: any = fileInfo;

    copiedFileInfo[screenId].fileList = Array.from(fileList);
    setFileInfo(Object.assign(copiedFileInfo));
    setSelectedFileList(Array.from(fileList));
  };

  const onCalcleDcsLayoutModal = () => {
    setIsLayoutModal(false);
  };

  const formBlockStyle = {
    marginTop: '25px',
  };

  const sendDcsInfo = () => {
    if (!!!fileInfo) {
      message.error('컨텐츠가 등록되지 않았습니다');
      return;
    }
    let fileInfoKeys: any = Object.keys(fileInfo).filter(
      (item) => !item.includes('select')
    );
    if (dcsTitle.trim().length === 0) {
      message.error('제목을 입력해주세요');
      return;
    }

    if (fileInfoKeys.length === 0) {
      message.error('컨텐츠가 등록되지 않았습니다');
      return;
    }

    for (var i = 0; i < fileInfoKeys.length; i++) {
      if (
        Object.entries(fileInfo[fileInfoKeys[i]]).length === 0 &&
        fileInfo[fileInfoKeys[i]]['fileList'].length === 0
      ) {
        message.error(`${fileInfoKeys[i]}에 컨텐츠가 등록되지 않았습니다`);
        return;
      } else if (fileInfo[fileInfoKeys[i]].fileList.length === 0) {
        message.error(`${fileInfoKeys[i]}에 컨텐츠가 등록되지 않았습니다`);
        return;
      }
    }

    const postDcs = () => {
      if (mode === 'regist') {
        axios
          .post(`/dcs`, {
            cSubject: dcsTitle,
            cContents: dcsTitle,
            cType: 'DCS',
            equip_idx: dcsSelectedEquip,
            dcsType: dcsType,
            dspType: dspType,
            layoutType: layoutType, // db와 대조하기 위한 layouttype
            layout_type: layoutType, // tbl_ges_dcs 테이블에 layout_type:: 저장된 레이아웃을 불러오기 위한 선택..
          })
          .then((res) => {
            sendData(fileInfoKeys, (res.data as any).dcsList);
            // props.history.goBack();
          })
          .catch((err) => {
            console.error(err);
          });
      } else if (mode === 'modify') {
        axios
          .put(`/dcs`, {
            idx: record.master_idx,
            cSubject: dcsTitle,
            cContents: dcsTitle,
            cType: 'DCS',
            equip_idx: dcsSelectedEquip,
            dcsType: dcsType,
            dspType: dspType,
            layoutType: layoutType,
            layout_type: layoutType,
          })
          .then((res) => {
            sendData(fileInfoKeys, (res.data as any).dcsList);
            // props.history.goBack();
          })
          .catch((err) => {
            console.error(err);
          });
      }
    };

    confirm({
      title: `컨텐츠를 등록하시겠습니까?`,
      content: ``,
      onOk: () => {
        postDcs();
      },
    });
  };

  const sendData = (fileInfoKeys = [], dcsList: any[]) => {
    var formData = new FormData();
    var info = new Array();

    const config = {
      onUploadProgress: function (progressEvent: {
        loaded: number;
        total: number;
      }) {
        var percentCompleted = Math.round(
          (progressEvent.loaded * 100) / progressEvent.total
        );
        setProgressPercentage(percentCompleted);
      },
    };

    // data 전송 start
    setProgressPercentage(0);
    setIsProgress(true);

    fileInfoKeys
      .filter((key) => 'fileList' in fileInfo[key])
      .sort()
      .forEach((screenId, fikIdx) => {
        fileInfo[screenId].fileList.forEach((item: any, idx: number) => {
          var file_type = String(item.type);
          if ('originFileObj' in item) {
            formData.append('files', item.originFileObj);
          } else {
            formData.append('files', '');
          }
          info.push({
            uid: item.uid,
            org_name: item.name,
            file_type: file_type.includes('video') ? 'video' : 'image',
            item: item,
            type: 'DCS',
            idx: item.seq,
            duration: item.duration,
            layoutType: layoutType,
            screenId: screenId,
            ref_idx: dcsList[fikIdx],
            equip_idx: dcsSelectedEquip,
            file_status: 'file_status' in item ? item.file_status : 'uploading',
          });
        });
      });

    formData.append('info', JSON.stringify(info));
    axios
      .post('/file', formData, config)
      .then((res) => {
        setTimeout(() => {
          setProgressPercentage(0);
          setIsBack(false);
          setIsProgress(false);
          props.history.goBack();
        }, 1000);
      })
      .catch((err) => {
        console.error(err);
        setProgressPercentage(0);
        setIsProgress(false);
      });
  };

  return (
    <div style={{ width: '100%' }}>
      <Prompt
        when={isBack}
        message='변경사항이 저장되지 않았습니다. 뒤로가시겠습니까?'
      />
      <UploadingProgressIndicator
        isProgress={isProgress}
        progressPercentage={progressPercentage}
      />
      <MenuGuide
        title='DCS 등록/수정'
        subTitle='화면별 DCS 출력 목록을 편집할 수 있습니다.'
      />
      <Row
        style={{
          border: '1px solid #dfe6e9',
          padding: '50px',
        }}
      >
        <Col span={13}>
          <div>
            <FormGuideComponent title='제목' />
            <Input
              value={dcsTitle}
              onChange={(e) => {
                setDcsTitle(e.target.value);
              }}
            />
          </div>
          <div style={formBlockStyle}>
            <FormGuideComponent title='기기 선택' />
            <Select
              value={dcsSelectedEquip}
              showArrow
              mode='multiple'
              placeholder='DCS 기기를 선택해주세요'
              style={{ width: '100%' }}
              onChange={(e) => {
                onChangeEquip(e);
              }}
            >
              {dcsEquipList.map((item: any, idx) => {
                return (
                  <Select.Option key={uuid()} value={item.idx}>
                    {item.eqpType === 'DCS-S' ? 'DCS-싱글' : 'DCS-멀티'} :{' '}
                    {item.eqpName}
                  </Select.Option>
                );
              })}
            </Select>
          </div>

          <div style={formBlockStyle}>
            <FormGuideComponent
              title={`레이아웃 ${
                layoutType ? `(${layoutType})` : '(선택된 레이아웃 없음)'
              }`}
            />
            <Link
              to=''
              onClick={(e) => {
                e.preventDefault();
                if (!dspType) {
                  message.error('화면 타입을 선택해주세요');
                  return;
                }
                setIsLayoutModal(true);
              }}
              style={{
                fontFamily: 'NotoSansKR-Bold',
                marginTop: '15px',
              }}
            >
              <Button
                style={{
                  marginTop: '10px',
                  marginBottom: '10px',
                  width: '100%',
                }}
              >
                레이아웃 선택하기
              </Button>
            </Link>
            <ApplyLayout
              layoutType={layoutType}
              fileInfo={fileInfo}
              onChangeFileInfo={onChangeFileInfo}
              onSelectScreen={onSelectScreen}
            />
            <div style={formBlockStyle}>
              <FormGuideComponent title='이미지 전체 시간 설정' />
              <ManagementDcsPlaylistTimeControlPanel
                setPlayDuration={setPlayDuration}
              />
            </div>
            <Modal
              title={'레이아웃 선택을 선택해주세요'}
              visible={isLayoutModal}
              footer={null}
              onCancel={onCalcleDcsLayoutModal}
              width={1000}
            >
              <ManagementDcsLayout
                dcsType={dcsType}
                onChangeLayout={onChangeLayout}
              />
            </Modal>
          </div>
          <div style={{ marginTop: '20px' }}>
            <Button type='primary' block onClick={sendDcsInfo}>
              업 로 드
            </Button>
          </div>
        </Col>
        <Col span={10} offset={1}>
          <div>
            <FormGuideComponent
              title={`컨텐츠 목록 ${
                selectedScreen ? `(${selectedScreen})` : ''
              }`}
            />

            <ManagementDcsContentsListComponent
              fileList={
                selectedScreen !== '' ? fileInfo[selectedScreen]?.fileList : []
              }
              selectedScreen={selectedScreen}
              onChangeFileList={onChangeFileList}
            />
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default ManagementDcsFormV2;
