import styled from 'styled-components';
import DragIndicatorIcon from '@material-ui/icons/DragIndicator';
import { DeleteOutlined } from '@ant-design/icons';

type TypeNotice = {
  isEdit?: boolean;
};

export const CaptionListWrapped = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  /* margin-top: 10%; */
  overflow: auto;
  padding: 0 30px;
  font-family: 'bmjua';
`;
export const NoticeBox = styled.div<TypeNotice>`
  display: flex;
  height: 0;
  visibility: hidden;
  position: absolute;
  left: 0;
  justify-content: center;
  align-items: center;
  line-height: 100%;
  padding: 0 3rem;
  top: 0;
  z-index: 99;
  background-color: white;
  transition: all 0.1s;
  width: 100%;
  ${(props) =>
    props.isEdit &&
    `
      visibility: visible;
      height: 11%;
    `};
`;

type TypeCaptionItemWrapped = {
  isSelected?: boolean;
};
export const CaptionItemWrapped = styled.div<TypeCaptionItemWrapped>`
  color: black;
  ${(props) =>
    props.isSelected &&
    `
      cursor:pointer;
      color:blue;
      `}
`;
export const ItemsWrapped = styled.div<TypeCaptionItemWrapped>`
  display: flex;
  justify-content: start;
  align-items: center;
  transition: filter 0.25s;
  font-size: 1.3rem;
  margin: 0.2rem;
  ${(props) =>
    props.isSelected &&
    `
filter : blur(2px)
`}
`;

export const DragIndicatorIconWrapped = styled(DragIndicatorIcon)`
  color: #dfe6e9;
  vertical-align: middle;
`;

export const SpanIconWrapped = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
`;

export const CaptionSpan = styled.span`
  display: flex;
  flex: 1;
  align-items: center;
`;

export const DeleteOutlinedWrapped = styled(DeleteOutlined)`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 3rem;
  color: #ce1b1b;
`;
