/* eslint-disable eqeqeq */
import { Descriptions } from 'antd';
import React, { useEffect } from 'react';
import EventPosterUploadComponent from './EventPosterUploadComponent';

const EventDetailComponent = (props) => {
  const {
    eventMaster,
    eventTarget,
    eventPoster,
    setFileList,
    fileList,
  } = props;

  useEffect(() => {
    console.log('EventDetailComponent props', props);
    return () => {};
  }, []);

  const covertDays = (daysStr = '') => {
    let dayArray = daysStr.split(',');
    let daysStrArray = dayArray.map((day) => {
      if (day == 6) {
        return '일';
      } else if (day == 0) {
        return '월';
      } else if (day == 1) {
        return '화';
      } else if (day == 2) {
        return '수';
      } else if (day == 3) {
        return '목';
      } else if (day == 4) {
        return '금';
      } else if (day == 5) {
        return '토';
      } else {
        return '설정 안됨';
      }
    });
    return daysStrArray.toString().replace(/,/g, ', ');
  };
  const getAgeName = (age) => {
    switch (age) {
      case 0:
        return '연령무관';
      case 6:
        return '유아';
      case 8:
        return '초등';
      case 14:
        return '청소년';
      case 20:
        return '성인';
      default:
        return '';
    }
  };

  return (
    <div>
      <Descriptions className='event-wrap' bordered>
        <Descriptions.Item label='등록 번호' span={3}>
          {eventMaster.event_master_idx}
        </Descriptions.Item>
        <Descriptions.Item label='행사 명' span={2}>
          {' '}
          {eventMaster.event_title}
        </Descriptions.Item>
        <Descriptions.Item label='포스터' span={1}>
          <EventPosterUploadComponent
            setFileList={setFileList}
            eventPoster={eventPoster}
            fileList={fileList}
          />
        </Descriptions.Item>
        <Descriptions.Item label='내 용' span={3}>
          {eventMaster.event_description}
        </Descriptions.Item>
        <Descriptions.Item
          label='행사 신청 기간'
          span={2}
        >{`${eventMaster.event_apply_start_date}~${eventMaster.event_apply_end_date}`}</Descriptions.Item>
        <Descriptions.Item label='행사 수행 기간' span={1}>
          {`${eventMaster.event_activity_start_date}~${eventMaster.event_activity_end_date}`}
        </Descriptions.Item>
        <Descriptions.Item label='행사 요일' span={2}>
          {covertDays(eventMaster.event_activity_weekday)}
        </Descriptions.Item>
        <Descriptions.Item label='행사 시간' span={1}>
          {`${eventMaster.event_activity_start_time}~${eventMaster.event_activity_end_time}`}
        </Descriptions.Item>
        <Descriptions.Item label='장 소' span={2}>
          {eventMaster.event_place}
        </Descriptions.Item>
        <Descriptions.Item label='행사 대상(연령/성별)' span={1}>
          {`${getAgeName(eventTarget.event_target_age)} / ${
            eventTarget.event_target_sex === 'MALE' ? '남성' : ''
          }${eventTarget.event_target_sex === 'FEMALE' ? '여성' : ''}${
            eventTarget.event_target_sex === 'COMMON' ? '제한없음' : ''
          }`}
        </Descriptions.Item>
        <Descriptions.Item label='그룹 신청 여부' span={2}>
          {`${
            eventMaster.event_is_group === 'Y'
              ? `허용 (최대 ${eventMaster.event_group_limit_cnt} 명)`
              : `비허용`
          }`}
        </Descriptions.Item>
        <Descriptions.Item label='공개 여부' span={1}>
          {eventMaster.event_is_public === 'Y' ? `공개` : `비공개`}
        </Descriptions.Item>
      </Descriptions>
    </div>
  );
};

export default EventDetailComponent;
