import { useEffect, useState } from 'react';
import { v4 as uuid } from 'uuid';
import { HolidayProcessType, HolidayType } from '../../../../../../types/Lss';

/**
 *
 * @param Holiday 정기,임시공휴일배열
 * @param Legal 법정공휴일배열
 * @returns 하나의 휴가배열로 생성하면서 uuid를 할당하여 반환한다.
 */
const useGenerateHolidayUuid = (Holiday: Array<HolidayType>, Legal: Array<HolidayType>) => {
  const [holidayArray, setHolidayArray] = useState<Array<HolidayProcessType>>([]);

  useEffect(() => {
    const temp = [...Holiday, ...Legal];
    const result: Array<HolidayProcessType> = temp.map((item) => {
      let badge: 'success' | 'processing' | 'error' | 'default' | 'warning' = 'success';
      if (item.type === '임시휴일') {
        badge = 'warning';
      } else if (item.type === '법정공휴일') {
        badge = 'error';
      }
      const data: HolidayProcessType = {
        ...item,
        uuid: uuid(),
        badgeType: badge,
      };
      return data;
    });

    setHolidayArray(result);
  }, [Holiday, Legal]);

  return holidayArray;
};

export default useGenerateHolidayUuid;
