import Axios from 'axios';
import moment from 'moment';

export const requestEventStatisticPerDayForChart = (
  start_date = moment().subtract(14, 'day').format('YYYY-MM-DD'),
  end_date = moment().format('YYYY-MM-DD')
) =>
  Axios.get(`/event/statistic/perPeriod`, {
    params: {
      start_date,
      end_date,
    },
  });

export const requestEventStatisticPerEventForSheet = () =>
  Axios.get(`/event/statistic/perEvent`);

export const requestEventStatisticPerAgedForSheet = () =>
  Axios.get(`/event/statistic/perAged`);
