import React, { FC, useCallback } from 'react';
import { Button, InputNumberProps } from 'antd';
import { Inner, Title, Wrapped, InputNumberWrapped } from './ReturnDate.styled';
import {
  fetchPutReturnDate,
  LSS_DETAIL,
  selectLssDetail,
  setReturnDate,
} from '../../../../../slice/LssSlice';
import { useDispatch, useSelector } from 'react-redux';
import { valueType } from 'antd/lib/statistic/utils';

type ReturnDateComponentType = {
  //대여일 값
  value: number | undefined;
};

const ReturnDateComponent: FC<ReturnDateComponentType> = ({
  value,
}: ReturnDateComponentType) => {
  const dispatch = useDispatch();
  const state = useSelector(selectLssDetail);

  //대여일 변경을위한 이벤트핸들러
  const onChangeDate = useCallback(
    (date: valueType) => {
      const returnDate = date.toString();
      dispatch(
        setReturnDate({
          returnDate: returnDate,
        })
      );
    },
    [state]
  );
  //저장이벤트
  const onChangeDateSave = useCallback(() => {
    const date = state[LSS_DETAIL].returnDate.toString();
    dispatch(
      fetchPutReturnDate({
        codeName: date,
        codeGroup: 'LSS',
        codeID: 'rental_date',
      })
    );
  }, [value]);

  return (
    <>
      <Wrapped>
        <Inner>
          <Title>대여일 : </Title>
          {/* onChange 이벤트핸들러를 전달하기위해 아래 타입을 넣음. */}
          {/* https://github.com/DefinitelyTyped/DefinitelyTyped/issues/39136 링크참조 */}
          <InputNumberWrapped<FC<InputNumberProps<number>>>
            size={'large'}
            min={1}
            max={31}
            value={value}
            onChange={onChangeDate}
          />
          <Button size={'large'} type='primary' onClick={onChangeDateSave}>
            적 용
          </Button>
        </Inner>
      </Wrapped>
    </>
  );
};

export default ReturnDateComponent;
