import React from 'react';
import { Typography } from 'antd';
import CountUp from 'react-countup';

const EquipUsageContentComponent = (props) => {
  const {
    equipInfo = { powerYN: 'N', cpuNumber: '-', memNumber: '-' },
  } = props;

  return (
    <Typography
      style={{
        position: 'absolute',
        fontFamily: 'bmjua',
        bottom: '2px',
        left: '59px',
        cursor: 'default',
        marginLeft: '10px',
      }}
    >
      CPU :{' '}
      {equipInfo ? (
        equipInfo.powerYN === 'Y' ? (
          <CountUp
            end={equipInfo.cpuNumber}
            start={0}
            duration={3}
            suffix={'%'}
          />
        ) : (
          '-'
        )
      ) : (
        '-'
      )}{' '}
      | MEM :{' '}
      {equipInfo ? (
        equipInfo.powerYN === 'Y' ? (
          <CountUp
            end={equipInfo.memNumber}
            start={0}
            duration={2}
            suffix={'%'}
          />
        ) : (
          '-'
        )
      ) : (
        '-'
      )}
    </Typography>
  );
};

export default EquipUsageContentComponent;
