import { DatePicker, Divider, Modal, Popconfirm, Row } from 'antd';
import { motion, useAnimation } from 'framer-motion';
import moment from 'moment';
import React, { FC, memo, useCallback, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import useMotionOpacity from './hooks/useMotionOpacity';
import { fetchPostHoliday } from '../../../../../slice/LssSlice';
import { PostHolidayType } from '../../../../../types/Lss';
import FormGuideComponent from '../../../../FormGuideComponent';
import {
  ButtonWrapped,
  DateWrapped,
  DivideWrapped,
  InnerColumnButton,
  InputWrapped,
  MotionDivWrapped,
  RangePickerWrapped,
  TitleTextWrapped,
  TitleWrapped,
} from './HolidayAddModal.styled';
import useMotionMovingAxisY from './hooks/useMotionMovingAxisY';

type HolidayAddModalType = {
  visible: boolean;
  setIsVisible: React.Dispatch<React.SetStateAction<boolean>>;
  selectedDate?: string;
};

type SelectedStartDayAndEndDayType = Array<string>;

const HolidayAddModal: FC<HolidayAddModalType> = ({ visible, setIsVisible, selectedDate }) => {
  const dispatch = useDispatch();
  const dateFormat = 'YYYY/MM/DD';
  const { RangePicker } = DatePicker;

  //임시공휴일 추가를 위한 상태값
  const [addForHolidayDate, setAddForHolidayDate] = useState<SelectedStartDayAndEndDayType>([
    moment(selectedDate).format('YYYY-MM-DD').toString(),
    moment(selectedDate).format('YYYY-MM-DD').toString(),
  ]);
  const startMonth = addForHolidayDate[0].split('-')[1]; //시작 달
  const startDay = addForHolidayDate[0].split('-')[2]; //시작 일
  const endMonth = addForHolidayDate[1].split('-')[1]; //종료 달
  const endDay = addForHolidayDate[1].split('-')[2]; //종료 일
  const [addForInputDesc, setAddForInputDesc] = useState('');

  /**
   * 달력에서 선택된 날짜를 기반으로 addForInputDesc문자열로 넣음
   */
  useEffect(() => {
    const selectedDateInCalendar = moment(selectedDate).format('YYYY-MM-DD').toString();
    setAddForHolidayDate([selectedDateInCalendar, selectedDateInCalendar]);
  }, [selectedDate]);

  /* 
  모션을 위한 기능
  */
  const ModalControls = useAnimation();
  const StartTitleControls = useAnimation();
  const EndTitleControls = useAnimation();
  useMotionOpacity(ModalControls, visible);
  useMotionMovingAxisY(StartTitleControls, 50, addForHolidayDate[0]);
  useMotionMovingAxisY(EndTitleControls, 50, addForHolidayDate[1]);

  /**
   * 모달 닫는 이벤트 핸들러
   */
  const handleCancel = useCallback(() => {
    setIsVisible(false);
  }, []);

  /**
   * 설명을 바꾸는 이벤트 핸들러
   */
  const onChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setAddForInputDesc(e.target.value);
  }, []);

  /**
   * 임시 공휴일 추가를 위한 이벤트 핸들러
   */
  const onAddConfirm = () => {
    const result: Partial<PostHolidayType> = {};
    result.type = '임시휴일';
    result.desc = addForInputDesc === '' ? '임시휴일' : addForInputDesc;
    result.date = {
      startDay: addForHolidayDate[0],
      endDay: addForHolidayDate[1],
    };
    dispatch(fetchPostHoliday(result as PostHolidayType));
    setIsVisible(false); //모달 닫기
    setAddForInputDesc(''); //초기화
  };

  const Title: FC = memo(() => {
    return (
      <TitleWrapped>
        <TitleTextWrapped>임시휴일 추가</TitleTextWrapped>
      </TitleWrapped>
    );
  });

  const getPopupContainer = useCallback((e: HTMLElement) => e.parentElement as HTMLElement, []);
  return (
    <Modal onCancel={handleCancel} visible={visible} title={<Title />} footer={[]}>
      <MotionDivWrapped animate={ModalControls}>
        <div>
          <Row justify={'space-around'}>
            <DateWrapped>
              <h1>시작일</h1>
              <motion.h1 animate={StartTitleControls}>
                {startMonth}/{startDay}
              </motion.h1>
            </DateWrapped>
            <DivideWrapped type={'vertical'} />
            <DateWrapped>
              <h1>종료일</h1>
              <motion.h1 animate={EndTitleControls}>
                {endMonth}/{endDay}
              </motion.h1>
            </DateWrapped>
          </Row>
          <RangePickerWrapped>
            <RangePicker
              getPopupContainer={getPopupContainer}
              style={{ width: '100%' }}
              value={[moment(addForHolidayDate[0]), moment(addForHolidayDate[1])]}
              format={dateFormat}
              onCalendarChange={(e) => {
                if (e) {
                  if (!!e[0] && !!e[1]) {
                    const days = e as Array<moment.Moment>;
                    const startday = days[0].format('YYYY-MM-DD');
                    const endDay = days[1].format('YYYY-MM-DD');
                    setAddForHolidayDate([startday, endDay]);
                  }
                }
              }}
            />
          </RangePickerWrapped>
          <Divider />
          <FormGuideComponent title="설명" guide="임시휴관일에 대한 설명을 적어주세요 (최대 9글자)" />

          <Row justify={'space-around'}>
            <InputWrapped value={addForInputDesc} placeholder="설명을 입력하세요 (최대 9글자)" maxLength={9} onChange={onChange}></InputWrapped>
          </Row>
        </div>
        <InnerColumnButton>
          <Popconfirm title="추가 하시겠습니까?" okText="추가" onConfirm={onAddConfirm} okType="primary">
            <ButtonWrapped type="primary">추가</ButtonWrapped>
          </Popconfirm>
        </InnerColumnButton>
      </MotionDivWrapped>
    </Modal>
  );
};
export default HolidayAddModal;
